/**
 * AABB Boundingbox
 */
export class AABB {
	protected _x: number = 0;
	protected _y: number = 0;
	protected _width: number = 0;
	protected _height: number = 0;
	protected _originPoints: number[][] = [];
	protected _points: number[][] = [];

	/**
	 * @param [x=0] - The X coordinate of the upper-left corner of the rectangle
	 * @param [y=0] - The Y coordinate of the upper-left corner of the rectangle
	 * @param [width=0] - The overall width of this rectangle
	 * @param [height=0] - The overall height of this rectangle
	 */
	constructor(x = 0, y = 0, width = 0, height = 0) {
		this._x = Number(x);
		this._y = Number(y);
		this._width = Number(width);
		this._height = Number(height);
	}

	/**
	 *
	 */
	set originPoints(points: number[][]) {
		this._originPoints = points;
		const aabb = AABB.fromPoints(points);
		this.x = aabb.x;
		this.y = aabb.y;
		this.width = aabb.width;
		this.height = aabb.height;
		this.update();
	}

	get x(): number {
		return this._x;
	}

	set x(value: number) {
		this._x = value;
	}

	get y(): number {
		return this._y;
	}

	set y(value: number) {
		this._y = value;
	}

	get width(): number {
		return this._width;
	}

	set width(value: number) {
		this._width = value;
	}

	get height(): number {
		return this._height;
	}

	set height(value: number) {
		this._height = value;
	}

	get points(): number[][] {
		this.update();
		return this._points;
	}

	get topLeft(): number[] {
		this.update();
		return this._points[0];
	}

	get topRight(): number[] {
		this.update();
		return this._points[1];
	}

	get bottomRight(): number[] {
		this.update();
		return this._points[2];
	}

	get bottomLeft(): number[] {
		this.update();
		return this._points[3];
	}

	protected update(): void {
		this._points = [
			[this._x, this._y],
			[this._x + this._width, this._y],
			[this._x + this._width, this._y + this._height],
			[this._x, this._y + this._height]
		];
	}

	/**
	 * returns the left edge of the rectangle
	 *
	 * @member {number}
	 */
	get left(): number {
		return this.x;
	}

	/**
	 * returns the right edge of the rectangle
	 */
	get right(): number {
		return this.x + this.width;
	}

	/**
	 * returns the top edge of the rectangle
	 */
	get top(): number {
		return this.y;
	}

	/**
	 * returns the bottom edge of the rectangle
	 */
	get bottom(): number {
		return this.y + this.height;
	}

	/**
	 * Creates a clone of this Rectangle
	 *
	 * @return A copy of this AABB.
	 */
	clone(): AABB {
		return new AABB(this.x, this.y, this.width, this.height);
	}

	/**
	 * Copies another rectangle to this one.
	 *
	 * @param rectangle - The rectangle to copy from.
	 * @return Returns itself.
	 */
	copyFrom(rectangle: AABB): AABB {
		this.x = rectangle.x;
		this.y = rectangle.y;
		this.width = rectangle.width;
		this.height = rectangle.height;

		return this;
	}

	/**
	 * Copies this rectangle to another one.
	 *
	 * @param rectangle - The rectangle to copy to.
	 * @return Returns given parameter.
	 */
	copyTo(rectangle: AABB): AABB {
		rectangle.x = this.x;
		rectangle.y = this.y;
		rectangle.width = this.width;
		rectangle.height = this.height;

		return rectangle;
	}

	/**
	 * Checks whether the x and y coordinates given are contained within this Rectangle
	 *
	 * @param x - The X coordinate of the point to test
	 * @param y - The Y coordinate of the point to test
	 * @return Whether the x/y coordinates are within this Rectangle
	 */
	contains(x: number, y: number): boolean {
		if (this.width <= 0 || this.height <= 0) {
			return false;
		}

		if (x >= this.x && x < this.x + this.width) {
			if (y >= this.y && y < this.y + this.height) {
				return true;
			}
		}

		return false;
	}

	/**
	 * Checks whether the given {@code bounds} are equal to this.
	 *
	 * @param bounds
	 */
	equals(bounds: AABB): boolean {
		if (!bounds) return false;

		return (
			bounds.x === this.x && bounds.y === this.y && bounds.width === this.width && bounds.height === this.height
		);
	}

	/**
	 * Pads the rectangle making it grow in all directions.
	 * If paddingY is omitted, both paddingX and paddingY will be set to paddingX.
	 *
	 * @param [paddingX=0] - The horizontal padding amount.
	 * @param [paddingY=0] - The vertical padding amount.
	 * @return Returns itself.
	 */
	pad(paddingX = 0, paddingY = paddingX): this {
		this.x -= paddingX;
		this.y -= paddingY;

		this.width += paddingX * 2;
		this.height += paddingY * 2;

		return this;
	}

	/**
	 * Fits this rectangle around the passed one.
	 *
	 * @param rectangle - The rectangle to fit.
	 * @return Returns itself.
	 */
	fit(rectangle: AABB): this {
		const x1 = Math.max(this.x, rectangle.x);
		const x2 = Math.min(this.x + this.width, rectangle.x + rectangle.width);
		const y1 = Math.max(this.y, rectangle.y);
		const y2 = Math.min(this.y + this.height, rectangle.y + rectangle.height);

		this.x = x1;
		this.width = Math.max(x2 - x1, 0);
		this.y = y1;
		this.height = Math.max(y2 - y1, 0);

		return this;
	}

	/**
	 * Enlarges rectangle that way its corners lie on grid
	 *
	 * @param [resolution=1] - resolution
	 * @param [eps=0.001] - precision
	 * @return Returns itself.
	 */
	ceil(resolution = 1, eps = 0.001): this {
		const x2 = Math.ceil((this.x + this.width - eps) * resolution) / resolution;
		const y2 = Math.ceil((this.y + this.height - eps) * resolution) / resolution;

		this.x = Math.floor((this.x + eps) * resolution) / resolution;
		this.y = Math.floor((this.y + eps) * resolution) / resolution;

		this.width = x2 - this.x;
		this.height = y2 - this.y;

		return this;
	}

	/**
	 * Enlarges this rectangle to include the passed rectangle.
	 *
	 * @param rectangle - The rectangle to include.
	 * @return Returns itself.
	 */
	enlarge(rectangle: AABB): this {
		const x1 = Math.min(this.x, rectangle.x);
		const x2 = Math.max(this.x + this.width, rectangle.x + rectangle.width);
		const y1 = Math.min(this.y, rectangle.y);
		const y2 = Math.max(this.y + this.height, rectangle.y + rectangle.height);

		this.x = x1;
		this.width = x2 - x1;
		this.y = y1;
		this.height = y2 - y1;

		return this;
	}

	/**
	 *
	 * @param points
	 */
	public static fromPoints(points: number[][]) {
		const aabb = new AABB(0, 0, 0, 0);
		if (!points || points.length === 0) return aabb;
		const count = points.length;
		let point = points[0];
		aabb.x = point[0];
		aabb.y = point[1];

		for (let i = 1; i < count; i += 1) {
			point = points[i];
			const x1 = Math.min(aabb.x, point[0]);
			const y1 = Math.min(aabb.y, point[1]);
			const x2 = Math.max(aabb.x + aabb.width, point[0]);
			const y2 = Math.max(aabb.y + aabb.height, point[1]);
			aabb.x = x1;
			aabb.y = y1;
			aabb.width = x2 - x1;
			aabb.height = y2 - y1;
		}
		return aabb;
	}
}
