import { EventEmitter } from 'eventemitter3';
import { EventsSelection } from './events';
import { ISelection } from './interfaces';
import { Model } from './model';
import { TType } from './types';

export interface SelectionProps<S extends TType> {
	model: Model<S>;
}

/**
 * 数据管理容器
 * Model
 * 	--Model
 */
export class SelectionModel<S extends TType> extends EventEmitter implements ISelection<S> {
	_model: Model<S>;
	_pros?: SelectionProps<S>;
	_datas: S[] = [];
	_datasMap: Map<string, S> = new Map();
	_selectionMode: string = 'single';
	_filterFunction: (() => {}) | undefined;

	public constructor(pros?: SelectionProps<S>) {
		super();
		this._pros = pros;
		this._model = pros?.model || new Model<S>();
	}
	/**
	 * 判断是否包含
	 * @param data
	 * @returns
	 */
	contains(data: S): boolean {
		if (!data) return false;
		return !!this._datasMap.get(data._id);
	}

	/**
	 * add data to datas
	 * @param data
	 * @returns
	 */
	add(data: S) {
		const datas = this._datas,
			datasMap = this._datasMap;
		if (this.contains(data)) {
			return this;
		}
		const old: Array<S> = [];
		Object.assign(old, datas);
		datas.push(data);
		datasMap.set(data._id, data);
		return this;
	}

	/**
	 * remove data from datas
	 * @param data
	 * @returns
	 */
	remove(data: S) {
		if (!data) return this;
		const datas = this._datas,
			index = datas.indexOf(data);
		if (index >= 0) {
			datas.splice(index, 1);
			this._datasMap.delete(data._id);
		}
		return this;
	}

	/**
	 * get by index
	 * @param id
	 * @returns
	 */
	get(index: number): S | undefined {
		return this._datas[index];
	}

	/**
	 * get S By Id
	 * @param id
	 * @returns
	 */
	getById(id: string): S | undefined {
		return this._datasMap.get(id);
	}

	/**
	 * clear
	 * @returns
	 */
	clear() {
		const old = this._datas;
		if (this._datas.length === 0) return;
		this._datas = [];
		this._datasMap.clear();
		this.emit(EventsSelection.Change, {
			source: this,
			type: EventsSelection.ClearSelection,
			oldValue: old
		});
		return this;
	}

	/**
	 * get datas array
	 * @returns
	 */
	getDatas(): S[] {
		return this._datas;
	}

	/**
	 * get datas map
	 * @returns
	 */
	getDatasMap(): Map<string, S> {
		return this._datasMap;
	}

	/**
	 * 批量添加数据
	 * @param data {S[]} 添加的数据
	 * @returns
	 */
	batchAdd(data: S[]) {
		const datas = this._datas,
			datasMap = this._datasMap;
		const old: S[] = [];
		Object.assign(old, datas);
		data.forEach((d: S) => {
			if (this.contains(d)) {
				return this;
			}
			datas.push(d);
			datasMap.set(d._id, d);
		});
		return this;
	}

	getSelectionMode(): string {
		return this._selectionMode;
	}

	/**
	 * 追加选中数据
	 * @param data
	 */
	appendSelection(data: S | S[]) {
		if (!data) return;
		if (data instanceof Array) {
			this.batchAdd(data);
		} else {
			this.add(data);
		}
		this.emit(EventsSelection.Change, {
			source: this,
			type: EventsSelection.AppendSelection
		});
	}

	/**
	 * 删除选中数据
	 * @param data
	 */
	removeSelection(data: S | S[]) {
		if (!data) return;
		if (data instanceof Array) {
			data.forEach((d: S) => {
				this.remove(d);
			});
		} else {
			this.remove(data);
		}
		this.emit(EventsSelection.Change, {
			source: this,
			type: EventsSelection.RemoveSelection
		});
	}

	/**
	 * 获取当前选中的数据
	 * @returns
	 */
	getSelection(): S[] {
		return this._datas;
	}

	/**
	 * 设置选中数据
	 * @param data
	 */
	setSelection(data: S | S[]) {
		const oldSelection = this._datas;
		this.clear();
		if (!data) return;
		if (data instanceof Array) {
			this.batchAdd(data);
		} else {
			this.add(data);
		}
		this.emit(EventsSelection.Change, {
			source: this,
			type: EventsSelection.SetSelection,
			oldValue: oldSelection,
			newValue: data
		});
	}

	/**
	 * 清楚选中
	 */
	clearSelection() {
		this.clear();
		this.emit(EventsSelection.Change, {
			source: this,
			type: EventsSelection.ClearSelection
		});
	}

	/**
	 * select all
	 */
	selectAll() {
		this.emit(EventsSelection.SelectionAll, {
			source: this,
			data: this._datas
		});
	}

	/**
	 * 获取选中数据大小
	 * @returns
	 */
	size(): number {
		return this._datas.length;
	}

	/**
	 * 获取最后一个被选中的数据
	 * @returns
	 */
	getLastData(): S | undefined {
		return this._datas[this._datas.length - 1];
	}

	/**
	 * 获取第一个被选中的数据
	 * @returns
	 */
	getFirstData(): S | undefined {
		return this._datas[0];
	}
}
