import { InteractionEvent } from "pixi.js";
import { Element } from "@blood4/modeling";
import { DefaultInteraction } from "../interaction/DefaultInteraction";
import { PixiRenderer } from "../PixiRenderer";
import { BaseGizmo } from "./BaseGizmo";
import { DimensionSpot } from "./spots";

export class DimensionGizmo extends BaseGizmo {
	private _spots: Map<string | number, DimensionSpot> = new Map();
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super(element, renderer);
		this._element = element;
		this.interactive = element.styles.dimensionGizmoInteractive;
		this.buttonMode = element.styles.dimensionGizmoButtonMode;
		this.zIndex = element.styles.dimensionGizmoZIndex;
	}

	/**
	 * _invalidate
	 */
	public _invalidate(): void {
		// console.log('_invalidate');
		this.clear();
	}

	/**
	 * validate
	 */
	public _validate(): void {
		// console.log('_validate');
		this._updateStyles();
		this._validateImpl();
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		const element: Element = this._element;
		if (!element) return;
		this.visible = element.selected;
		this.interactive = element.styles.dimensionGizmoInteractive;
		this.buttonMode = element.styles.dimensionGizmoButtonMode;
		this.zIndex = element.styles.dimensionGizmoZIndex;
	}

	/**
	 * update
	 */
	protected _drawBody(): void {
		const element = this._element,
			renderer = this._renderer;
		this.clear();
		if (!element || !renderer || !this.visible) return;
		// const offsetPoints = element.getOffsetPoints(10, 'chamfer', 16) as number[][];
		const points = element.points;
		/* const result: number[][][] = [];
		for (let i = 0; i < offsetPoints.length; i += 2) {
			if (offsetPoints[i] && offsetPoints[i + 1]) {
				result.push([
					[offsetPoints[i][0], offsetPoints[i][1]],
					[offsetPoints[i + 1][0], offsetPoints[i + 1][1]]
				]);
			}
		} */
		// 边界拉伸, 每个边的中心点
		if (this._spots && this._spots.size > 0) {
			this._spots.forEach((spot: DimensionSpot, key: string | number) => {
				const f1 = points[Number(key) - 0.5];
				const t1 = points[(Number(key) - 0.5 + 1) % points.length];

				const from = {
					p1: f1,
					p2: [],
				},
					to = {
						p1: t1,
						p2: [],
					};
				spot.setFromTo(from, to);
			});
		} else {
			for (let i = 0; i < points.length; i++) {
				const f1 = points[i];
				const t1 = points[(i + 1) % points.length];
				const from = {
					p1: f1,
					p2: [],
				},
					to = {
						p1: t1,
						p2: [],
					};

				const spot = new DimensionSpot(
					element,
					from,
					to,
					renderer,
					i + 0.5
				);
				this._spots.set(i + 0.5, spot);
				this.addChild(spot);
			}
		}

		if (element.debug) {
			// points.forEach((point: number[]) => {
			// 	this.beginFill(0x000000, 1.0);
			// 	this.drawCircle(point[0], point[1], 2);
			// });
			// result.forEach((points: number[][]) => {
			// 	this.beginFill(0x00ff00, 1.0);
			// 	this.drawCircle(points[0][0], points[0][1], 2);
			// 	this.beginFill(0x0000ff, 1.0);
			// 	this.drawCircle(points[1][0], points[1][1], 2);
			// });
		}
	}

	/**
	 * set up interaction event
	 */
	public mount(): void {
		// console.log('mount dimension gizmo');
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;

		const interactionManager = renderer.interactionManager;
		const current = interactionManager.getCurrentInteraction();
		if (!(current instanceof DefaultInteraction)) return;

		// 边界拉伸, 每个边的中心点
		// if (points) {
		// 	points.forEach((point: number[], index: number) => {
		// 		const from = point,
		// 			to = points[(index + 1) % points.length];
		// 		const spot = new DimensionSpot(element, from, to, renderer, index + 0.5);
		// 		this._spots.set(index + 0.5, spot);
		// 		this.addChild(spot);
		// 	});
		// }
	}

	/**
	 * tear down interaction event
	 */
	public unmount(): void {
		// console.log('unmount Dimension gizmo');
		// if (this._spots) {
		// 	this._spots.forEach((spot: DimensionSpot) => {
		// 		spot.unmount();
		// 		this.removeChild(spot);
		// 	});
		// 	this._spots.clear();
		// }
		this._invalidate();
		this._validate();
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void { }
}
