import { Text, Rectangle, Matrix, Texture, BaseTexture } from "pixi.js";
import { Element, ElementType, FillType } from "@blood4/modeling";
import { UI } from ".";
import { union } from "../utils";
import { PixiRenderer } from "../PixiRenderer";

export class NodeUI extends UI {
	text?: Text;
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super(element, renderer);

	}

	/**
	 * 计算UI部分的Bounds,包含body 、gizmo、icon and so on;
	 * @returns
	 */
	calculateBounds(): void {
		const element = this._element as Element;
		if (!element) return;
		const boundingbox = element.getAABB();
		const rect1 = new Rectangle(
			boundingbox.x,
			boundingbox.y,
			boundingbox.width,
			boundingbox.height
		);
		if (this.text) {
			const text = this.text;
			const rect: Rectangle = new Rectangle();
			text.getLocalBounds(rect);
			union(
				rect1,
				new Rectangle(text.x, text.y, rect.width, rect.height),
				rect1
			);
		}
		this._boundsRect = rect1;
	}

	/**
	 * update global styles from view
	 * @returns
	 */
	protected _updateStyles(): void {
		super._updateStyles();
		const element = this._element as Element;
		if (!element) return;
	}

	/**
	 * 绘制 BoundingBox
	 */
	protected _drawBoundingBox() {
		const element = this._element as Element;
		if (!element) return;
		const rect: Rectangle = new Rectangle();
		if (this.text) {
			this.text.getLocalBounds(rect);
		}
		const boundingbox = element.getBoundingBox(10);
		this.lineStyle(1, 0xff6666, 1.0);
		this.beginFill(0x0000, 0.0);
		this.drawRect(
			boundingbox.x,
			boundingbox.y,
			boundingbox.width,
			boundingbox.height
		);
		this.endFill();

		const boundingbox2 = this._boundsRect;
		this.lineStyle(1, 0xff0033, 1.0);
		this.beginFill(0x0000, 0.0);
		this.drawRect(
			boundingbox2.x,
			boundingbox2.y,
			boundingbox2.width,
			boundingbox2.height
		);
		this.endFill();

		if (this.text) {
			const rect: Rectangle = new Rectangle();
			if (this.text) {
				this.text.getLocalBounds(rect);
			}
			const viewport: any = this.parent;
			const scale = viewport.scaled || 1.0;
			this.lineStyle(1, 0xffff66, 1.0);
			this.beginFill(0x0000, 0.0);
			this.drawRect(this.text.x, this.text.y, rect.width, rect.height);
			this.endFill();
		}
	}

	/**
	 * 绘制AABB Bounds
	 */
	protected _drawAABB() {
		const element = this._element as Element;
		if (!element) return;
		const aabb = element.getAABB();
		this.lineStyle(1, 0x00ff00, 1.0);
		this.beginFill(0xffff00, 0.0);
		this.drawRect(aabb.x, aabb.y, aabb.width, aabb.height);
		this.endFill();
	}

	/**
	 * 绘制OBB Bounds
	 */
	protected _drawOBB() {
		const element = this._element as Element;
		if (!element) return;
		const obb = element.getOBB();
		const points = obb.points;
		this.lineStyle(1, 0xffff00, 1.0);
		this.beginFill(0xffff00, 0.0);
		const path: number[] = [];
		points.forEach((point: number[]) => {
			path.push(point[0], point[1]);
		});
		this.drawPolygon(path);
	}

	/**
	 * draw Body
	 */
	protected _drawBody(): void {
		const element = this._element as Element;
		if (!element) return;

		const points = element.points;
		if (!points || points.length === 0) return;

		const path: number[] = [];
		points.forEach((point: any) => {
			path.push(point[0], point[1]);
		});
		const selected = element.selected;
		const fill = selected ? element.styles.selectFill : element.styles.fill,
			fillColor = selected
				? element.styles.selectFillColor
				: element.styles.fillColor,
			fillAlpha = selected
				? element.styles.selectFillAlpha
				: element.styles.fillAlpha,
			fillImage = element.styles.fillImage,
			fillVector = element.styles.fillVector,
			fillType = element.styles.fillType,
			strokeColor = element.styles.strokeColor,
			strokeWidth = element.styles.strokeWidth,
			strokeAlpha = element.styles.strokeAlpha,
			closed = element.styles.shapeClosed;

		if (!fill) return;
		const box2d = element.getAABB();
		if (element.image) {
			if (!this._imageCache) {
				this.loadImage(element.image)
					.then((res) => {
						if (res instanceof HTMLImageElement) {
							this._imageCache = res;
							this.update();
						}
					})
					.catch((e) => { });
			}
		}
		if (
			fillType === FillType.Default ||
			fillType === FillType.ImageVector
		) {
			if (fillVector) {
				this.lineStyle(strokeWidth, strokeColor, strokeAlpha);
				this.beginFill(fillColor, fillAlpha);
				this.drawPolygon(path);
				this.endFill();
			}

			if (fillImage && this._imageCache) {
				// this.lineStyle(strokeWidth, strokeColor, strokeAlpha);
				this.beginFill(fillColor, fillAlpha);
				let matrix = new Matrix();
				matrix = matrix
					.translate(-box2d.x, -box2d.y)
					.scale(
						this._imageCache.width / box2d.width,
						this._imageCache.height / box2d.height
					);

				if (!this._textureCache) {
					const texture = new Texture(
						new BaseTexture(this._imageCache)
					);
					this._textureCache = texture;
				}
				this.fill.visible = fillImage;
				this.fill.texture = this._textureCache;
				this.fill.matrix = matrix;
				this.fill.color = 0xffffff;
				this.drawPolygon(path);
				this.endFill();
			}
		} else if (fillType === FillType.VectorImage) {
			if (fillImage && this._imageCache) {
				let matrix = new Matrix();
				matrix = matrix
					.translate(-box2d.x, -box2d.y)
					.scale(
						this._imageCache.width / box2d.width,
						this._imageCache.height / box2d.height
					);

				if (!this._textureCache) {
					const texture = new Texture(
						new BaseTexture(this._imageCache)
					);
					this._textureCache = texture;
				}
				this.fill.visible = fillImage;
				this.fill.texture = this._textureCache;
				this.fill.matrix = matrix;
				this.fill.color = 0xffffff;
				this.drawPolygon(path);
				this.endFill();
			}
			if (fillVector) {
				this.lineStyle(strokeWidth, strokeColor, strokeAlpha);
				this.beginFill(fillColor, fillAlpha);
				this.drawPolygon(path);
				this.endFill();
			}
		}

		closed && this.closePath();
	}
}
