import * as PIXI from "pixi.js";
import { GuideDataType, GuideOptions } from "./type";
import { DashLine } from "../dashline";

export class Guide extends PIXI.Graphics {
	private readonly _graphics: PIXI.Graphics;
	private _graphicsDash: DashLine;
	private _dirty: boolean = true;
	private _datas: GuideDataType[] = [];

	constructor(options: Partial<GuideOptions> = {}) {
		super();
		this.set(options);
		this._graphics = new PIXI.Graphics();
		this._graphicsDash = new DashLine(this, {
			dash: [1, 1],
			width: 1,
			color: 0x000000,
			alpha: 1.0,
			useTexture: false,
			useDots: true,
		});
		this.addChild(this._graphics);
		this._dirty = true;
	}

	public get datas(): GuideDataType[] {
		return this._datas;
	}

	public set datas(value: GuideDataType[]) {
		if (this._datas === value) return;
		this._datas = value;
		this._dirty = true;
	}

	/**
	 * Apply graph options
	 * @param {GuideOptions} options - Options to be applied
	 */
	public set(options: Partial<GuideOptions> = {}) {
		Object.assign(this, options);
		this._dirty = true;
	}

	/**
	 * On invalidation, rendering operations to redraw.
	 *
	 * @override
	 * @param {PIXI.Rectangle} renderer
	 */
	public render(renderer: PIXI.Renderer) {
		super.render(renderer);
		if (!this._dirty) return;
		this._dirty = false;
		this._graphics.clear();
		this._graphicsDash.clear();
		this.clear();
		this.renderDatas();
	}

	private renderDatas() {
		const datas = this._datas;
		datas.forEach((data: GuideDataType, index: number) => {
			const { from, to, type, dash } = data;
			if (dash) {
				this._graphicsDash.setStyle({
					dash: dash || [1, 1],
					width: 1,
					color: 0x011641,
					alpha: 0.2,
					useTexture: false,
					useDots: true,
					scale: 1,
					alignment: 0.5,
				});
				this._graphicsDash.moveTo(from[0], from[1]);
				this._graphicsDash.lineTo(to[0], to[1]);
				this._graphicsDash.closePath();
			} else {
				this._graphics.lineStyle(1, 0x011641, 0.2);
				this._graphics.moveTo(from[0], from[1]);
				this._graphics.lineTo(to[0], to[1]);
				this._graphics.closePath();
			}
		});
	}
}
