import { Text, Rectangle, Polygon, DisplayObject } from "pixi.js";
import { Element, Line, OBB } from "@blood4/modeling";
import { UI } from "./UI";
import { DashLine } from "../components";
import { PixiRenderer } from "../PixiRenderer";

export class LineUI extends UI {
	text?: Text;
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super(element, renderer);
		// const shadowFilter = new DropShadowFilter({
		// 	rotation: 0,
		// 	distance: 0,
		// 	color: 0xff0000,
		// 	alpha: 1.0,
		// 	shadowOnly: false,
		// 	blur: 2,
		// 	quality: 7
		// });
		// this.filters = [shadowFilter];
	}

	/**
	 * update global styles from view
	 * @returns
	 */
	protected _updateStyles(): void {
		super._updateStyles();
		const element = this._element as Element;
		if (!element) return;
		const points = element.getHitArea(2);
		let gizmo = this._gizmos.get("editorGizmo");
		if (gizmo) {
			const unionPoints: number[][] = points.map((point: number[]) => {
				return [point[0], point[1]];
			});
			gizmo.children.forEach((child: DisplayObject) => {
				const localBounds = child.getLocalBounds();
				unionPoints.push([localBounds.x, localBounds.y]);
				unionPoints.push([
					localBounds.x,
					localBounds.y + localBounds.height,
				]);
				unionPoints.push([
					localBounds.x + localBounds.width,
					localBounds.y + localBounds.height,
				]);
				unionPoints.push([
					localBounds.x + localBounds.width,
					localBounds.y,
				]);
			});
			const obb = new OBB(unionPoints);
			const path: number[] = [];
			obb.points.forEach((point: number[]) => {
				path.push(point[0], point[1]);
			});
			this.hitArea = new Polygon(path);
		} else {
			const path: number[] = [];
			points.forEach((point: number[]) => {
				path.push(point[0], point[1]);
			});
			this.hitArea = new Polygon(path);
		}
	}

	/**
	 * 绘制 BoundingBox
	 */
	protected _drawBoundingBox() {
		const element = this._element as Element;
		if (!element) return;
		const rect: Rectangle = new Rectangle();
		if (this.text) {
			this.text.getLocalBounds(rect);
		}
		const boundingbox = element.getBoundingBox(10);
		this.lineStyle(1, 0xff6666, 1.0);
		this.beginFill(0x0000, 0.0);
		this.drawRect(
			boundingbox.x,
			boundingbox.y,
			boundingbox.width,
			boundingbox.height
		);
		this.endFill();

		const boundingbox2 = this._boundsRect;
		this.lineStyle(1, 0xff0033, 1.0);
		this.beginFill(0x0000, 0.0);
		this.drawRect(
			boundingbox2.x,
			boundingbox2.y,
			boundingbox2.width,
			boundingbox2.height
		);
		this.endFill();

		if (this.text) {
			const rect: Rectangle = new Rectangle();
			if (this.text) {
				this.text.getLocalBounds(rect);
			}
			const viewport: any = this.parent;
			const scale = viewport.scaled || 1.0;
			this.lineStyle(1, 0xffff66, 1.0);
			this.beginFill(0x0000, 0.0);
			this.drawRect(this.text.x, this.text.y, rect.width, rect.height);
			this.endFill();
		}
	}

	/**
	 * 绘制AABB Bounds
	 */
	protected _drawAABB() {
		const element = this._element as Element;
		if (!element) return;
		const aabb = element.getAABB();
		this.lineStyle(1, 0x00ff00, 1.0);
		this.beginFill(0xffff00, 0.0);
		this.drawRect(aabb.x, aabb.y, aabb.width, aabb.height);
		this.endFill();
		this.lineStyle(1, 0x000000, 1.0);
		this.drawEllipse(
			aabb.x + aabb.width / 2,
			aabb.y + aabb.height / 2,
			aabb.width / 2,
			aabb.height / 2
		);
		this.closePath();
	}

	/**
	 * 绘制OBB Bounds
	 */
	protected _drawOBB() {
		const element = this._element as Element;
		if (!element) return;
		const obb = element.getOBB();
		const points = obb.points;
		this.lineStyle(2, 0xffff00, 1.0);
		this.beginFill(0xffff00, 0.0);
		const path: number[] = [];
		points.forEach((point: number[]) => {
			path.push(point[0], point[1]);
		});
		this.drawPolygon(path);
		this.closePath();

		if (element.debug) {
			const points4 = element.getHitArea(2);
			this.lineStyle(1, 0x99cc33, 1.0);
			this.beginFill(0x99cc33, 0.1);
			const path4: number[] = [];
			points4.forEach((point: number[]) => {
				path4.push(point[0], point[1]);
			});
			this.drawPolygon(path4);
		}
		let gizmo = this._gizmos.get("editorGizmo");
		if (gizmo) {
			const unionPoints: number[][] = points.map((point: number[]) => {
				return [point[0], point[1]];
			});
			gizmo.children.forEach((child: DisplayObject) => {
				const localBounds = child.getLocalBounds();
				unionPoints.push([localBounds.x, localBounds.y]);
				unionPoints.push([
					localBounds.x,
					localBounds.y + localBounds.height,
				]);
				unionPoints.push([
					localBounds.x + localBounds.width,
					localBounds.y + localBounds.height,
				]);
				unionPoints.push([
					localBounds.x + localBounds.width,
					localBounds.y,
				]);
			});
			const obb = new OBB(unionPoints);
			const path: number[] = [];
			obb.points.forEach((point: number[]) => {
				path.push(point[0], point[1]);
			});
			this.drawPolygon(path);
		} else {
			const path: number[] = [];
			points.forEach((point: number[]) => {
				path.push(point[0], point[1]);
			});
			this.drawPolygon(path);
		}
	}

	/**
	 * draw Body
	 */
	protected _drawBody(): void {
		const element = this._element as Line;

		if (!element) return;
		// const points = element.points;
		// if(!points || points.length === 0) return;

		// const matrix = element._matrix, angle = element.angle;
		// if (angle !== 0) {
		// 	this.setMatrix(new Matrix(matrix[0], matrix[1], matrix[2], matrix[3], matrix[4], matrix[5]));
		// }
		// if (element.selected) {
		// 	this.lineStyle(1, 0x000000, 1.0);
		// 	this.beginFill(0x66CC99, 0.6);
		// } else {
		// 	this.lineStyle(1, 0x000000, 1.0);
		// 	this.beginFill(0x99CC99, 0.6);
		// }
		// const path: number[] = [];
		// points.forEach((point: any) => {
		// 	path.push(point[0], point[1]);
		// });
		// this.drawPolygon(path);
		// // this.drawEllipse(element.x, element.y, element.width / 2, element.height / 2);
		// this.endFill();
		// if (angle !== 0) {
		// 	this.setMatrix(new Matrix());
		// }
		const from = element.from,
			to = element.to;

		if (element.selected) {
			const points4 = element.getHitArea(2);
			this.lineStyle(1, 0x000000, 0.0);
			this.beginFill(0x000000, 0.05);
			const path4: number[] = [];
			points4.forEach((point: number[]) => {
				path4.push(point[0], point[1]);
			});
			this.drawPolygon(path4);
			this.lineStyle(
				element.styles.selectStrokeWidth || 1,
				element.styles.selectStrokeColor || 0x000000,
				element.styles.selectStrokeAlpha || 1.0
			);
		} else {
			this.lineStyle(
				element.styles.strokeWidth || 1,
				element.styles.strokeColor || 0x000000,
				element.styles.strokeAlpha || 1.0
			);
		}
		const lineDash = element.dash;
		if (lineDash) {
			const dash = new DashLine(this, {
				dash: lineDash,
				width: element.styles.strokeWidth || 1,
				color: element.styles.strokeColor,
				alpha: element.styles.strokeAlpha,
				useTexture: false,
				useDots: true,
			});
			dash.moveTo(from[0], from[1]).lineTo(to[0], to[1]);
		} else {
			this.moveTo(from[0], from[1]);
			this.lineTo(to[0], to[1]);
			this.endFill();
		}

		if (element.debug) {
			const offset = -1 * element.styles.offsetDelta;
			const alongPoints = element.getAlongPoints(offset);
			this.lineStyle(1, 0xff0000, 1.0);
			this.moveTo(alongPoints.from[0], alongPoints.from[1]);
			this.lineTo(alongPoints.to[0], alongPoints.to[1]);
			this.endFill();
		}
	}

	protected _drawSelect(): void {
		const element = this._element as Element;
		if (!element) return;
		const points = element.points;
		if (!points || points.length === 0) return;
	}

	protected _drawHover(): void {
		const element = this._element as Line;
		if (!element) return;
		if (element.hovered && !element.selected) {
			const from = element.from,
				to = element.to,
				lineDash = element.dash;

			const points4 = element.getHitArea(2);
			this.lineStyle(1, 0x000000, 0.0);
			this.beginFill(0x000000, 0.05);
			const path4: number[] = [];
			points4.forEach((point: number[]) => {
				path4.push(point[0], point[1]);
			});

			this.drawPolygon(path4);

			this.lineStyle(
				element.styles.hoverStrokeWidth || 1,
				element.styles.hoverStrokeColor || 0xff0000,
				element.styles.hoverStrokeAlpha || 1.0
			);
			if (lineDash) {
				const dash = new DashLine(this, {
					dash: lineDash,
					width: element.styles.strokeWidth || 1,
					color: element.styles.selectFillColor,
					alpha: element.styles.strokeAlpha,
					useTexture: false,
					useDots: true,
				});
				dash.moveTo(from[0], from[1]).lineTo(to[0], to[1]);
			} else {
				this.moveTo(from[0], from[1]);
				this.lineTo(to[0], to[1]);
				this.endFill();
			}
		}
	}

	/**
	 * check all gizmos
	 */
	protected _checkGizmos(): void {
		const element = this._element;
		if (!element) return;
		this._checkLabelGizmo();
		this._checkLabel2Gizmo();
		this._checkEditorGizmo();
	}
}
