import { Point, Rectangle } from "../shapes";
import { mat2d, vec2, vec3 } from "gl-matrix";

/**
 *
 * @param points
 * @returns
 */
export function aabb(points: vec2[] | number[][]): Rectangle {
	const aabb = new Rectangle(0, 0, 0, 0);
	if (!points || points.length === 0) return aabb;
	const count = points.length;
	let point = points[0];
	aabb.x = point[0];
	aabb.y = point[1];

	for (let i = 1; i < count; i += 1) {
		point = points[i];
		const x1 = Math.min(aabb.x, point[0]);
		const y1 = Math.min(aabb.y, point[1]);
		const x2 = Math.max(aabb.x + aabb.width, point[0]);
		const y2 = Math.max(aabb.y + aabb.height, point[1]);
		aabb.x = x1;
		aabb.y = y1;
		aabb.width = x2 - x1;
		aabb.height = y2 - y1;
	}
	return aabb;
}

/**
 * union rectangle
 * @param a
 * @param b
 * @param out
 * @returns
 */
export function union(a: Rectangle, b: Rectangle, out?: Rectangle): Rectangle {
	if (!out) {
		// eslint-disable-next-line no-param-reassign
		(out as any) = new Rectangle();
	}

	const x1 = Math.min(a.x, b.x);
	const x2 = Math.max(a.x + a.width, b.x + b.width);
	const y1 = Math.min(a.y, b.y);
	const y2 = Math.max(a.y + a.height, b.y + b.height);

	if (out) {
		out.x = x1;
		out.y = y1;
		out.width = x2 - x1;
		out.height = y2 - y1;
		return out;
	}
	return new Rectangle();
}

/**
 * If the area of the intersection between the Rectangles `b` and `a` is not zero,
 * returns the area of intersection as a Rectangle object. bwise, return an empty Rectangle
 * with its properties set to zero.
 * Rectangles without area (width or height equal to zero) can't intersect or be intersected
 * and will always return an empty rectangle with its properties set to zero.
 *
 * _Note: Only available with **@pixi/math-extras**._
 *
 * @method intersects
 * @memberof PIXI.Rectangle#
 * @param {Rectangle} b - The Rectangle to intersect with `a`.
 * @param {Rectangle} [out] - A Rectangle object in which to store the value,
 * optional (bwise will create a new Rectangle).
 * @returns {Rectangle} The intersection of `a` and `b`.
 */
export function intersection(
	a: Rectangle,
	b: Rectangle,
	out?: Rectangle
): Rectangle {
	if (!out) {
		// eslint-disable-next-line no-param-reassign
		(out as any) = new Rectangle();
	}
	const x0 = a.x < b.x ? b.x : a.x;
	const x1 = a.right > b.right ? b.right : a.right;
	if (out) {
		if (x1 <= x0) {
			out.x = out.y = out.width = out.height = 0;

			return out;
		}

		const y0 = a.y < b.y ? b.y : a.y;
		const y1 = a.bottom > b.bottom ? b.bottom : a.bottom;

		if (y1 <= y0) {
			out.x = out.y = out.width = out.height = 0;

			return out;
		}

		out.x = x0;
		out.y = y0;
		out.width = x1 - x0;
		out.height = y1 - y0;

		return out;
	}
	return new Rectangle();
}

/**
 *
 * @param min
 * @param max
 * @param x
 * @returns
 */
export function clamp(min: number, max: number, x: number) {
	return Math.min(Math.max(x, min), max);
}

/**
 * 判断一个点是否在多边形内部
 * @param points
 * @param point
 * @returns
 */
export function isPointInPolygon(points: Point[], point: Point): boolean {
	let i = 0,
		j = 0,
		c = false,
		size = points.length;
	for (i = 0, j = size - 1; i < size; j = i++) {
		var p1 = points[i];
		var p2 = points[j];
		if (
			p1.y > point.y != p2.y > point.y &&
			point.x < ((p2.x - p1.x) * (point.y - p1.y)) / (p2.y - p1.y) + p1.x
		) {
			c = !c;
		}
	}
	return c;
}

/**
 * 按照逆时针，左上角为起始点进行排序
 * @param points
 * @returns
 */
export function sortCCW(points: number[][], axisX?: number[]): number[][] {
	let center = points[0];
	if (!axisX) center = points[0];
	if (axisX && axisX[1] === 0) {
		points.sort((a, b) => a[1] - b[1]);
		center = points[0][0] < points[1][0] ? points[0] : points[1];
	} else if (axisX && axisX[0] === 0) {
		points.sort((a, b) => a[1] - b[1]);
		center = points[0][0] > points[1][0] ? points[0] : points[1];
	} else if (axisX && axisX[0] * axisX[1] > 0) {
		points.sort((a, b) => a[0] - b[0]);
		center = points[0];
	} else if (axisX && axisX[0] * axisX[1] < 0) {
		points.sort((a, b) => a[1] - b[1]);
		center = points[0];
	}

	const angles = points.map((point: number[]) => {
		const angle = vec2.angle(
			vec2.fromValues(1, 0),
			vec2.fromValues(point[0] - (center[0] + 1), -(point[1] - center[1]))
		);
		return [point[0], point[1], angle];
	});

	const sorted = angles.sort((a, b) => a[2] - b[2]);
	return sorted;
}

function nearest(v1: vec2, v2s: vec2[]) {
	let max = Number.MAX_VALUE,
		nearest = null,
		current = 0;
	v2s.forEach((v2: vec2) => {
		current = vec2.distance(v1, v2);
		if (current <= max) {
			max = current;
			nearest = v2;
		}
	});
	return nearest;
}

/**
 * 判断是否为凹多变性
 * @param points
 * @returns
 */
export function isConvex(points: number[][] = []) {
	const { length } = points;
	let pre = 0,
		curr = 0;
	for (let i = 0; i < length; ++i) {
		let dx1 = points[(i + 1) % length][0] - points[i][0];
		let dx2 = points[(i + 2) % length][0] - points[(i + 1) % length][0];
		let dy1 = points[(i + 1) % length][1] - points[i][1];
		let dy2 = points[(i + 2) % length][1] - points[(i + 1) % length][1];
		curr = dx1 * dy2 - dx2 * dy1;
		if (curr != 0) {
			if ((curr > 0 && pre < 0) || (curr < 0 && pre > 0)) return false;
			else pre = curr;
		}
	}
	return true;
}

/**
 * 判断是否为矩形
 * @param points
 * @returns
 */
export function isRectangle(points: number[][]): boolean {
	if (points.length !== 4) {
		return false;
	} else {
		const d02 =
			(points[0][0] - points[2][0]) * (points[0][0] - points[2][0]) +
			(points[0][1] - points[2][1]) * (points[0][1] - points[2][1]);
		const d13 =
			(points[1][0] - points[3][0]) * (points[1][0] - points[3][0]) +
			(points[1][1] - points[3][1]) * (points[1][1] - points[3][1]);
		return Math.abs(d02 - d13) < 1;
	}
}
