import EventEmitter from "eventemitter3";
import { Graphics, InteractionEvent, Point } from "pixi.js";
import { Element } from "@blood4/modeling";
import { KeyboardEvents } from "../events";
import { PixiRenderer } from "../PixiRenderer";

export class BaseGizmo extends Graphics {
	protected _renderer?: PixiRenderer<any>;
	protected _element: Element;
	protected _mouseDownEvent: any;
	protected _mouseUpEvent: any;
	protected _mouseMoveEvent: any;
	protected _mouseOverEvent: any;
	protected _mouseOutEvent: any;
	protected _mousePoint: Point = new Point(0, 0);
	_keydownEvent!: (evt: KeyboardEvent) => void;
	_keyupEvent!: (evt: KeyboardEvent) => void;
	_ctrlDown: boolean = false;
	_shiftDown: boolean = false;
	_altDown: boolean = false;
	_eventEmitter: EventEmitter = new EventEmitter();

	protected _isDragging = false;

	constructor(element: Element, renderer: PixiRenderer<any>) {
		super();
		this.interactive = true;
		this.buttonMode = true;
		this._element = element;
		this._renderer = renderer;
		this.zIndex = element.styles.zIndex + 1;
		this.visible = element.visible;
		this.cursor = element.styles.cursor;
	}

	/**
	 * set up interaction event
	 */
	public mount(): void {
		this._mouseDownEvent = this.handle_mousedown.bind(this);
		// this._mouseUpEvent = this.handle_mouseup.bind(this);
		// this._mouseMoveEvent = this.handle_mousemove.bind(this);
		// this.on('mousedown', this._mouseDownEvent);
		// this.on('mouseup', this._mouseUpEvent);
		// this.on('mousemove', this._mouseMoveEvent);
	}

	/**
	 * tear down interaction event
	 */
	public unmount(): void {
		// this.off('mousedown', this._mouseDownEvent);
		// this.off('mouseup', this._mouseUpEvent);
		// this.off('mousemove', this._mouseMoveEvent);
	}

	/**
	 * _invalidate
	 */
	public _invalidate(): void {
		// console.log('_invalidate');
		this.clear();
	}

	/**
	 * validate
	 */
	public _validate(): void {
		// console.log('_validate');
		this._updateStyles();
		this._validateImpl();
	}

	protected _validateImpl(): void {
		this._draw();
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		// const element: Element = this.element;
		// if (element instanceof Node) {
		// 	const bounds = element.getBounds();
		// 	this.pivot.x = bounds.x + bounds.width / 2;
		// 	this.pivot.y = bounds.y + bounds.height / 2;
		// 	this.x = bounds.x + bounds.width / 2;
		// 	this.y = bounds.y + bounds.height / 2;
		// 	// this.angle = -element.angle;
		// }
	}

	/**
	 *
	 * @returns
	 */
	public update(): void {
		this._invalidate();
		this._validate();
	}

	protected _draw() {
		this.clear();
		const element = this._element;
		if (!element || !element.visible) return;
		// const matrix = element._matrix,
		// 	angle = element.angle;
		// if (angle !== 0) {
		// 	this.setMatrix(new Matrix(matrix[0], matrix[1], matrix[2], matrix[3], matrix[4], matrix[5]));
		// }
		this._drawBody();
		if (this._element && this._element.debug) {
			this._drawAABB();
		}

		// if (angle !== 0) {
		// 	this.setMatrix(new Matrix());
		// }
	}

	/**
	 * draw body
	 */
	protected _drawBody() { }

	/**
	 * 绘制AABB Bounds
	 */
	protected _drawAABB() {
		// const aabb = this.getBounds();
		// this.lineStyle(1, 0xff0000, 1.0);
		// this.beginFill(0xff0000, 0.2);
		// this.drawRect(aabb.x, aabb.y, aabb.width, aabb.height);
		// this.endFill();
	}
	/**
	 * handle mouse down event
	 * @param evt
	 */
	protected handle_mousedown(evt: InteractionEvent) {
		evt.stopPropagation();
		const ui2d = evt.currentTarget as BaseGizmo;
		const element = ui2d._element;
		if (element.movable) {
			this.__dragStart(evt);
			// if (evt !== undefined) {
			//     evt.stopPropagation();
			// }
		}
	}

	/**
	 * handle mouse up event
	 * @param evt
	 */
	handle_mouseup(evt: InteractionEvent) {
		this.__dragEnd(evt);
	}

	/**
	 * handle mouse move event
	 * @param evt
	 */
	protected handle_mousemove(evt: InteractionEvent) {
		// console.log(e.type);
		this.__dragMove(evt);
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseout(evt: InteractionEvent): void {
		this.__dragEnd(evt);
	}

	/**
	 * drag start
	 * @param evt
	 */
	protected __dragStart(evt: InteractionEvent) { }

	/**
	 * drag End
	 * @param evt
	 */
	protected __dragEnd(evt: InteractionEvent) { }

	/**
	 * drag Move
	 * @param evt
	 */
	protected __dragMove(evt: InteractionEvent) { }

	/**
	 * destory
	 */
	public destroy(): void { }

	/**
	 * key down event
	 * @param scope
	 * @param evt
	 */
	protected handle_keydown(evt: KeyboardEvent): void {
		const key = evt.code;
		// control key
		const oldCtrlDown = this._ctrlDown;
		this._ctrlDown = this.isCtrlDown(evt);
		const newCtrlDown = this._ctrlDown;
		if (oldCtrlDown !== newCtrlDown) {
			this._eventEmitter.emit(KeyboardEvents.CtrlKeyDown, {
				source: this,
				key,
				oldCtrlDown,
				newCtrlDown,
			});
		}

		// control key
		const oldShiftDown = this._shiftDown;
		this._shiftDown = this.isShiftDown(evt);
		const newShiftDown = this._shiftDown;
		if (oldShiftDown !== newShiftDown) {
			this._eventEmitter.emit(KeyboardEvents.ShiftKey, {
				source: this,
				key,
				oldShiftDown,
				newShiftDown,
			});
		}

		// control key
		const oldAltDown = this._altDown;
		this._altDown = this.isAltDown(evt);
		const newAltDown = this._altDown;
		if (oldAltDown !== newAltDown) {
			this._eventEmitter.emit(KeyboardEvents.AltKey, {
				source: this,
				key,
				oldAltDown,
				newAltDown,
			});
		}
	}

	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_keyup(evt: KeyboardEvent): void {
		const key = evt.code;
		// control key
		const oldCtrlDown = this._ctrlDown;
		this._ctrlDown = this.isCtrlDown(evt);
		const newCtrlDown = this._ctrlDown;
		if (oldCtrlDown !== newCtrlDown) {
			this._eventEmitter.emit(KeyboardEvents.CtrlKeyUp, {
				source: this,
				key,
				oldCtrlDown,
				newCtrlDown,
			});
		}

		// control key
		const oldShiftDown = this._shiftDown;
		this._shiftDown = this.isShiftDown(evt);
		const newShiftDown = this._shiftDown;
		if (oldShiftDown !== newShiftDown) {
			this._eventEmitter.emit(KeyboardEvents.ShiftKey, {
				source: this,
				key,
				oldShiftDown,
				newShiftDown,
			});
		}

		// control key
		const oldAltDown = this._altDown;
		this._altDown = this.isAltDown(evt);
		const newAltDown = this._altDown;
		if (oldAltDown !== newAltDown) {
			this._eventEmitter.emit(KeyboardEvents.AltKey, {
				source: this,
				key,
				oldAltDown,
				newAltDown,
			});
		}
	}

	isCtrlDown(evt: KeyboardEvent) {
		return evt.ctrlKey || evt.metaKey;
	}

	isShiftDown(evt: KeyboardEvent) {
		return evt.shiftKey;
	}

	isAltDown(evt: KeyboardEvent) {
		return evt.altKey;
	}
}
