import EventEmitter from "eventemitter3";
import { Viewport } from "pixi-viewport";
import { Application, DisplayObject, Rectangle } from "pixi.js";
import * as Stats from "stats.js";
import {
	Model,
	EventsModel,
	SelectionModel,
	EventsSelection,
} from "@blood4/models";

import { Element, Node, Events, ElementType, SHAPE } from "@blood4/modeling";
import {
	UI,
	NodeUI,
	LineUI,
	ShapeUI,
	EdgeUI,
	ShapeLineUI,
	GroupUI,
} from "./ui";
import { Grid, GridStyle, Guide, RefLine } from "./components";
import { InteractionManager, DefaultInteraction } from "./interaction";
export interface PixiRendererConfig {
	canvas?: HTMLCanvasElement;
	stats?: Stats;
}

export interface Grid2DDescriptor {
	canvas: HTMLCanvasElement;
}

/**
 * PixiRenderer
 */
export class PixiRenderer<M extends Model<Element>> extends Application {
	private _config: PixiRendererConfig;
	private _canvas?: HTMLCanvasElement;
	private _viewrect?: Rectangle = new Rectangle();
	private _enabled?: boolean = true;
	private _stats?: Stats;
	private _model?: M;
	private _uis: Map<string, UI> = new Map();
	eventEmitter: EventEmitter = new EventEmitter();
	interactionManager: InteractionManager;
	viewport?: Viewport;
	selectionModel?: SelectionModel<Element>;
	grid?: Grid;
	guide: Guide;
	refline: RefLine;
	_timeout: NodeJS.Timeout | null = null;

	constructor(config: PixiRendererConfig) {
		super({
			width: config.canvas?.clientWidth || 0,
			height: config.canvas?.clientHeight || 0,
			resolution: window.devicePixelRatio || 2,
			antialias: true,
			backgroundAlpha: 1.0,
			view: config.canvas as HTMLCanvasElement,
			resizeTo: window,
		});
		this._config = config || {};
		this._canvas = config.canvas || document.createElement("canvas");
		const canvas = this._canvas;
		const rect = canvas.getBoundingClientRect();

		this.renderer.backgroundColor = 0xffffff;
		this.renderer.resize(rect.width, rect.height);

		const viewport = new Viewport({
			screenWidth: rect.width,
			screenHeight: rect.height,
			worldWidth: rect.width,
			worldHeight: rect.height,
			interaction: this.renderer.plugins.interaction,
			disableOnContextMenu: true,
		});
		this.stage.addChild(viewport);
		/* const shadowFilter = new DropShadowFilter({
			rotation: 0,
			distance: 0,
			color: 0xff0000,
			alpha: 1.0,
			shadowOnly: false,
			blur: 2,
			quality: 7
		});
		viewport.filters = [shadowFilter]; */
		this.viewport = viewport;
		viewport.sortableChildren = true;
		viewport.children.sort((a: DisplayObject, b: DisplayObject) => {
			a.zIndex = a.zIndex || 0;
			b.zIndex = b.zIndex || 0;
			return b.zIndex - a.zIndex;
		});
		this._viewrect = new Rectangle(rect.x, rect.y, rect.width, rect.height);
		this.selectionModel = new SelectionModel();
		this.selectionModel.on(EventsSelection.ClearSelection, (e: any) => {
			const { oldValue } = e;
			if (oldValue) {
				oldValue.forEach((old: Element) => {
					old.selected = false;
				});
			}
		});
		this.selectionModel.on(EventsSelection.Change, (e: any) => {
			this.handleSelectionChange(e);
		});
		this.interactionManager = new InteractionManager(this);

		const grid = new Grid({
			graphWidth: 2000,
			graphHeight: 2000,
		});
		grid.set(GridStyle.dark());

		this.grid = grid;
		grid.visible = false;
		viewport.addChild(grid);
		this.guide = new Guide();
		viewport.addChild(this.guide);
		this.refline = new RefLine();
		viewport.addChild(this.refline);
		this.stopTimer();
		this.startTimer();
	}

	/**
	 * render loop
	 * @returns
	 */
	public render(): void {
		if (!this._enabled) return;
		const stats = this._stats;
		stats && stats.update();
		super.render();
	}

	/**
	 *
	 * @param v
	 */
	public enable(v = false): void {
		if (!this._canvas) return;
		if (v) {
			this._canvas.style.display = "block";
		} else {
			this._canvas.style.display = "none";
		}
		this._enabled = v;
	}

	/**
	 * set model
	 */
	public set model(model: M | undefined) {
		if (!model) return;
		this._model = model;
		this._model.on(EventsModel.Add, (e: any) => {
			const { source, data } = e;
			if (data) {
				this._add(data);
			}
		});

		this._model.on(EventsModel.BatchAdd, (e: any) => {
			const { source, data } = e;
			if (data && data instanceof Array) {
				data.forEach((node: Node) => {
					this._add(node);
				});
			}
		});

		this._model.on(EventsModel.Remove, (e: any) => {
			const { source, data } = e;
			if (data) {
				this._remove(data);
			}
		});

		this._model.on(EventsModel.Clear, (e: any) => {
			this._clear();
		});
	}

	public get model(): M | undefined {
		return this._model;
	}

	private _add(node: Node) {
		if (!node) return;
		// node.on(Events.ChildrenChangeAdd, (e: any) => {
		// 	const { oldValue, newValue } = e;
		// 	this._model?.add(newValue);
		// });
		// node.on(Events.ChildrenChangeRemove, (e: any) => {
		// 	const { oldValue, newValue } = e;
		// 	this._model?.remove(newValue);
		// });
		// node.on(Events.ChildrenChangeClear, (e: any) => {
		// 	const { oldValue, newValue } = e;
		// 	if (newValue) {
		// 		newValue.forEach((child: Element) => {
		// 			this._model?.remove(child);
		// 		});
		// 	}
		// });
		node.on(Events.AttributeChange, (e: any) => {
			const ui = this._uis.get(node._id);
			ui?.update();
			this.stopTimer(e);
			this.startTimer(e);
		});
		node.on(Events.StyleChange, (e: any) => {
			const ui = this._uis.get(node._id);
			ui?.update();
		});
		const clazz = node.ui2d;
		if (clazz === "NodeUI") {
			if (!this._uis.get(node._id)) {
				const ui = new NodeUI(node, this);
				this._uis.set(node._id, ui);
				this.viewport?.addChild(ui);
			}
		} else if (clazz === "ShapeUI") {
			if (!this._uis.get(node._id)) {
				const ui = new ShapeUI(node, this);
				this._uis.set(node._id, ui);
				this.viewport?.addChild(ui);
			}
		} else if (clazz === "GroupUI") {
			if (!this._uis.get(node._id)) {
				const ui = new GroupUI(node, this);
				this._uis.set(node._id, ui);
				this.viewport?.addChild(ui);
			}
		} else if (clazz === "LineUI") {
			if (!this._uis.get(node._id)) {
				const ui = new LineUI(node, this);
				this._uis.set(node._id, ui);
				this.viewport?.addChild(ui);
			}
		} else if (clazz === "ShapeLineUI") {
			if (!this._uis.get(node._id)) {
				const ui = new ShapeLineUI(node, this);
				this._uis.set(node._id, ui);
				this.viewport?.addChild(ui);
			}
		} else if (clazz === "EdgeUI") {
			if (!this._uis.get(node._id)) {
				const ui = new EdgeUI(node, this);
				this._uis.set(node._id, ui);
				this.viewport?.addChild(ui);
			}
		} else if (clazz === "DomainUI") {
		} else if (clazz === "DoorUI") {
		} else {
			if (!this._uis.get(node._id)) {
				const ui = new UI(node, this);
				this._uis.set(node._id, ui);
				this.viewport?.addChild(ui);
			}
		}
	}

	/**
	 *
	 * @param node
	 * @returns
	 */
	private _remove(node: Node): void {
		if (!node) return;
		const ui = this._uis.get(node._id);
		if (ui) {
			this.viewport && this.viewport.removeChild(ui);
			this._uis && this._uis.delete(node._id);
		}
	}

	/**
	 * clear all ui datas
	 */
	private _clear(): void {
		if (this._uis) {
			this._uis.forEach((ui: UI) => {
				this.viewport?.removeChild(ui);
			});
			this._uis.clear();
		}
	}

	/**
	 * @todo
	 * @param rect
	 */
	public zoomOverview(rect: Rectangle): void {
		if (!rect) return;
		const w = rect?.width || 0;
		const h = rect?.height || 0;
		if (this._viewrect && this.viewport) {
			const ratio =
				Math.min(this._viewrect.width / w, this._viewrect.height / h) *
				0.75;
			this.viewport.setTransform(
				this._viewrect.x +
				this._viewrect.width / 2 -
				(rect.x + rect.width / 2),
				this._viewrect.y +
				this._viewrect.height / 2 -
				56 -
				(rect.y + rect.height / 2),
				1,
				1,
				0,
				0,
				0,
				0,
				0
			);
			this.viewport.setZoom(ratio, true);
		}
	}

	public zoomIn() {
		if (!this.viewport) return;
		let scale = this.viewport.scale;
		scale.x += 0.1;
		scale.y += 0.1;
		this.viewport.setZoom(scale.x, true);
	}

	public zoomOut() {
		if (!this.viewport) return;
		let scale = this.viewport.scale;
		if (scale.x - 0.1 < 0) return;
		scale.x -= 0.1;
		scale.y -= 0.1;

		this.viewport.setZoom(scale.x, true);
	}

	/**
	 * 处理选中事件
	 */
	private handleSelectionChange(e: any) {
		const { oldValue } = e;
		if (oldValue) {
			oldValue.forEach((old: Element) => {
				old.selected = false;
			});
		}
		const sm = this.selectionModel;
		if (!sm) return;
		const datas = sm.getSelection();
		datas.forEach((data: Element) => {
			data.selected = true;
		});
	}

	/**
	 *
	 * @param dx
	 * @param dy
	 */
	moveUI(ui: UI, dx: number, dy: number) {
		const element = ui._element;
		if (!ui || !element) return;
		element.translation(dx, dy);
	}

	/**
	 *
	 * @param dx
	 * @param dy
	 */
	moveSelectedElements(dx: number, dy: number) {
		const sm = this.selectionModel;
		if (!sm) return;
		const elements = sm.getSelection();
		elements.forEach((element: Element) => {
			if (this.isMovable(element)) {
				element.translation(dx, dy);
			}
		});
	}

	isMovable(element: Element) {
		return element.movable;
	}

	/**
	 *
	 * @param element
	 * @returns
	 * @todo 根据业务需求，需要精确判断是否为矩形
	 */
	isEditable(element: Element) {
		const points = element.points;
		const mode = element.mode;
		if (element.type === SHAPE.Domain) {
			const currentInteraction =
				this.interactionManager.getCurrentInteraction();
			if (currentInteraction instanceof DefaultInteraction) {
				if (currentInteraction._shiftDown) {
					return true;
				}
			}
			if (mode === ElementType.Show) {
				if (points.length !== 4) {
					return false;
				} else {
					const d02 =
						(points[0][0] - points[2][0]) *
						(points[0][0] - points[2][0]) +
						(points[0][1] - points[2][1]) *
						(points[0][1] - points[2][1]);
					const d13 =
						(points[1][0] - points[3][0]) *
						(points[1][0] - points[3][0]) +
						(points[1][1] - points[3][1]) *
						(points[1][1] - points[3][1]);
					return Math.abs(d02 - d13) < 1;
				}
			}
		}
		return true;
	}

	setGridVisible(v: boolean) {
		if (this.grid) {
			this.grid.visible = v;
		} else {
			this.grid = new Grid();
			this.viewport?.addChild(this.grid);
		}
	}

	/**
	 *
	 * @param v
	 * @returns
	 */
	setGridStyle(v: string) {
		if (!this.grid) return;
		if (this.grid instanceof Grid) {
			switch (v) {
				case "dark":
					this.grid.set(GridStyle.dark());
					break;
				case "blueprint":
					this.grid.set(GridStyle.blueprint());
					break;
				case "engineering":
					this.grid.set(GridStyle.engineering());
					break;
				case "quad":
					this.grid.set(GridStyle.quad());
					break;
			}
		}
	}

	setEditable(v: boolean) {
		// this.interactionManager.setInteractions(['default', 'edit']);
		this.interactionManager.setInteractions(["edit"]);
	}

	/**
	 * get all uis
	 */
	public get uis(): Map<string, UI> {
		return this._uis;
	}

	/**
	 * change elements Layer
	 * @param element {Element}
	 */
	public changeLayer(elements: Element[] | Element, zIndex: number) {
		if (!elements) return;
		if (elements instanceof Array) {
			elements.forEach((element: Element) => {
				element.styles.zIndex = zIndex;
			});
		} else if (elements instanceof Element) {
			elements.styles.zIndex = zIndex;
		}
	}

	/**
	 *
	 * @returns
	 */
	getScaled(): number {
		return this.viewport?.scaled || 1.0;
	}

	invalidatElementUI(element: Element) {
		if (!element) return;
		const ui = this._uis.get(element.id);
		if (ui) {
			ui._invalidate();
		}
	}

	selectElements(elements: Element | Element[]) { }
	unSelectElements(elements: Element | Element[]) {
		if (!elements) return;
		if (elements instanceof Array) {
			elements.forEach((element: Element) => {
				const ui = this._uis.get(element.id);
				if (ui) {
					ui._invalidate();
				}
			});
		} else {
			const ui = this._uis.get(elements.id);
			if (ui) {
				ui._invalidate();
			}
		}
	}

	public startTimer(e?: any) {
		this._timeout = setTimeout(() => {
			if (this.eventEmitter) {
				this.eventEmitter.emit(Events.UnDirty, {
					source: this,
					target: e,
				});
			}
		}, 5 * 60 * 1000);
	}
	public pauseTimer(e?: any) {
		if (this._timeout) {
			clearTimeout(this._timeout);
		}
	}
	public resumeTimer(e?: any) {
		if (this._timeout) {
			clearTimeout(this._timeout);
		}
		this.startTimer();
	}
	public stopTimer(e?: any) {
		if (this._timeout) {
			clearTimeout(this._timeout);
			if (this.eventEmitter) {
				this.eventEmitter.emit(Events.Dirty, {
					source: this,
					target: e,
				});
			}
		}
	}

	/**
	 * exportImage
	 */
	public exportImage(callback: (result: any) => void, download = false) {
		const viewport = this.viewport;
		if (!viewport) return;
		if (this.renderer) {
			this.renderer.plugins.extract.canvas(viewport).toBlob((b: any) => {
				let a = new FileReader();
				a.onload = (e: any) => {
					callback && callback(e.target.result);
				};
				a.readAsDataURL(b);
				if (download) {
					const a = document.createElement("a");
					document.body.append(a);
					a.download = "bubble.png";
					a.href = URL.createObjectURL(b);
					a.click();
					a.remove();
				}
			}, "image/png");
		}
	}

	getUIById(id: string): UI | undefined {
		return this._uis.get(id);
	}
}
