export function guide(): string {
	const tS4 = function () {
		return Math.floor((1 + Math.random()) * 0x10000)
			.toString(16)
			.substring(1);
	};
	return tS4() + tS4() + '-' + tS4() + '-' + tS4() + '-' + tS4() + '-' + tS4() + tS4() + tS4();
}

export function s4() {
	return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
}

export function s8() {
	return s4() + s4();
}
