import { vec2 } from "gl-matrix";
import { InteractionEvent } from "pixi.js";
import { Element, SHAPE } from "@blood4/modeling";
import { KeyboardEvents } from "../events";
import { DefaultInteraction } from "../interaction/DefaultInteraction";
import { PixiRenderer } from "../PixiRenderer";
import { BaseGizmo } from "./BaseGizmo";
import { DragSpot } from "./spots";
import { RotateSpot } from "./spots/RotateSpot";

export class EditorGizmo extends BaseGizmo {
	private _dragSpots: Map<string | number, DragSpot> = new Map();
	private _rotateSpot?: RotateSpot;
	_zoomedEvent!: (evt: InteractionEvent) => void;
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super(element, renderer);
		this.interactive = true;
		this.buttonMode = true;
		this._element = element;
		this.visible = element.selected;
		this.zIndex = element.styles.editorGizmoZIndex;
		this.visible = element.styles.editorGizmoVisible;
		this.cursor = element.styles.editorGizmoCursor;
		this.mount();
		this._eventEmitter.on(KeyboardEvents.ShiftKey, (evt: any) => {
			this.unmount();
			this.mount();
		});
		// this._eventEmitter.on(KeyboardEvents.CtrlKeyDown, (evt: any) => {
		// 	if (this._dragSpots) {
		// 		this._dragSpots.forEach((spot: DragSpot) => {
		// 			spot.unmount();
		// 		});
		// 		this._dragSpots.clear();
		// 	}
		// });
		// this._eventEmitter.on(KeyboardEvents.CtrlKeyUp, (evt: any) => {
		// 	this.unmount();
		// 	this.mount();
		// });
	}

	/**
	 * _invalidate
	 */
	public _invalidate(): void {
		// console.log('_invalidate');
		this.clear();
	}

	/**
	 * validate
	 */
	public _validate(): void {
		// console.log('_validate');
		this._updateStyles();
		this._validateImpl();
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		const element: Element = this._element;
		if (!element) return;
		this.visible = element.selected;
		this.zIndex = element.styles.editorGizmoZIndex;
		this.visible = element.styles.editorGizmoVisible;
		this.cursor = element.styles.editorGizmoCursor;
		// if (element.mode === ElementType.Show) {
		// 	this.interactive = false;
		// 	this.buttonMode = false;
		// } else {
		// 	this.interactive = true;
		// 	this.buttonMode = true;
		// }
	}

	/**
	 * update
	 */
	protected _drawBody(): void {
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;
		this.clear();
		const interactionManager = renderer.interactionManager;
		const current = interactionManager.getCurrentInteraction();
		if (!(current instanceof DefaultInteraction)) return;
		if (
			element.type === SHAPE.AlignLine ||
			element.type === SHAPE.Line ||
			element.type === SHAPE.ShapeLine
		) {
			const points = element.points;
			if (points && this._dragSpots && this._dragSpots.size > 0) {
				this._dragSpots.forEach(
					(dragSpot: DragSpot, key: string | number) => {
						const point = points[Number(key)];
						dragSpot.center = point;
						dragSpot.mode = "resize";
						dragSpot.update();
					}
				);
			}
		} else {
			if (current._shiftDown) {
				// 缩放拉伸，AABB的端点
				const points = element.getAABB().points;
				if (points && this._dragSpots && this._dragSpots.size > 0) {
					const path: number[] = [];
					points.forEach((point: any) => {
						path.push(point[0], point[1]);
					});
					this.lineStyle(0.6, 0x3477fc, 0.5);
					this.drawPolygon(path);

					this._dragSpots.forEach(
						(dragSpot: DragSpot, key: string | number) => {
							const point = points[Number(key)];
							if (point) {
								dragSpot.center = point;
								dragSpot.mode = "scale";
								dragSpot.update();
							} else {
								const from = points[Number(key) - 0.5],
									to =
										points[
										(Number(key) - 0.5 + 1) %
										points.length
										];
								if (!from || !to) {
									this.unmount();
									return;
								}
								const center = [
									(from[0] + to[0]) / 2,
									(from[1] + to[1]) / 2,
								];
								dragSpot.center = center;
								dragSpot.mode = "scale";
								dragSpot.update();
							}
						}
					);
				}
				if (this._rotateSpot) {
					this._rotateSpot.unmount();
					this._rotateSpot.visible = false;
				}
			} else {
				const points = element.points;
				// 边界拉伸, 每个边的中心点
				if (points && this._dragSpots && this._dragSpots.size > 0) {
					this._dragSpots.forEach(
						(dragSpot: DragSpot, key: string | number) => {
							const from = points[Number(key) - 0.5],
								to =
									points[
									(Number(key) - 0.5 + 1) % points.length
									];
							if (!from || !to) {
								this.unmount();
								return;
							}
							const v = vec2.fromValues(
								to[0] - from[0],
								to[1] - from[1]
							);
							const angle = vec2.angle(
								v,
								vec2.fromValues(v[0], 0)
							);
							const center = [
								(from[0] + to[0]) / 2,
								(from[1] + to[1]) / 2,
							];
							dragSpot.center = center;
							dragSpot.mode = "resize";
							dragSpot.setAngle(v[0] * v[1] > 0 ? angle : -angle);
						}
					);
				}
				if (this._rotateSpot) {
					const size = element.size;
					this._rotateSpot.center = [element.x, element.y];
					this._rotateSpot.offset = [0, size[1] / 2 + 52 / 2];
					this._rotateSpot.visible = !element.resizing;
				}
			}
		}
	}

	/**
	 * set up interaction event
	 */
	public mount(): void {
		this._keydownEvent = this.handle_keydown.bind(this);
		this._keyupEvent = this.handle_keyup.bind(this);
		document.addEventListener("keydown", this._keydownEvent);
		document.addEventListener("keyup", this._keyupEvent);

		// console.log('mount editor gizmo');
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;
		const interactionManager = renderer.interactionManager;
		const current = interactionManager.getCurrentInteraction();

		this._zoomedEvent = this.handle_zoomed.bind(this);
		renderer.viewport?.on("zoomed", this._zoomedEvent);

		if (!(current instanceof DefaultInteraction)) return;
		if (
			element.type === SHAPE.AlignLine ||
			element.type === SHAPE.Line ||
			element.type === SHAPE.ShapeLine
		) {
			const points = element.points;
			if (points) {
				points.forEach((point: number[], index: number) => {
					const dragSpot = new DragSpot(
						element,
						renderer,
						point,
						index,
						element.angle,
						"circle"
					);
					this._dragSpots.set(index, dragSpot);
					this.addChild(dragSpot);
				});
			}
		} else if (element.type === SHAPE.Domain) {
			if (current._shiftDown) {
				// 缩放拉伸，AABB的端点
				const points = element.getAABB().points;
				points.forEach((point: number[], index: number) => {
					const dragSpot = new DragSpot(
						element,
						renderer,
						point,
						index
					);
					this._dragSpots.set(index, dragSpot);
					this.addChild(dragSpot);
					const from = point,
						to = points[(index + 1) % points.length];
					if (!from || !to) {
						this.unmount();
						return;
					}
					const center = [
						(from[0] + to[0]) / 2,
						(from[1] + to[1]) / 2,
					];
					const centerDragSpot = new DragSpot(
						element,
						renderer,
						center,
						index + 0.5
					);
					this._dragSpots.set(index + 0.5, centerDragSpot);
					this.addChild(centerDragSpot);
				});
			} else {
				const points = element.points;
				// 边界拉伸, 每个边的中心点
				if (points) {
					points.forEach((point: number[], index: number) => {
						const from = point,
							to = points[(index + 1) % points.length];
						if (!from || !to) {
							this.unmount();
							return;
						}
						const v = vec2.fromValues(
							to[0] - from[0],
							to[1] - from[1]
						);
						const angle = vec2.angle(v, vec2.fromValues(v[0], 0));
						const center = [
							(from[0] + to[0]) / 2,
							(from[1] + to[1]) / 2,
						];
						const centerDragSpot = new DragSpot(
							element,
							renderer,
							center,
							index + 0.5,
							v[0] * v[1] >= 0 ? angle : -angle
						);
						this._dragSpots.set(index + 0.5, centerDragSpot);
						this.addChild(centerDragSpot);
					});
				}
				if (!this._rotateSpot) {
					const size = element.size;
					this._rotateSpot = new RotateSpot(
						element,
						renderer,
						[element.x, element.y],
						[0, size[1] / 2 + 52 / 2],
						"renderer/icons/ic_rotate.png"
					);
					this.addChild(this._rotateSpot);
				}
			}
		}
		this._invalidate();
		this._validate();
	}

	/**
	 * tear down interaction event
	 */
	public unmount(): void {
		// console.log('unmount editor gizmo');
		this._renderer?.viewport?.off("zoomed", this._zoomedEvent);
		if (this._dragSpots) {
			this._dragSpots.forEach((spot: DragSpot) => {
				this.removeChild(spot);
				spot.unmount();
			});
			this._dragSpots.clear();
		}
		if (this._rotateSpot) {
			this._rotateSpot.unmount();
		}

		// document.removeEventListener('keydown', this._keydownEvent);
		// document.removeEventListener('keyup', this._keyupEvent);
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void {
		// console.log('handle_mouseup');
		if (this._dragSpots) {
			this._dragSpots.forEach((spot: DragSpot) => {
				spot.handle_mouseup(evt);
			});
		}
		if (this._rotateSpot) {
			this._rotateSpot.handle_mouseup(evt);
		}
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseout(evt: InteractionEvent): void {
		// console.log('handle_mouseout');
		if (this._dragSpots) {
			this._dragSpots.forEach((spot: DragSpot) => {
				spot.handle_mouseout(evt);
			});
		}
		if (this._rotateSpot) {
			this._rotateSpot.handle_mouseout(evt);
		}
	}

	/**
	 * handle zoomed
	 * @param e
	 */
	handle_zoomed(evt: InteractionEvent) {
		this._invalidate();
		this._validate();
	}
}
