/**
 * access_line_lst: 动线默认颜色参考UI
 * no_access_line_lst: 未连接主入口的动线为黄色
 * proscenium_line_lst: 前台区域动线颜色区别展示为紫红色
 */

export enum LineType {
	AccessLine, // 默认动线
	UnAccessLine, // 未连接主入口的动线
	ProsceniumLine, // 前台区域动线
}
/**
 * Interface Application
 */
export interface IApplication {
	id?: string;
	canvas?: HTMLCanvasElement | null;
	debug?: {
		model?: boolean;
		ui?: boolean;
		origin?: boolean;
		controlPane?: boolean;
		stats?: boolean;
	};
}

export const Config = {
	scale: 1 / 50,
	axis: [1, -1],
};

