	hoverStrokeWidth: 0.6,
	hoverStrokeColor: 0x3477fc,
	hoverStrokeAlpha: 1.0,
	hoverFill: false,
	hoverFillColor: 0xffffff,
	hoverFillAlpha: 0.0,
	selectFill: true,
	selectFillColor: 0xffffff,
	selectFillAlpha: 0.1,
	selectStrokeWidth: 1.0,
	selectStrokeColor: 0x3477fc,
	selectStrokeAlpha: 1.0,
	labelGizmoCursor: "move",
	alarmFillColor: 0xe02929,
	alarmFillAlpha: 0.09,
