	graphics?: Graphics = new Graphics();

	mementoManager: MementoManager = new MementoManager();
	commandManager: CommandManager = new CommandManager();

	constructor(options?: Partial<IApplication>) {
		super();
		this._options = {
			...options,
		};
		this._debug = {
			...options?.debug,
		};
		this.pixiRenderer = new PixiRenderer({
			canvas: options?.canvas || document.createElement("canvas"),
		});
		if (!this.model) {
			this.model = new Model();
		}
		const model = this.model;

		const pixiRenderer = this.pixiRenderer;
		pixiRenderer.renderer.backgroundColor = 0xf5f5f5;
		pixiRenderer.model = model;
		// pixiRenderer.setGridVisible(true);

		if (!!this._debug.controlPane) {
			this._controlPane = new ControlPane({
				title: "Control Pane",
				model: this.model,
				app: this,
			});
			this._controlPane.visible = !!this._debug.controlPane;
			this._controlPane.renderer = pixiRenderer;
			this._controlPane.model = model;
		}
		this.initCommandManager();
		this.initMementoManager();
		this.initInteractionListener();
		this.initModelListener();
	}

	/**
	 * 添加交互监听事件
	 * @returns
	 */
	initInteractionListener() {
		const pixiRenderer = this.pixiRenderer,
			model = this.model;
		if (!pixiRenderer || !model) return;
		const currentInteraction =
			pixiRenderer.interactionManager.getCurrentInteraction();
		if (currentInteraction instanceof DefaultInteraction) {
			currentInteraction.on(KeyboardEvents.KeyUp, async (evt: any) => {
				if (evt.event.key === "Alt") {
					// await this.handleAlignPreview();
					// await this.doMinArea();
					// await this.doEarlyWarning();
				}
			});
			currentInteraction.on(InteractionEvents.Scale, (evt: any) => {
				const { source, target } = evt;
				if (!target) return;
			});

			currentInteraction.on(
				InteractionEvents.UnableResize,
				(evt: any) => { }
			);

			currentInteraction.on(InteractionEvents.Click, async (evt: any) => {
				const { source, target } = evt;
				if (!source || !target) return;
			});

			currentInteraction.on(InteractionEvents.Sleep, (evt: any) => {
				this.emit(InteractionEvents.Sleep, {
					source: this,
				});
			});

			currentInteraction.on(KeyboardEvents.KeyDown, async (evt: any) => {
				const { source, event } = evt;
				if (event.code === "KeyL" && this.mode === "edit") {
				} else if (event.code === "Delete") {
					const sm = this.pixiRenderer.selectionModel;
					const model = this.model;
					if (sm && model) {
						const selected = sm.getSelection();
						selected.forEach((select: Element) => {
							const children = select.getChildren();
							children &&
								children.forEach((child: Element) => {
									model.remove(child);
								});
							model.remove(select);
						});
					}
				} else if (source._ctrlDown && event.code === "KeyA") {
					const sm = this.pixiRenderer.selectionModel;
					const model = this.model;
					if (sm && model && this._debug.ui) {
						const domains = model.getDatas((element: Element) => {
							return element instanceof Element;
						});
						sm.setSelection(domains);
					}
				} else if (source._ctrlDown && event.code === "KeyS") {
					event.stopPropagation();
					event.preventDefault();
					await this.save();
				}
			});
		}
	}

