import * as PIXI from "pixi.js";
import { MatchedLine, RefLineDataType, RefLineOptions } from "./type";
import { DashLine } from "../dashline";

export class RefLine extends PIXI.Graphics {
	private readonly _graphics: PIXI.Graphics;
	private _graphicsDash: DashLine;
	private _dirty: boolean = true;
	private _datas: MatchedLine[] = [];

	constructor(options: Partial<RefLineOptions> = {}) {
		super();
		this.set(options);
		this._graphics = new PIXI.Graphics();
		this._graphicsDash = new DashLine(this, {
			dash: [1, 1],
			width: 1,
			color: 0x000000,
			alpha: 1.0,
			useTexture: false,
			useDots: true,
		});
		this.addChild(this._graphics);
		this._dirty = true;
	}

	public get datas(): MatchedLine[] {
		return this._datas;
	}

	public set datas(value: MatchedLine[]) {
		if (this._datas === value) return;
		this._datas = value;
		this._dirty = true;
	}

	/**
	 * Apply graph options
	 * @param {RefLineOptions} options - Options to be applied
	 */
	public set(options: Partial<RefLineOptions> = {}) {
		Object.assign(this, options);
		this._dirty = true;
	}

	/**
	 * On invalidation, rendering operations to redraw.
	 *
	 * @override
	 * @param {PIXI.Rectangle} renderer
	 */
	public render(renderer: PIXI.Renderer) {
		super.render(renderer);
		if (!this._dirty) return;
		this._dirty = false;
		this._graphics.clear();
		this.renderDatas();
	}

	private renderDatas() {
		const datas = this._datas;
		if (false) {
			// this._graphicsDash.moveTo(from[0], from[1]);
			// this._graphicsDash.lineTo(to[0], to[1]);
			datas.map((RefLine: any, index: number) => {
				const { left, top, size } = RefLine;
				if (RefLine.type === "horizontal") {
					this._graphicsDash.setStyle({
						dash: [2, 1],
						width: 10,
						color: 0xff0000,
						alpha: 1.0,
						useTexture: false,
						useDots: true,
						scale: 1,
						alignment: 0.5,
					});
					this._graphicsDash.moveTo(left, top);
					this._graphicsDash.lineTo(left + size, top);
					this._graphicsDash.closePath();
				} else if (RefLine.type === "vertical") {
					this._graphicsDash.setStyle({
						dash: [1, 1],
						width: 1,
						color: 0xff0000,
						alpha: 1.0,
						useTexture: false,
						useDots: true,
						scale: 1,
						alignment: 0.5,
					});
					this._graphicsDash.moveTo(left, top);
					this._graphicsDash.lineTo(left, top + size);
					this._graphicsDash.closePath();
				}
			});
			this._graphicsDash.closePath();
			return;
		}
		datas.map((RefLine: any, index: number) => {
			const { left, top, size } = RefLine;
			if (RefLine.type === "horizontal") {
				this._graphics.lineStyle(1, 0xff0000, 1);
				this._graphics.moveTo(left, top);
				this._graphics.lineTo(left + size, top);
				this._graphics.closePath();
			} else if (RefLine.type === "vertical") {
				this._graphics.lineStyle(1, 0xff0000, 1);
				this._graphics.moveTo(left, top);
				this._graphics.lineTo(left, top + size);
				this._graphics.closePath();
			}
		});
	}

	/**
	 * clear
	 * @returns
	 */
	public clear(): this {
		this._datas = [];
		this._dirty = true;
		return this;
	}
}
