import { mat2d, vec2 } from "gl-matrix";
import { Element, ElementProps } from "./Element";
import { BaseClientProps, BaseStyleProps, SHAPE, UI2D } from "./types";
import { AABB, OBB } from "../shapes";
import { expand, offset } from "@jscad/modeling/src/operations/expansions";
import path2 from "@jscad/modeling/src/geometries/path2";
import geom2 from "@jscad/modeling/src/geometries/geom2";
import { Vec2 } from "@jscad/modeling/src/maths/vec2";

/**
 * Shape object props
 */
export interface ShapeLineProps<StyleProps = any, ClientProps = any>
	extends ElementProps {
	points?: number[][];
	dash?: number[];
}

/**
 * Shape object: Shape
 */
export class ShapeLine<
	StyleProps extends BaseStyleProps = any,
	ClientProps extends BaseClientProps = any
	> extends Element<StyleProps, ClientProps> {
	dash?: number[];
	constructor(props?: ShapeLineProps<StyleProps, ClientProps>) {
		super({
			type: SHAPE.ShapeLine,
			...props,
		});
		this.ui2d = UI2D.ShapeLineUI;
		this._points = props?.points || [];
		this.dash = props?.dash;
		this._updateSize();
	}

	/**
	 *
	 * @param padding  {number | { top: number, right: number, bottom: number, left: number }} 间距
	 * @returns
	 */
	public getBoundingBox(
		padding?:
			| number
			| { top: number; right: number; bottom: number; left: number }
	): AABB {
		const aabb = this.getAABB().clone();
		let rect = new AABB(aabb.x, aabb.y, aabb.width, aabb.height);
		if (padding && typeof padding === "number") {
			rect.x -= padding;
			rect.y -= padding;
			rect.width += 2 * padding;
			rect.height += 2 * padding;
		} else if (padding && typeof padding === "object") {
			rect.x -= padding.left || 0;
			rect.y -= padding.top || 0;
			rect.width += (padding.left || 0) + (padding.right || 0);
			rect.height += (padding.top || 0) + (padding.bottom || 0);
		}
		return rect;
	}

	/**
	 * AABB Bounds
	 */
	public getAABB(): AABB {
		const aabb = AABB.fromPoints(this._points);
		this._aabb = aabb;
		return aabb;
	}

	/**
	 * get OBB
	 * @returns
	 */
	public getOBB(): OBB {
		this._aabb = this.getAABB();
		const obb = new OBB(this._points);
		this._obb = obb;
		return this._obb;
	}

	/**
	 *
	 * @param padding
	 * @returns
	 */
	public getHitArea(
		padding?:
			| number
			| { top: number; right: number; bottom: number; left: number }
	): number[][] {
		this._aabb = this.getAABB();
		const matrix = this._matrix;
		const points: number[][] = [];
		this._points.map((point: any) => {
			const out = vec2.create();
			const pp = vec2.transformMat2d(
				out,
				vec2.fromValues(point[0], point[1]),
				mat2d.clone(matrix)
			);
			points.push([pp[0], pp[1]]);
		});
		const obb = new OBB(points);
		this._obb = obb;
		if (padding) {
			if (padding && typeof padding === "number") {
				if (points.length < 3) return [];
				const linePath2 = path2.fromPoints(
					{ closed: false },
					points as Vec2[]
				);
				const expandedPathGeom2 = expand(
					{ delta: padding, corners: "edge", segments: 8 },
					linePath2
				);
				const expandedPoints = geom2.toPoints(expandedPathGeom2);
				const result: number[][] = [];
				expandedPoints.forEach((point: number[]) => {
					result.push([point[0], point[1]]);
				});
				return result;
			}
		}
		return [];
	}

	/**
	 * 获取中心点位置
	 * @returns
	 */
	public getCenter() {
		const aabb = this.getAABB();
		return {
			x: aabb.x + aabb.width / 2,
			y: aabb.y + aabb.height / 2,
		};
	}

	translation(dx: number, dy: number) {
		const oldValue = this._points;
		const points: number[][] = [];
		oldValue.forEach((old: number[]) => {
			points.push([old[0] + dx, old[1] + dy]);
		});
		this._points = points;
		this._updateSize();
	}
}
