export interface Rect {
	key: string | number;
	left: number;
	top: number;
	width: number;
	height: number;
	rotate?: number;
	[x: string]: any;
}

export interface BoundingRect {
	left: number;
	top: number;
	right: number;
	bottom: number;
	width: number;
	height: number;
}

export type LineType = "horizontal" | "vertical";

// horizontal/vertical  top/center/bottom left/center/right
export type VRefLinePosition = "vl" | "vc" | "vr";
export type HRefLinePosition = "ht" | "hc" | "hb";
export type RefLinePosition = VRefLinePosition | HRefLinePosition;

export interface RefLineMeta<T extends Rect = Rect> {
	type: LineType;
	position: RefLinePosition;
	offset: number;
	start: number;
	end: number;
	rect: T;
	adsorbOnly?: boolean;
}

export interface MatchedLine<T extends Rect = Rect> {
	type: LineType;
	left: number;
	top: number;
	size: number;
	refLineMetaList: RefLineMeta<T>[];
}

export interface LineGroup<T extends Rect = Rect> {
	min: number;
	max: number;
	offset: number;
	refLineMetaList: RefLineMeta<T>[];
}

export interface Delta {
	left: number;
	top: number;
}

export enum MOVE_DIR {
	MOVE_TOP,
	MOVE_RIGHT,
	MOVE_BOTTOM,
	MOVE_LEFT,
	NONE,
}

export interface AdsorbLine {
	key: string;
	type: LineType;
	offset: number;
}

export type AdsorbVLine = Omit<AdsorbLine, "type">;
export type AdsorbHLine = Omit<AdsorbLine, "type">;
export type Cursor =
	| "auto"
	| "default"
	| "none"
	| "context-menu"
	| "help"
	| "pointer"
	| "progress"
	| "wait"
	| "cell"
	| "crosshair"
	| "text"
	| "vertical-text"
	| "alias"
	| "copy"
	| "move"
	| "no-drop"
	| "not-allowed"
	| "e-resize"
	| "n-resize"
	| "ne-resize"
	| "nw-resize"
	| "s-resize"
	| "se-resize"
	| "sw-resize"
	| "w-resize"
	| "ns-resize"
	| "ew-resize"
	| "nesw-resize"
	| "col-resize"
	| "nwse-resize"
	| "row-resize"
	| "all-scroll"
	| "zoom-in"
	| "zoom-out"
	| "grab"
	| "grabbing";
export type DireactionX = "left" | "right" | "none";
export type DireactionY = "up" | "down" | "none";
