import { Rectangle, Text } from "pixi.js";
import { Element, PositionType } from "@blood4/modeling";
import { PixiRenderer } from "../PixiRenderer";
import { BaseGizmo } from "./BaseGizmo";

export class LabelGizmo extends BaseGizmo {
	private _text?: Text;
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super(element, renderer);
		this.interactive = true;
		this.buttonMode = true;
		this._element = element;
		this.zIndex = element.styles.labelGizmoZIndex;
		this.visible = element.styles.labelGizmoVisible;
		this.cursor = element.styles.labelGizmoCursor;

		if (element instanceof Element) {
			this._text = new Text("Length: ", {
				fontFamily: "PingFangSC-Regular, PingFang SC",
				fontSize: 12,
				fill: 0x000000,
				align: "center",
				fontWeight: "600",
			});
			this._text.resolution = 2;
			this._text.anchor.set(0.5, 0.5);
			this._text.text = `${element.name}`;
			this.addChild(this._text);
		}
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		const element: Element = this._element;
		if (!element) return;

		if (this._text) {
			const aabb = element.getAABB();
			const rect: Rectangle = new Rectangle();
			this._text.getLocalBounds(rect);
			this._text.x = 0;
			this._text.y = 0;
			this._text.anchor.set(0.5, 0.5);
			this._text.text = `${element.name}`;
			if (element.styles.labelGizmoRotation) {
				this._text.angle = element.angle;
			}
			/* const scale = this._renderer?.getScaled() || 1;
			this._text.scale.x = 1 / scale > 1 ? 1 / scale : 1;
			this._text.scale.y = 1 / scale > 1 ? 1 / scale : 1; */
			const position =
				element.styles.labelGizmoPosition || PositionType.Center;
			switch (position) {
				case PositionType.Center:
					this.x = element.center[0];
					this.y = element.center[1];
					break;
				case PositionType.Top:
					this.x = element.center[0];
					this.y = aabb.y;
					break;
				case PositionType.Bottom:
					this.x = element.center[0];
					this.y = aabb.y + aabb.height;
					break;
				case PositionType.Left:
					this.x = aabb.x;
					this.y = aabb.y + aabb.height / 2;
					break;
				case PositionType.Right:
					this.x = aabb.x + aabb.width;
					this.y = aabb.y + aabb.height / 2;
					break;
				case PositionType.TopLeft:
					this.x = aabb.x;
					this.y = aabb.y;
					break;
				case PositionType.TopRight:
					this.x = aabb.x + aabb.width;
					this.y = aabb.y;
					break;
				case PositionType.BottomLeft:
					this.x = aabb.x;
					this.y = aabb.y + aabb.height;
					break;
				case PositionType.BottomRight:
					this.x = aabb.x + aabb.width;
					this.y = aabb.y + aabb.height;
					break;
			}
			const offsetX = element.styles.labelGizmoPositionOffsetX;
			const offsetY = element.styles.labelGizmoPositionOffsetY;
			this.x += offsetX;
			this.y += offsetY;
		}
	}

	/**
	 * update
	 */
	protected _drawBody(): void {
		const element = this._element;
		if (!element) return;
		this.clear();
		if (this._text) {
			const rect: Rectangle = new Rectangle();
			this._text.getLocalBounds(rect);
			if (element.debug) {
				this.beginFill(0xff0000, 0.5);
				this.lineStyle(1, 0x0099cc, 1.0);
				this.drawRect(
					0 - rect.width / 2,
					0 - rect.height / 2,
					rect.width,
					rect.height
				);
				this.endFill();
			}
			// const scale: number = this._viewer2d?.viewport.scaled || 1.0;
			// this._text.style = new PIXI.TextStyle({
			// 	fontSize: 14 / scale
			// });
			// this._text.style.fontSize = 14/scale;
			// this._text.rotation = element.angle * DEG_TO_RAD;
		}
	}
}
