import { Element } from "@blood4/modeling";
import {
	Sprite,
	Texture,
	InteractionEvent,
	Point,
	Graphics,
	DEG_TO_RAD,
	Matrix,
} from "pixi.js";
import { PixiRenderer } from "../../PixiRenderer";
import { InteractionEvents } from "../../events";
import { DefaultInteraction } from "../../interaction/DefaultInteraction";

export class RotateSpot extends Graphics {
	protected _element: Element;
	protected _renderer: PixiRenderer<any>;
	protected _center: number[] = [0, 0];
	protected _offset: number[] = [0, 0];
	protected _fillColor = 0xcdcdcd;
	protected _sprite?: Sprite;
	protected _url: string;

	_mousedownEvent!: (evt: InteractionEvent) => void;
	_mouseupEvent!: (evt: InteractionEvent) => void;
	_mousemoveEvent!: (evt: InteractionEvent) => void;
	_mouseoutEvent!: (evt: InteractionEvent) => void;

	_hitTarget?: RotateSpot;
	_isDragging = false;
	_mousePoint?: Point = undefined;
	_ctrlDown: boolean = false;
	_doubleClickedTimeout: NodeJS.Timeout | null = null;

	constructor(
		element: Element,
		renderer: PixiRenderer<any>,
		center: number[],
		offset: number[],
		url: string,
		name?: string
	) {
		// super(Texture.from(url));
		super();
		this._element = element;
		this._renderer = renderer;
		this._center = center;
		this._offset = offset;
		this._url = url;
		this.interactive = true;
		this.buttonMode = true;
		this.cursor = "pointer";
		this._invalidate();
		this._validate();
		this.mount();
	}

	public mount(): void {
		// console.log('drag spot mount');
		this._mousedownEvent = this.handle_mousedown.bind(this);
		this._mouseupEvent = this.handle_mouseup.bind(this);
		this._mousemoveEvent = this.handle_mousemove.bind(this);
		this._mouseoutEvent = this.handle_mouseout.bind(this);
		this.on("mousedown", this._mousedownEvent);
		this.on("mouseup", this._mouseupEvent);
		// this.on('mouseout', this._mouseoutEvent);
		// this.on('mousemove', this._mousemoveEvent);
		if (!this._sprite) {
			const sprite = new Sprite(Texture.from(this._url));
			sprite.anchor.set(0.5);
			sprite.scale.set(0.5, 0.5);
			sprite.x = 0;
			sprite.y = 0;
			this.addChild(sprite);
			this._sprite = sprite;
		}
	}

	public unmount(): void {
		// console.log('drag spot unmount');
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		this.off("mousedown", this._mousedownEvent);
		this.off("mouseup", this._mouseupEvent);
		this.off("mousemove", this._mousemoveEvent);
		if (viewport) {
			viewport.off("mousemove", this._mousemoveEvent);
		}
	}

	/**
	 * _invalidate
	 */
	public _invalidate(): void {
		// console.log('_invalidate');
		// this.unmount();
	}

	/**
	 * validate
	 */
	public _validate(): void {
		// console.log('_validate');
		this._updateStyles();
		this._validateImpl();
	}

	protected _validateImpl(): void {
		this._draw();
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		const element = this._element;
		if (!element) return;
		if (element.resizing) {
			this.visible = false;
		} else {
			this.visible = element.selected;
		}
		const x = this._center[0] + this._offset[0];
		const y = this._center[1] + this._offset[1];
		const center = element.center;
		const a = Math.sin(element.angle * DEG_TO_RAD);
		const b = Math.cos(element.angle * DEG_TO_RAD);
		const tx = center[0] * (1 - b) + center[1] * a;
		const ty = center[1] * (1 - b) - center[0] * a;
		const matrix = new Matrix(b, a, -a, b, tx, ty);
		const out = matrix.apply(new Point(x, y));
		this.x = out.x;
		this.y = out.y;
		const scale = this._renderer?.getScaled() || 1;
		this.scale.x = 1 / scale;
		this.scale.y = 1 / scale;
	}

	protected _draw() {
		this._drawBody();
	}

	/**
	 * update
	 */
	public _drawBody(): void {
		const element = this._element;
		if (!element) return;
		// this.lineStyle(1.5, 0x737373, 1.0);
		// this.beginFill(0xffff00, 1.0);
		// this.drawCircle(0, 0, 10);
	}

	/**
	 * set fill color
	 */
	public set fillColor(color: number) {
		this._fillColor = color;
		this.update();
	}

	public set center(center: number[]) {
		this._center = center;
		this.update();
	}

	/**
	 * update
	 */
	public update() {
		this._invalidate();
		this._validate();
	}

	/**
	 * handle zoomed
	 * @param e
	 */
	handle_zoomed(evt: InteractionEvent) { }

	/**
	 * handle panned
	 * @param e
	 */
	handle_panned(evt: InteractionEvent) { }

	isCtrlDown(evt: KeyboardEvent) {
		return evt.ctrlKey || evt.metaKey;
	}

	/**
	 * mouse down
	 * @param e
	 */
	handle_mousedown(evt: InteractionEvent): void {
		evt.stopPropagation();
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		const hitTarget = renderer.renderer.plugins.interaction.hitTest(
			evt.data.global
		);
		this._hitTarget = hitTarget;
		if (hitTarget instanceof RotateSpot) {
			this.__dragStart(evt);
			viewport.on("mousemove", this._mousemoveEvent);
			viewport.on("mouseout", this._mouseoutEvent);
		}
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void {
		this.__dragEnd(evt);
	}

	/**
	 * mouse move
	 * @param e
	 */
	handle_mousemove(evt: InteractionEvent): void {
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		this.__dragMove(evt);
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseout(evt: InteractionEvent): void {
		this.__dragEnd(evt);
	}

	/**
	 * drag start
	 * @param evt
	 */
	protected __dragStart(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._isDragging) return;
		if (hitTarget instanceof RotateSpot) {
			const position = evt.data.getLocalPosition(viewport);
			this._mousePoint = new Point(position.x, position.y);
			// this._isDragging = true;
			// console.log('__dragStart', this._isDragging);
			// if (!hitTarget._element.rotating) {
			// 	hitTarget._element.rotating = true;
			// }
		}
	}

	/**
	 * drag Move
	 * @param evt
	 */
	protected __dragMove(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		if (!hitTarget || !hitTarget.parent) return;
		const position = evt.data.getLocalPosition(hitTarget.parent);
		if (!this._isDragging && this._mousePoint) {
			if (
				Math.abs(position.x - this._mousePoint.x) >= 0.1 ||
				Math.abs(position.y - this._mousePoint.y) >= 0.1
			) {
				this._isDragging = true;
				if (!hitTarget._element.rotating) {
					hitTarget._element.rotating = true;
				}
				const interactionManager = this._renderer.interactionManager;
				const currentInteraction =
					interactionManager.getCurrentInteraction();
				if (currentInteraction instanceof DefaultInteraction) {
					currentInteraction.emit(InteractionEvents.RotateStart, {
						source: this,
						target: hitTarget._element,
					});
				}
			}
		}
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport || !this._isDragging || !this._mousePoint) return;
		if (hitTarget instanceof RotateSpot && hitTarget.parent.parent) {
			const center = hitTarget.parent.parent.getGlobalPosition();
			const element = hitTarget._element;
			const angle = this._calculateAngle(
				[center.x, center.y],
				[evt.data.global.x, evt.data.global.y]
			);
			element.angle = -angle;
			const interactionManager = renderer.interactionManager;
			const current = interactionManager.getCurrentInteraction();
			if (current instanceof DefaultInteraction) {
				current?.emit(InteractionEvents.Rotating, {
					source: this,
					target: hitTarget._element,
				});
			}
		}
	}

	/**
	 * 计算旋转角度，逆时针为正
	 * @param center
	 * @param point
	 */
	_calculateAngle(center: number[], point: number[]) {
		return -Math.round(
			(Math.atan2(center[0] - point[0], point[1] - center[1]) * 180) /
			Math.PI
		);
	}

	/**
	 * drag End
	 * @param evt
	 */
	protected __dragEnd(evt: InteractionEvent) {
		if (this._isDragging) {
			this._isDragging = false;
			this.off("mousemove", this._mousemoveEvent);
			// evt.stopPropagation();
			const hitTarget = this._hitTarget;
			if (hitTarget && hitTarget._element) {
				hitTarget._element.rotating = false;
				const interactionManager = this._renderer.interactionManager;
				const currentInteraction =
					interactionManager.getCurrentInteraction();
				if (currentInteraction instanceof DefaultInteraction) {
					currentInteraction.emit(InteractionEvents.RotateEnd, {
						source: this,
						target: hitTarget._element,
					});
				}
			}
		}
		this._mousePoint = undefined;
	}

	/**
	 * key down event
	 * @param scope
	 * @param evt
	 */
	protected handle_keydown(evt: KeyboardEvent): void {
		const key = evt.code;
		this._ctrlDown = this.isCtrlDown(evt);
	}

	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_keyup(evt: KeyboardEvent): void {
		const key = evt.code;
		this._ctrlDown = this.isCtrlDown(evt);
	}
	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_contextmenu(e: any): void {
		e.preventDefault();
		this.__dragEnd(e);
	}

	protected translate(dx: number, dy: number) {
		this._center[0] += dx;
		this._center[1] += dy;
		this.update();
	}

	set offset(offset: number[]) {
		this._offset = offset;
		this.update();
	}
}
