import { mat2d, vec2 } from "gl-matrix";
import { Element, ElementProps } from "./Element";
import { BaseClientProps, BaseStyleProps, SHAPE, UI2D } from "./types";
import { AABB, DEG_TO_RAD, OBB } from "../shapes";
import primitives from "@jscad/modeling/src/primitives";
import { Line } from "./Line";
import { Geom2 } from "@jscad/modeling/src/geometries/types";
import geometries from "@jscad/modeling/src/geometries";

/**
 * Shape object props
 */
export interface ShapeProps<StyleProps = any, ClientProps = any>
	extends ElementProps {
	points?: number[][];
}

/**
 * Shape object: Shape
 */
export class Shape<
	StyleProps extends BaseStyleProps = any,
	ClientProps extends BaseClientProps = any
	> extends Element<StyleProps, ClientProps> {
	constructor(props?: ShapeProps<StyleProps, ClientProps>) {
		super({
			type: SHAPE.Shape,
			...props,
		});
		this.ui2d = UI2D.ShapeUI;
		this._points = props?.points || [];
		const bounds = AABB.fromPoints(this._points);
		this.x = bounds.x + bounds.width / 2;
		this.y = bounds.y + bounds.height / 2;
		this.center = [this.x, this.y];
		// this._updateRotate(this.angle, this.angle);
		this._updateSize();
		this._matrix = this._angleToMatrix(this.angle);
	}

	protected _updateRotate(
		oldAngle: number = 0,
		newAngle: number = this.angle
	): void {
		const points = this._points;
		// reverse
		const oldMatrix = this._angleToMatrix(-1 * oldAngle);
		const oldPoints: number[][] = [];
		for (let i = 0, n = points.length; i < n; i++) {
			const out = vec2.create();
			const point = points[i];
			const pp = vec2.transformMat2d(
				out,
				vec2.fromValues(point[0], point[1]),
				mat2d.clone(oldMatrix)
			);
			oldPoints[i] = [pp[0], pp[1]];
		}
		// rotate
		const newMatrix = this._angleToMatrix(newAngle);
		const newPoints: number[][] = [];
		for (let i = 0, n = oldPoints.length; i < n; i++) {
			const out = vec2.create();
			const point = oldPoints[i];
			const pp = vec2.transformMat2d(
				out,
				vec2.fromValues(point[0], point[1]),
				mat2d.clone(newMatrix)
			);
			newPoints[i] = [pp[0], pp[1]];
		}
		this._points = newPoints;
		this._matrix = newMatrix;
	}

	/**
	 *
	 * @param padding  {number | { top: number, right: number, bottom: number, left: number }} 间距
	 * @returns
	 */
	public getBoundingBox(
		padding?:
			| number
			| { top: number; right: number; bottom: number; left: number }
	): AABB {
		const aabb = this.getAABB().clone();
		let rect = new AABB(aabb.x, aabb.y, aabb.width, aabb.height);
		if (padding && typeof padding === "number") {
			rect.x -= padding;
			rect.y -= padding;
			rect.width += 2 * padding;
			rect.height += 2 * padding;
		} else if (padding && typeof padding === "object") {
			rect.x -= padding.left || 0;
			rect.y -= padding.top || 0;
			rect.width += (padding.left || 0) + (padding.right || 0);
			rect.height += (padding.top || 0) + (padding.bottom || 0);
		}
		return rect;
	}

	/**
	 * AABB Bounds
	 */
	public getAABB(): AABB {
		const aabb = AABB.fromPoints(this._points);
		this._aabb = aabb;
		return aabb;
	}

	/**
	 * AABB Bounds
	 */
	public getInnerAABB(): AABB {
		const offset = this.styles.offsetDelta || 0;
		const innerPoints = this.getOffsetPoints(offset) as number[][];
		const aabb = AABB.fromPoints(innerPoints);
		this._aabb = aabb;
		return aabb;
	}

	/**
	 * get OBB
	 * @returns
	 */
	public getOBB(): OBB {
		this._aabb = this.getAABB();
		const obb = new OBB(this._points);
		this._obb = obb;
		return this._obb;
	}

	/**
	 * 获取中心点位置
	 * @returns
	 */
	public getCenter() {
		const aabb = this.getAABB();
		return {
			x: aabb.x + aabb.width / 2,
			y: aabb.y + aabb.height / 2,
		};
	}

	translation(dx: number, dy: number) {
		const oldValue = this._points;
		const points: number[][] = [];
		oldValue.forEach((old: number[]) => {
			points.push([old[0] + dx, old[1] + dy]);
		});
		this._points = points;
		this._updateSize();
		this._matrix = this._angleToMatrix(this.angle);
	}

	/**
	 * 切割多边形
	 */
	public splitPolygon(
		line: Line,
		points: number[][],
		along: boolean = false
	): {
		intersectionCount: number;
		polygons: number[][][];
	} {
		const from = line.from,
			to = line.to;
		if (!line)
			return {
				intersectionCount: 0,
				polygons: [],
			};

		const self = this;
		const poly1: number[][] = [];
		const poly2: number[][] = [];

		let checkPoint = false;
		let intersectionCount = 0;
		if (!along) {
			let linePoints: number[][] = [
				[from[0], from[1]],
				[to[0], to[1]],
			];

			for (let i = 0; i < points.length; i++) {
				let polygonPoints =
					i == points.length - 1
						? [points[i], points[0]]
						: [points[i], points[i + 1]];
				let t = self.getT(linePoints, polygonPoints);
				let u = self.getU(linePoints, polygonPoints);
				if (0 <= t && t <= 1 && 0 <= u && u <= 1) {
					intersectionCount++;
					checkPoint = !checkPoint;
					poly2.push(self.getIntersection(linePoints, t));
					if (intersectionCount == 1) {
						poly1.push(points[i]);
						poly2.push(points[i + 1]);
					}
					poly1.push(self.getIntersection(linePoints, t));
				} else {
					checkPoint
						? poly2.push(points[i + 1])
						: poly1.push(points[i]);
				}
			}
		} else {
			const offset = -1 * line.styles.offsetDelta;
			const alongPoints = line.getAlongPoints(offset);
			let linePoints: number[][] = [
				[alongPoints.from[0], alongPoints.from[1]],
				[alongPoints.to[0], alongPoints.to[1]],
			];
			for (let i = 0; i < points.length; i++) {
				let polygonPoints =
					i == points.length - 1
						? [points[i], points[0]]
						: [points[i], points[i + 1]];
				let t = self.getT(linePoints, polygonPoints);
				let u = self.getU(linePoints, polygonPoints);
				if (0 <= t && t <= 1 && 0 <= u && u <= 1) {
					intersectionCount++;
					checkPoint = !checkPoint;
					poly2.push(self.getIntersection(linePoints, t));
					if (intersectionCount == 1) {
						poly1.push(points[i]);
						poly2.push(points[i + 1]);
					}
					poly1.push(self.getIntersection(linePoints, t));
				} else {
					checkPoint
						? poly2.push(points[i + 1])
						: poly1.push(points[i]);
				}
			}
		}

		return {
			intersectionCount: intersectionCount,
			polygons: [poly1, poly2],
		};
	}
	getT(l1: number[][], l2: number[][]) {
		return (
			((l1[0][0] - l2[0][0]) * (l2[0][1] - l2[1][1]) -
				(l1[0][1] - l2[0][1]) * (l2[0][0] - l2[1][0])) /
			((l1[0][0] - l1[1][0]) * (l2[0][1] - l2[1][1]) -
				(l1[0][1] - l1[1][1]) * (l2[0][0] - l2[1][0]))
		);
	}
	getU(l1: number[][], l2: number[][]) {
		return -(
			((l1[0][0] - l1[1][0]) * (l1[0][1] - l2[0][1]) -
				(l1[0][1] - l1[1][1]) * (l1[0][0] - l2[0][0])) /
			((l1[0][0] - l1[1][0]) * (l2[0][1] - l2[1][1]) -
				(l1[0][1] - l1[1][1]) * (l2[0][0] - l2[1][0]))
		);
	}
	getIntersection(l1: number[][], t: number) {
		return [
			l1[0][0] + t * (l1[1][0] - l1[0][0]),
			l1[0][1] + t * (l1[1][1] - l1[0][1]),
		];
	}

	/**
	 * angle to matrix
	 * @returns
	 */
	static angleToMatrix(center: number[], angle: number = 0): mat2d {
		const a = Math.sin(angle * DEG_TO_RAD);
		const b = Math.cos(angle * DEG_TO_RAD);
		const tx = center[0] * (1 - b) + center[1] * a;
		const ty = center[1] * (1 - b) - center[0] * a;
		const matrix = mat2d.fromValues(b, a, -a, b, tx, ty);
		return matrix;
	}

	static fromNode(center: number[], size: number[], attributes?: any): Shape {
		const rect: Geom2 = primitives.rectangle({
			center: [center[0], center[1]],
			size: [size[0], size[1]],
		});
		const points = geometries.geom2.toPoints(rect);
		const shape = new Shape({
			points,
			...attributes,
		});
		return shape;
	}
}
