import { InteractionEvent, Point } from "pixi.js";
import { PixiRenderer } from "..";
import { LayerType, Element } from "@blood4/modeling";
import { Grid } from "../components/grid";
import { InteractionEvents, KeyboardEvents } from "../events";
import { BaseGizmo, EditorGizmo, LabelGizmo } from "../gizmos";
import { RotateSpot } from "../gizmos/spots";
import { LineUI, UI } from "../ui";
import { BaseInteraction } from "./BaseInteraction";
export class DefaultInteraction extends BaseInteraction {
	_zoomedEvent!: (evt: InteractionEvent) => void;
	_pannedEvent!: (evt: InteractionEvent) => void;
	_selectedEvent!: (evt: InteractionEvent) => void;
	_mousedownEvent!: (evt: InteractionEvent) => void;
	_mouseupEvent!: (evt: InteractionEvent) => void;
	_mousemoveEvent!: (evt: InteractionEvent) => void;
	_mouseoutEvent!: (evt: InteractionEvent) => void;
	_mouseoverEvent!: (evt: InteractionEvent) => void;

	_keydownEvent!: (evt: KeyboardEvent) => void;
	_keyupEvent!: (evt: KeyboardEvent) => void;
	_contextmenu!: (evt: any) => void;

	_onDocumentMouseDown!: (evt: MouseEvent) => void;
	_onDocumentMouseUp!: (evt: MouseEvent) => void;

	_hitTarget?: UI;
	_hoverTarget?: UI;
	_isDragging = false;
	_mousePoint?: Point = undefined;

	_ctrlDown: boolean = false;
	_shiftDown: boolean = false;
	_altDown: boolean = false;
	_doubleClickedTimeout: NodeJS.Timeout | null = null;
	protected _oldOutTarget?: UI;
	protected _oldMousePoint?: Point = undefined;

	constructor(renderer: PixiRenderer<any>) {
		super(renderer);
	}

	/**
	 * mount
	 * @returns
	 */
	mount() {
		// console.log('default interaction mount');
		const renderer = this.renderer;
		const viewport = renderer.viewport;

		if (!viewport) return;
		document.body.style.cursor = "default";
		viewport
			.drag({
				wheel: false,
				mouseButtons: "right",
				pressDrag: true,
				direction: "all", // 'x' | 'y' |'all' | null
			})
			.pinch()
			.wheel({
				wheelZoom: true,
			});
		this._zoomedEvent = this.handle_zoomed.bind(this);
		this._pannedEvent = this.handle_panned.bind(this);
		this._mousedownEvent = this.handle_mousedown.bind(this);
		this._mouseupEvent = this.handle_mouseup.bind(this);
		this._mousemoveEvent = this.handle_mousemove.bind(this);
		this._mouseoutEvent = this.handle_mouseout.bind(this);
		this._mouseoverEvent = this.handle_mouseover.bind(this);
		this._keydownEvent = this.handle_keydown.bind(this);
		this._keyupEvent = this.handle_keyup.bind(this);
		this._contextmenu = this.handle_contextmenu.bind(this);

		viewport.on("zoomed", this._zoomedEvent);
		viewport.on("moved", this._pannedEvent);
		viewport.on("mousedown", this._mousedownEvent);
		viewport.on("mouseup", this._mouseupEvent);
		viewport.on("mousemove", this._mousemoveEvent);
		viewport.on("mouseout", this._mouseoutEvent);
		viewport.on("mouseover", this._mouseoverEvent);

		document.addEventListener("keydown", this._keydownEvent);
		document.addEventListener("keyup", this._keyupEvent);
		document.addEventListener("contextmenu", this._contextmenu);

		/* const sm = renderer.selectionModel;
		sm?.on(EventsSelection.Change, (evt: any) => {
			const oldValue = evt.oldValue;
			if (oldValue) {
				oldValue.forEach((data: Element) => {});
			}
		}); */
	}

	/**
	 * unmount
	 * @returns
	 */
	unmount() {
		// console.log('default interaction unmount');
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		viewport
			.drag({
				wheel: false,
				mouseButtons: "right",
				pressDrag: false,
			})
			.wheel({
				wheelZoom: false,
			});
		viewport.off("zoomed", this._zoomedEvent);
		viewport.off("moved", this._pannedEvent);
		viewport.off("mousedown", this._mousedownEvent);
		viewport.off("mouseup", this._mouseupEvent);
		viewport.off("mousemove", this._mousemoveEvent);
		viewport.off("mouseout", this._mouseoutEvent);

		document.removeEventListener("keydown", this._keydownEvent);
		document.removeEventListener("keyup", this._keyupEvent);
		document.removeEventListener("contextmenu", this._contextmenu);

		const sm = renderer.selectionModel;
		// sm?.off(EventsSelection.Change);
		this.off(InteractionEvents.Resizing, (evt: any) => { });
	}

	/**
	 * update
	 */
	update() {
		// console.log('default interaction update');
	}

	/**
	 * clear
	 */
	clear() {
		// console.log('default interaction clear');
	}

	/**
	 * handle zoomed
	 * @param e
	 */
	handle_zoomed(evt: InteractionEvent) {
		if (this.renderer.grid && this.renderer.grid instanceof Grid) {
			// this.renderer.grid.refline = [];
		}
	}

	/**
	 * handle panned
	 * @param e
	 */
	handle_panned(evt: InteractionEvent) {
		if (this.renderer.grid && this.renderer.grid instanceof Grid) {
			// this.renderer.grid.refline = [];
		}
	}
	isDelDown(evt: KeyboardEvent) {
		return evt.code === "Delete";
	}

	isCtrlDown(evt: KeyboardEvent) {
		return evt.ctrlKey || evt.metaKey;
	}

	isShiftDown(evt: KeyboardEvent) {
		return evt.shiftKey;
	}

	isAltDown(evt: KeyboardEvent) {
		return evt.altKey;
	}

	/**
	 * mouse down
	 * @param e
	 */
	handle_mousedown(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		const hitTarget = renderer.renderer.plugins.interaction.hitTest(
			evt.data.global
		);
		if (hitTarget instanceof UI) {
			this._hitTarget = hitTarget;
		} else if (hitTarget instanceof BaseGizmo) {
			this._hitTarget = hitTarget.parent as UI;
		} else if (hitTarget instanceof EditorGizmo) {
			this._hitTarget = hitTarget.parent as UI;
		}
		const element = hitTarget._element;

		if (this._hitTarget instanceof UI) {
			if (element && element.movable) {
				this.__dragStart(evt);
			}
		}

		const sm = renderer.selectionModel;
		if (sm) {
			// ctrl down append Or remove
			if (this._ctrlDown) {
				if (sm.contains(element)) {
					sm.removeSelection(element);
				} else {
					sm.appendSelection(element);
				}
			} else {
				// normal
				if (!sm.contains(element)) {
					// if (element instanceof Element) {
					// 	const selection = sm.getSelection();
					// 	renderer.changeLayer(selection, LayerType.DefaultZIndex);
					// 	renderer.changeLayer(element, LayerType.SelectZIndex);
					// }
					sm.setSelection(element);
				}
			}
		}

		if (this._doubleClickedTimeout) {
			clearTimeout(this._doubleClickedTimeout);
			this._doubleClickedTimeout = null;
			// console.log('double click：', element);
			this.emit(InteractionEvents.DoubleClick, {
				source: this,
				target: element,
			});
		} else {
			// console.log('click：', element);
			this.emit(InteractionEvents.Click, {
				source: this,
				target: element,
			});
		}
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void {
		this._doubleClickedTimeout = setTimeout(() => {
			this._doubleClickedTimeout = null;
		}, 300);
		if (this._hitTarget) {
			this._hitTarget.update();
			const gizmos = this._hitTarget?._gizmos;
			gizmos &&
				gizmos.forEach((gizmo: BaseGizmo) => {
					gizmo.handle_mouseup(evt);
				});
		} else {
			const renderer = this.renderer;
			const sm = renderer.selectionModel;
			const datas = sm?.getSelection();
			if (datas) {
				datas.forEach((data: Element) => {
					const ui = renderer.getUIById(data.id);
					if (ui) {
						ui.update();
						const gizmos = ui?._gizmos;
						gizmos &&
							gizmos.forEach((gizmo: BaseGizmo) => {
								gizmo.handle_mouseup(evt);
							});
					}
				});
			}
		}
		this.__dragEnd(evt);
	}

	/**
	 * mouse move
	 * @param e
	 */
	handle_mousemove(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		this.__dragMove(evt);
		const hoverTarget = renderer.renderer.plugins.interaction.hitTest(
			evt.data.global
		);
		const oldHover = this._hoverTarget;

		if (oldHover !== hoverTarget) {
			this._hoverTarget = hoverTarget;
			const newHover = hoverTarget;
			const oldElement = oldHover && oldHover._element;
			const newElement = newHover && newHover._element;
			if (oldElement) {
				oldElement.hovered = false;
				// if (oldHover && oldHover instanceof UI) {
				// 	oldHover.filterOff();
				// }
				// renderer.changeLayer(newElement, LayerType.DefaultZIndex);
			}
			if (newElement) {
				newElement.hovered = true;
				// if (oldHover && oldHover instanceof UI) {
				// 	oldHover.filterOn();
				// }
				// renderer.changeLayer(newElement, LayerType.SelectZIndex);
			}
			this.emit(InteractionEvents.Hover, {
				source: this,
				oldValue: oldElement,
				newValue: newElement,
			});
		}
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseout(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._hitTarget) {
			const gizmos = this._hitTarget?._gizmos;
			gizmos &&
				gizmos.forEach((gizmo: BaseGizmo) => {
					gizmo.handle_mouseout(evt);
				});
			this._oldOutTarget = this._hitTarget;
			const position = evt.data.getLocalPosition(viewport);
			this._oldMousePoint = new Point(position.x, position.y);
			this._onDocumentMouseUp = this.handle_documentMouseUp.bind(this);
			document.addEventListener("mouseup", this._onDocumentMouseUp);
		}
		this.__dragEnd(evt);
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseover(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._oldOutTarget && this._oldMousePoint) {
			const mousePoint = this._oldMousePoint;
			const position = evt.data.getLocalPosition(viewport);
			this._hitTarget = this._oldOutTarget;
			this._hitTarget._element.translation(
				position.x - mousePoint.x,
				position.y - mousePoint.y
			);
			this.__dragStart(evt);
			this._oldOutTarget = undefined;
			document.removeEventListener("mouseup", this._onDocumentMouseUp);
		}
	}

	/**
	 * drag start
	 * @param evt
	 */
	protected __dragStart(evt: InteractionEvent) {
		const hitTarget: any = this._hitTarget;
		if (this._isDragging) return;
		if (hitTarget instanceof UI) {
			const position = evt.data.getLocalPosition(hitTarget.parent);
			this._mousePoint = new Point(position.x, position.y);
			// this._isDragging = true;
			// this.emit(InteractionEvents.DragStart, {
			// 	source: this,
			// 	target: hitTarget._element
			// });
		}
	}

	/**
	 * drag Move
	 * @param evt
	 */
	protected __dragMove(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		if (!hitTarget || !hitTarget.parent) return;
		const position = evt.data.getLocalPosition(hitTarget.parent);
		if (!this._isDragging && this._mousePoint) {
			if (
				Math.abs(position.x - this._mousePoint.x) >= 0.1 ||
				Math.abs(position.y - this._mousePoint.y) >= 0.1
			) {
				this._isDragging = true;
				this.emit(InteractionEvents.DragStart, {
					source: this,
					target: hitTarget._element,
				});
				// console.log('__DragStart');
			}
		}
		if (!this._isDragging) return;

		if (hitTarget instanceof UI && this._mousePoint) {
			// console.log('__dragMove');
			this.renderer.moveSelectedElements(
				position.x - this._mousePoint.x,
				position.y - this._mousePoint.y
			);
			this._mousePoint = new Point(position.x, position.y);
			evt.stopPropagation();
			this.emit(InteractionEvents.Dragging, {
				source: this,
				target: hitTarget._element,
			});
			if (!hitTarget._element.dragging) {
				hitTarget._element.dragging = true;
			}
		}
	}

	/**
	 * drag End
	 * @param evt
	 */
	protected __dragEnd(evt?: InteractionEvent) {
		if (this._isDragging) {
			this._isDragging = false;
			evt && evt.stopPropagation();
			// console.log('__dragEnd', this._isDragging);
			const hitTarget = this._hitTarget;
			if (hitTarget && hitTarget._element) {
				hitTarget._element.dragging = false;
				this.emit(InteractionEvents.DragEnd, {
					source: this,
					target: hitTarget._element,
				});
			}
		}
		this._hitTarget = undefined;
		this._mousePoint = undefined;
	}

	/**
	 * key down event
	 * @param scope
	 * @param evt
	 */
	protected handle_keydown(evt: KeyboardEvent): void {
		const key = evt.code;
		// control key
		const oldCtrlDown = this._ctrlDown;
		this._ctrlDown = this.isCtrlDown(evt);
		const newCtrlDown = this._ctrlDown;
		if (oldCtrlDown !== newCtrlDown) {
			// document.body.style.cursor = "url('renderer/icons/ic_align.png') 18 18, auto";
			this.emit(KeyboardEvents.CtrlKeyDown, {
				source: this,
				key,
				oldCtrlDown,
				newCtrlDown,
			});
			const element = this._hoverTarget?._element;
			if (element) {
				element.hovered = false;
			}
		}

		// control key
		const oldShiftDown = this._shiftDown;
		this._shiftDown = this.isShiftDown(evt);
		const newShiftDown = this._shiftDown;
		if (oldShiftDown !== newShiftDown) {
			this.emit(KeyboardEvents.ShiftKey, {
				source: this,
				key,
				oldShiftDown,
				newShiftDown,
			});
		}

		// control key
		const oldAltDown = this._altDown;
		this._altDown = this.isAltDown(evt);
		const newAltDown = this._altDown;
		if (oldAltDown !== newAltDown) {
			this.emit(KeyboardEvents.AltKey, {
				source: this,
				key,
				oldAltDown,
				newAltDown,
			});
		}

		this.emit(KeyboardEvents.KeyDown, {
			source: this,
			event: evt,
		});
		this.__dragEnd();
	}

	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_keyup(evt: KeyboardEvent): void {
		const key = evt.code;
		const oldCtrlDown = this._ctrlDown;
		this._ctrlDown = this.isCtrlDown(evt);
		const newCtrlDown = this._ctrlDown;
		if (oldCtrlDown !== newCtrlDown) {
			document.body.style.cursor = "default";
			const element = this._hoverTarget?._element;
			if (element) {
				element.hovered = true;
			}
			this.emit(KeyboardEvents.CtrlKeyUp, {
				source: this,
				key,
				oldCtrlDown,
				newCtrlDown,
			});
		}

		// control key
		const oldShiftDown = this._shiftDown;
		this._shiftDown = this.isShiftDown(evt);
		const newShiftDown = this._shiftDown;
		if (oldShiftDown !== newShiftDown) {
			this.emit(KeyboardEvents.ShiftKey, {
				source: this,
				key,
				oldShiftDown,
				newShiftDown,
			});
		}

		// control key
		const oldAltDown = this._altDown;
		this._altDown = this.isAltDown(evt);
		const newAltDown = this._altDown;
		if (oldAltDown !== newAltDown) {
			this.emit(KeyboardEvents.AltKey, {
				source: this,
				key,
				oldAltDown,
				newAltDown,
			});
		}

		this.emit(KeyboardEvents.KeyUp, {
			source: this,
			event: evt,
		});
	}
	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_contextmenu(e: any): void {
		e.preventDefault();
		this.__dragEnd(e);
	}

	_cancel() { }

	handle_documentMouseDown(e: MouseEvent) {
		if (this._oldOutTarget) {
			e.preventDefault();
			e.stopPropagation();
			this._oldOutTarget = undefined;
		}
		document.removeEventListener("mousedown", this._onDocumentMouseDown);
	}

	handle_documentMouseUp(e: MouseEvent) {
		if (this._isDragging) {
			this._isDragging = false;
			const hitTarget = this._hitTarget;
			if (hitTarget && hitTarget._element) {
				hitTarget._element.dragging = false;
				this.emit(InteractionEvents.DragEnd, {
					source: this,
					target: hitTarget._element,
				});
			}
		}
		this._oldOutTarget = undefined;
		this._oldMousePoint = undefined;
		document.removeEventListener("mouseup", this._onDocumentMouseUp);
	}
}
