import { Graphics, Texture } from "pixi.js";
import { guide } from "../utils";
import { Element } from "@blood4/modeling";
import {
	AlignGizmo,
	BaseGizmo,
	DimensionGizmo,
	EditorGizmo,
	Label2Gizmo,
	LabelGizmo,
} from "../gizmos";
import { PixiRenderer } from "../PixiRenderer";
import { DefaultInteraction } from "../interaction/DefaultInteraction";
import { GlowFilter, OutlineFilter } from "pixi-filters";

export class UI extends Graphics {
	_id: string = guide();
	_element: Element;
	_renderer: PixiRenderer<any>;
	_imageCache?: HTMLImageElement;
	_textureCache?: Texture;
	_gizmos: Map<string, BaseGizmo> = new Map();
	_outlineFilterBlue = new OutlineFilter(2, 0x99ff99);
	_outlineFilterRed = new GlowFilter({
		distance: 10,
		outerStrength: 4,
		innerStrength: 0,
		color: 0xff0000,
		quality: 1.0,
		knockout: false,
	});
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super();
		this._element = element;
		this._renderer = renderer;
		this.visible = element.visible;
		this.interactive = element.styles.interactive;
		this.buttonMode = element.styles.buttonMode;
		this.cursor = element.styles.cursor;
		this.zIndex = element.styles.zIndex;
		this._invalidate();
		this._validate();
	}

	/**
	 * invalidate
	 * @param checkGizmo
	 */
	public _invalidate(checkGizmo?: boolean): void {
		// console.log('invalidate');
	}

	/**
	 * validate
	 */
	public _validate(checkGizmo?: boolean): void {
		this._updateStyles();
		this._checkGizmos();
		this._validateImpl();
	}

	calculateBounds(): void { }

	/**
	 * update glod styles from view
	 */
	protected _updateStyles(): void {
		const element = this._element as Element;
		if (!element) return;
		this.visible = element.visible;
		this.interactive = element.styles.interactive;
		this.buttonMode = element.styles.buttonMode;
		this.cursor = element.styles.cursor;
		this.zIndex = element.styles.zIndex;
		this.getBounds();
		const bounds = element.getAABB();
		this.pivot.x = bounds.x + bounds.width / 2;
		this.pivot.y = bounds.y + bounds.height / 2;
		this.x = bounds.x + bounds.width / 2;
		this.y = bounds.y + bounds.height / 2;
		// this.blendMode = BLEND_MODES.ADD;
		// this.filters = [new BloomFilter(2, 4, settings.FILTER_RESOLUTION, 5)];
	}

	/**
	 * check all gizmos
	 */
	protected _checkGizmos(): void {
		const element = this._element;
		if (!element) return;
		this._checkLabelGizmo();
		this._checkLabel2Gizmo();
		this._checkEditorGizmo();
		// this._checkAlignGizmo();
		this._checkDimensionGizmo();
	}

	protected _checkLabelGizmo(): void {
		const element = this._element;
		if (!element) return;
		const name = element.name;
		let gizmo = this._gizmos.get("labelGizmo");
		if (name != null && name !== "") {
			if (!gizmo) {
				gizmo = new LabelGizmo(element, this._renderer);
				this.addChild(gizmo);
				this._gizmos.set("labelGizmo", gizmo);
			}
		} else {
			if (gizmo) {
				this.removeChild(gizmo);
				gizmo.unmount();
				this._gizmos.delete("labelGizmo");
			}
		}
	}
	protected _checkLabel2Gizmo(): void {
		const element = this._element;
		if (!element) return;
		const name2 = element.name2;
		let gizmo = this._gizmos.get("label2Gizmo");
		if (name2 != null && name2 !== "") {
			if (!gizmo) {
				gizmo = new Label2Gizmo(element, this._renderer);
				this.addChild(gizmo);
				this._gizmos.set("label2Gizmo", gizmo);
			}
		} else {
			if (gizmo) {
				this.removeChild(gizmo);
				gizmo.unmount();
				this._gizmos.delete("label2Gizmo");
			}
		}
	}

	protected _checkEditorGizmo(): void {
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;
		let gizmo = this._gizmos.get("editorGizmo");
		if (element.selected && !element.dragging) {
			if (!gizmo) {
				gizmo = new EditorGizmo(element, this._renderer);
				this.addChild(gizmo);
				this._gizmos.set("editorGizmo", gizmo);
			}
		} else if (
			(!element.selected || element.dragging || element.isEditing) &&
			gizmo
		) {
			this.removeChild(gizmo);
			gizmo.unmount();
			this._gizmos.delete("editorGizmo");
		}
	}

	protected _checkAlignGizmo(): void {
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;
		let gizmo = this._gizmos.get("alignGizmo");
		const interactionManager = renderer.interactionManager;
		const current = interactionManager.getCurrentInteraction();
		// DefaultInteraction
		if (current instanceof DefaultInteraction) {
			if (current._ctrlDown) {
				if (!gizmo) {
					gizmo = new AlignGizmo(element, this._renderer);
					this.addChild(gizmo);
					this._gizmos.set("alignGizmo", gizmo);
				}
			} else {
				if (gizmo) {
					this.removeChild(gizmo);
					gizmo.unmount();
					this._gizmos.delete("alignGizmo");
				}
			}
		}
	}

	protected _checkDimensionGizmo(): void {
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;
		let gizmo = this._gizmos.get("dimensionGizmo");
		if (element.selected && !element.dragging) {
			if (!gizmo) {
				gizmo = new DimensionGizmo(element, this._renderer);
				this.addChild(gizmo);
				this._gizmos.set("dimensionGizmo", gizmo);
			}
		} else if (
			(!element.selected || element.dragging || element.isEditing) &&
			gizmo
		) {
			this.removeChild(gizmo);
			gizmo.unmount();
			this._gizmos.delete("dimensionGizmo");
		}
	}

	/**
	 * validate implementation
	 * 1. calculate boundingbox: body , gizmos
	 */
	protected _validateImpl(): void {
		this._draw();
	}

	/**
	 *
	 */
	_draw() {
		this.clear();
		const element = this._element as Element;
		if (!element || !element.visible) return;
		if (!this._element || !this._element.visible) return;
		this._drawBody();
		this._drawOuter();
		this._drawHover();
		this._drawSelect();
		this._drawGizmos();
		if (this._element && this._element.debug) {
			this._drawBoundingBox();
			this._drawAABB();
			this._drawOBB();
		}
	}

	/**
	 * 绘制 BoundingBox
	 */
	protected _drawBoundingBox() { }

	/**
	 * 绘制AABB Bounds
	 */
	protected _drawAABB() { }

	/**
	 * 绘制OBB Bounds
	 */
	protected _drawOBB() { }

	/**
	 * draw Body
	 */
	protected _drawBody(): void {
		const element = this._element as Element;
		if (!element) return;
		if (element.selected) {
			this.beginFill(0xff9966, 1.0);
			this.lineStyle(1, 0x00ff00, 1.0);
			this.drawEllipse(element.x, element.y, 20, 20);
			this.endFill();
		} else {
			this.beginFill(0x00ff00, 1.0);
			this.lineStyle(1, 0x00ff00, 1.0);
			this.drawEllipse(element.x, element.y, 20, 20);
			this.endFill();
		}
	}

	protected _drawBorder(): void { }

	/**
	 * 绘制Boundingbox外边框
	 * @returns
	 */
	protected _drawOuter(): void { }

	protected _drawHover(): void {
		const element = this._element as Element;
		if (!element) return;
		if (element.hovered) {
			const points = element.points;
			this.lineStyle(0.6, 0x3477fc, 1.0);
			const path: number[] = [];
			points.forEach((point: number[]) => {
				path.push(point[0], point[1]);
			});
			this.drawPolygon(path);
		}
	}

	/**
	 * 绘制_drawSelect: blur, grow
	 * @returns
	 */
	protected _drawSelect(): void {
		const element = this._element as Element;
		if (!element) return;
		if (element.selected) {
			const points = element.points;
			if (element.dragging) {
				this.beginFill(0x000000, 0.1);
				this.lineStyle(0.6, 0x3477fc, 1.0);
			} else {
				this.lineStyle(1, 0x3477fc, 1.0);
			}
			const path: number[] = [];
			points.forEach((point: number[]) => {
				path.push(point[0], point[1]);
			});
			this.drawPolygon(path);
		}
	}

	/**
	 * draw editor
	 * @todo
	 */
	protected _drawEditor(): void { }

	/**
	 * draw gizmos
	 */
	protected _drawGizmos(): void {
		if (this._gizmos) {
			this._gizmos.forEach((gizmo) => {
				if (gizmo instanceof BaseGizmo) {
					gizmo.update();
				}
			});
		}
	}

	/**
	 *
	 * @returns
	 */
	public update(): void {
		this._invalidate();
		this._validate();
	}

	filterOn() {
		this.filters = [this._outlineFilterRed];
	}

	filterOff() {
		this.filters = [this._outlineFilterBlue];
	}

	async loadImage(src: string) {
		return new Promise((resolve, reject) => {
			const img = new Image();
			img.onload = () => resolve(img);
			img.onerror = (e) => reject(e);
			img.src = src;
		});
	}
}
