import { isEqual } from "@antv/util";
import { EventEmitter } from "eventemitter3";
import { BaseInteraction } from "./BaseInteraction";
import { DefaultInteraction } from "./DefaultInteraction";
import { EditInteraction } from "./EditInteraction";
import { PixiRenderer } from "../PixiRenderer";
import { Point } from "pixi.js";
import { CreateInteraction } from "./CreateInteraction";
import { AutoCreateInteraction } from "./AutoCreateinteraction";
import { CreateLineInteraction } from "./CreateLineInteraction";

/**
 * 2D Viewer Interaction Manager
 */
export class InteractionManager extends EventEmitter {
	private _renderer: PixiRenderer<any>;
	private _current?: BaseInteraction | BaseInteraction[];
	private _interactionMap: Map<string, BaseInteraction> = new Map();

	constructor(renderer: PixiRenderer<any>) {
		super();
		this._renderer = renderer;
		const interaction = new DefaultInteraction(renderer);
		this.execute(interaction);
		this._interactionMap.set("default", interaction);
		this._current = interaction;
		const editInteraction = new EditInteraction(renderer);
		this._interactionMap.set("edit", editInteraction);
	}

	/**
	 * execute current interaction
	 * @param interaction
	 * @returns
	 */
	public execute(t: BaseInteraction | BaseInteraction[]): void {
		if (isEqual(this._current, t)) return;
		const oldValue = this._current;
		if (oldValue instanceof Array) {
			oldValue.forEach((interaction: BaseInteraction) => {
				interaction?.clear();
				interaction?.unmount();
			});
		} else {
			oldValue?.clear();
			oldValue?.unmount();
		}

		this._current = t;
		if (t instanceof Array) {
			t.forEach((i: BaseInteraction) => {
				i.mount();
			});
		} else {
			t?.mount();
		}

		// 清楚吸附相关数据
		if (this._renderer.refline) {
			this._renderer.refline.clear();
		}
		this.emit("interaction.change", {
			source: this,
			oldValue,
			newValue: t,
		});
	}

	public setInteractions(modes: string[] = ["default"]) {
		const interactions: BaseInteraction[] = [];
		modes.forEach((mode: string) => {
			let interaction = this._interactionMap.get(mode);
			if (!interaction) {
				if (mode === "default") {
					interaction = new DefaultInteraction(this._renderer);
					this._interactionMap.set(mode, interaction);
				} else if (mode === "edit") {
					interaction = new EditInteraction(this._renderer);
					this._interactionMap.set(mode, interaction);
				}
			}
			if (interaction) {
				interactions.push(interaction);
			}
		});

		this.execute(interactions);

		// const model = this._renderer.model;
		// model.forEach((element: Element) => {
		// 	element.gatherRects(model.getDatas());
		// });
	}

	/**
	 *
	 * @param mode
	 * @returns
	 */
	public getInteraction(mode: string = "default") {
		return this._interactionMap.get(mode);
	}

	/**
	 *
	 */
	public setDefaultInteraction() {
		// console.log('set default interaction');
		let interaction =
			this._interactionMap.get("default") ||
			new DefaultInteraction(this._renderer);
		this._interactionMap.set("default", interaction);
		this.execute(interaction);
	}

	/**
	 *
	 */
	public setEditInteraction(mode: string = "default") {
		// lazy, auto
		// console.log('set edit interaction', mode);
		let interaction =
			this._interactionMap.get("edit") ||
			new EditInteraction(this._renderer);
		this._interactionMap.set("edit", interaction);
		this.execute(interaction);

		// todo吸附
		// const model = this._renderer.model;
		// model.forEach((element: Element) => {
		// 	element.gatherRects(model.getDatas());
		// });
	}

	/**
	 * use Create Element Interaction
	 */
	public setCreateInteraction(
		onMouseDown?: (point?: Point, scope?: any) => any,
		onMouseMove?: (point?: Point, scope?: any) => any,
		onMouseUp?: (point?: Point, scope?: any) => any,
		onFinish?: (scope?: any) => any
	) {
		let interaction = (this._interactionMap.get("create") ||
			new CreateInteraction(
				this._renderer,
				onMouseDown,
				onMouseMove,
				onMouseUp,
				onFinish
			)) as CreateInteraction;
		this._interactionMap.set("create", interaction);
		if (onMouseDown) {
			interaction.onMouseDown = onMouseDown;
		}
		if (onMouseMove) {
			interaction.onMouseMove = onMouseMove;
		}
		if (onMouseUp) {
			interaction.onMouseUp = onMouseUp;
		}
		if (onFinish) {
			interaction.onFinish = onFinish;
		}
		this.execute(interaction);
	}

	/**
	 * use Auto Create Element Interaction
	 */
	public setAutoCreateInteraction(
		onMouseDown?: (point?: Point, scope?: any) => any,
		onMouseMove?: (point?: Point, scope?: any) => any,
		onMouseUp?: (point?: Point, scope?: any) => any,
		onFinish?: (scope?: any) => any
	) {
		let interaction = (this._interactionMap.get("autocreate") ||
			new AutoCreateInteraction(
				this._renderer,
				onMouseDown,
				onMouseMove,
				onMouseUp,
				onFinish
			)) as AutoCreateInteraction;
		this._interactionMap.set("autocreate", interaction);
		if (onMouseDown) {
			interaction.onMouseDown = onMouseDown;
		}
		if (onMouseMove) {
			interaction.onMouseMove = onMouseMove;
		}
		if (onMouseUp) {
			interaction.onMouseUp = onMouseUp;
		}
		if (onFinish) {
			interaction.onFinish = onFinish;
		}
		this.execute(interaction);
	}

	/**
	 * use Create Line Interaction
	 */
	public setCreateLineInteraction(
		onMouseDown?: (point?: Point, scope?: any) => any,
		onMouseMove?: (point?: Point, scope?: any) => any,
		onMouseUp?: (point?: Point, scope?: any) => any,
		onFinish?: (scope?: any) => any
	) {
		let interaction = (this._interactionMap.get("createline") ||
			new CreateLineInteraction(
				this._renderer,
				onMouseDown,
				onMouseMove,
				onMouseUp,
				onFinish
			)) as CreateLineInteraction;
		this._interactionMap.set("createline", interaction);
		if (onMouseDown) {
			interaction.onMouseDown = onMouseDown;
		}
		if (onMouseMove) {
			interaction.onMouseMove = onMouseMove;
		}
		if (onMouseUp) {
			interaction.onMouseUp = onMouseUp;
		}
		if (onFinish) {
			interaction.onFinish = onFinish;
		}
		this.execute(interaction);
	}

	/**
	 * use Create Shape Interaction
	 */
	public setCreateShapeInteraction(
		onMouseDown?: (point?: Point, scope?: any) => any,
		onMouseMove?: (point?: Point, scope?: any) => any,
		onMouseUp?: (point?: Point, scope?: any) => any,
		onFinish?: (scope?: any) => any
	) {
		let interaction = (this._interactionMap.get("create") ||
			new CreateInteraction(
				this._renderer,
				onMouseDown,
				onMouseMove,
				onMouseUp,
				onFinish
			)) as CreateInteraction;
		this._interactionMap.set("create", interaction);
		if (onMouseDown) {
			interaction.onMouseDown = onMouseDown;
		}
		if (onMouseMove) {
			interaction.onMouseMove = onMouseMove;
		}
		if (onMouseUp) {
			interaction.onMouseUp = onMouseUp;
		}
		if (onFinish) {
			interaction.onFinish = onFinish;
		}
		this.execute(interaction);
	}

	/**
	 *
	 * @returns
	 */
	public getCurrentInteraction():
		| BaseInteraction
		| BaseInteraction[]
		| undefined {
		return this._current;
	}
}
