import React, { Component, createRef } from "react";
import Application from "./application";
declare global {
	interface Window {
		renderer: Application;
		application: Application;
	}
}

const style = {
	width: "100%",
	height: "100%",
	backgroundColor: "#CDCDCD",
};

type Props = {};

type State = {};

class RendererComponent extends Component<Props, State> {
	app?: Application;
	canvasRef = createRef<HTMLCanvasElement>();

	/**
	 *
	 * @param props
	 */
	constructor(props: Props) {
		super(props);
	}

	/**
	 * 挂载组件
	 */
	componentDidMount() {
		console.log("PixiRenderer:componentDidMount");
		this.app = new Application({
			canvas: this.canvasRef.current,
			id: "application",
			debug: {
				model:
					!!location.search.includes("debug") &&
					!!location.search.includes("model"),
				ui:
					!!location.search.includes("debug") &&
					!!location.search.includes("ui"),
				origin:
					!!location.search.includes("debug") &&
					!!location.search.includes("origin"),
				controlPane: true || !!location.search.includes("debug"),
			},
		});
		window.renderer = this.app;
		window.application = this.app;
		this.app.load();
	}

	/**
	 * 卸载组件
	 */
	componentWillUnmount() {
		console.log("PixiRenderer:componentWillUnmount");
		if (this.app) {
			this.app.dispose();
		}
	}

	render() {
		return <canvas style={style} ref={this.canvasRef} />;
	}
}

export default RendererComponent;
