import { EventEmitter } from "eventemitter3";
import { PixiRenderer } from "..";

export class BaseInteraction extends EventEmitter {
	protected renderer: PixiRenderer<any>;

	constructor(renderer: PixiRenderer<any>) {
		super();
		this.renderer = renderer;
	}

	/**
	 * mount
	 * @returns
	 */
	mount() { }

	/**
	 * unmount
	 * @returns
	 */
	unmount() { }

	/**
	 * update
	 */
	update() { }

	/**
	 * clear
	 */
	clear() { }

	/**
	 * change pointer cursor
	 * @param cursor
	 * @returns
	 */
	changeCursorMode(cursor = "default"): void {
		const renderer = this.renderer;
		if (!renderer) return;
		renderer.renderer.plugins.interaction.cursorStyles.crosshair = cursor;
		renderer.renderer.plugins.interaction.cursorStyles.default = cursor;
		renderer.renderer.plugins.interaction.setCursorMode(cursor);
	}
}
