import { expand, offset } from '@jscad/modeling/src/operations/expansions';
import { line, square } from '@jscad/modeling/src/primitives';
import hull from 'hull.js';
import { matrix, transpose, multiply, min, max } from 'mathjs';

export class OBB {
	private _originPoints: number[][] = [];
	private _points: number[][] = [];

	constructor(originPoints: number[][]) {
		this._originPoints = originPoints;
		this._points = OBB.fromPoints(originPoints);
	}

	public get points(): number[][] {
		return this._points;
	}

	static fromPoints(points: number[][]): number[][] {
		if (!points || points.length === 0) return [];
		const hullPoints = hull(points, Infinity) as number[][];
		const edges: number[][] = [];
		for (let i = 0; i < hullPoints.length - 1; i++) {
			edges.push([hullPoints[i + 1][0] - hullPoints[i][0], hullPoints[i + 1][1] - hullPoints[i][1]]);
		}

		const angles = edges.map((edge) => Math.abs(Math.atan2(edge[1], edge[0]) % (Math.PI / 2)));

		const rotations = angles.map((angle) => {
			return [
				[Math.cos(angle), Math.cos(angle - Math.PI / 2)],
				[Math.cos(angle + Math.PI / 2), Math.cos(angle)]
			];
		});

		const rotPoints = rotations.map((rotation) =>
			multiply(matrix(rotation), transpose(matrix(hullPoints))).toArray()
		);

		const minXY = rotPoints.map((pMat) => {
			const minValues = min(pMat, 1);
			return [minValues[0], minValues[1]];
		});

		const maxXY = rotPoints.map((pMat) => {
			const minValues = max(pMat, 1);
			return [minValues[0], minValues[1]];
		});

		const minX = minXY.map((m) => m[0]);
		const minY = minXY.map((m) => m[1]);
		const maxX = maxXY.map((m) => m[0]);
		const maxY = maxXY.map((m) => m[1]);

		const areas = minX.map((_, i) => (maxX[i] - minX[i]) * (maxY[i] - minY[i]));

		const bestIdx = areas.reduce((iMax, x, i, arr) => (x < arr[iMax] ? i : iMax), 0);

		const x1 = maxX[bestIdx];
		const x2 = minX[bestIdx];
		const y1 = maxY[bestIdx];
		const y2 = minY[bestIdx];
		const r = rotations[bestIdx];
		const minRect: number[][] = [];
		minRect.push(multiply([x1, y2], r));
		minRect.push(multiply([x2, y2], r));
		minRect.push(multiply([x2, y1], r));
		minRect.push(multiply([x1, y1], r));
		return minRect;
	}

	/**
	 * Pads the rectangle making it grow in all directions.
	 * If paddingY is omitted, both paddingX and paddingY will be set to paddingX.
	 *
	 * @param [paddingX=0] - The horizontal padding amount.
	 * @param [paddingY=0] - The vertical padding amount.
	 * @return Returns itself.
	 */
	pad(paddingX = 0, paddingY = paddingX): this {
		let small = offset(
			{ delta: 10 },
			line([
				[0, 0],
				[100, 0]
			])
		);
		let newsquare = expand({ delta: 5, corners: 'chamfer' }, square({ size: 30 }));
		return this;
	}
}
