import { InteractionEvent } from "pixi.js";
import { Element } from "@blood4/modeling";
import { KeyboardEvents } from "../events";
import { DefaultInteraction } from "../interaction/DefaultInteraction";
import { PixiRenderer } from "../PixiRenderer";
import { BaseGizmo } from "./BaseGizmo";
import { AlignSpot } from "./spots";

export class AlignGizmo extends BaseGizmo {
	private _alignSpots: Map<string | number, AlignSpot> = new Map();
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super(element, renderer);
		this.interactive = true;
		this.buttonMode = false;
		this._element = element;
		this.zIndex = element.styles.editorGizmoZIndex;
		this.visible = element.styles.editorGizmoVisible;
		this.mount();
		this._eventEmitter.on(KeyboardEvents.CtrlKeyDown, (evt: any) => {
			this.unmount();
			this.mount();
		});
		this._eventEmitter.on(KeyboardEvents.CtrlKeyUp, (evt: any) => {
			if (this._alignSpots) {
				this._alignSpots.forEach((spot: AlignSpot) => {
					spot.unmount();
				});
				this._alignSpots.clear();
			}
		});
	}

	/**
	 * _invalidate
	 */
	public _invalidate(): void {
		// console.log('_invalidate');
		this.clear();
	}

	/**
	 * validate
	 */
	public _validate(): void {
		// console.log('_validate');
		this._updateStyles();
		this._validateImpl();
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		const element: Element = this._element;
		if (!element) return;
		this.zIndex = element.styles.editorGizmoZIndex;
		this.visible = element.styles.editorGizmoVisible;
	}

	/**
	 * update
	 */
	protected _drawBody(): void {
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;
		this.clear();
		// const interactionManager = renderer.interactionManager;
		// const current = interactionManager.getCurrentInteraction();
		// if (!(current instanceof DefaultInteraction)) return;
		// if (current._ctrlDown) {
		// 	const points = element.points;
		// 	// 边界拉伸, 每个边的中心点
		// 	if (points && this._alignSpots && this._alignSpots.size > 0) {
		// 		this._alignSpots.forEach((spot: AlignSpot, key: string | number) => {
		// 			const from = points[Number(key) - 0.5],
		// 				to = points[(Number(key) - 0.5 + 1) % points.length];
		// 			spot.setFromTo(from, to);
		// 			spot.visible = true;
		// 		});
		// 	}
		// } else if (this._alignSpots) {
		// 	this._alignSpots.forEach((spot: AlignSpot, key: string | number) => {
		// 		spot.visible = false;
		// 	});
		// }
		const points = element.points;
		// 边界拉伸, 每个边的中心点
		if (points && this._alignSpots && this._alignSpots.size > 0) {
			this._alignSpots.forEach(
				(spot: AlignSpot, key: string | number) => {
					const from = points[Number(key) - 0.5],
						to = points[(Number(key) - 0.5 + 1) % points.length];
					spot.setFromTo(from, to);
				}
			);
		}
	}

	/**
	 * set up interaction event
	 */
	public mount(): void {
		this._keydownEvent = this.handle_keydown.bind(this);
		this._keyupEvent = this.handle_keyup.bind(this);
		document.addEventListener("keydown", this._keydownEvent);
		document.addEventListener("keyup", this._keyupEvent);
		// console.log('mount align gizmo');
		const element = this._element,
			renderer = this._renderer;
		if (!element || !renderer) return;
		const interactionManager = renderer.interactionManager;
		const current = interactionManager.getCurrentInteraction();
		if (!(current instanceof DefaultInteraction)) return;
		const points = element.points;
		// 边界拉伸, 每个边的中心点
		if (points) {
			points.forEach((point: number[], index: number) => {
				const from = point,
					to = points[(index + 1) % points.length];
				const spot = new AlignSpot(
					element,
					from,
					to,
					renderer,
					index + 0.5,
					"rect"
				);
				this._alignSpots.set(index + 0.5, spot);
				spot.visible = true;
				spot.mode = "align";
				this.addChild(spot);
			});
		}
		this._invalidate();
		this._validate();
	}

	/**
	 * tear down interaction event
	 */
	public unmount(): void {
		// console.log('unmount align gizmo');
		if (this._alignSpots) {
			this._alignSpots.forEach((spot: AlignSpot) => {
				this.removeChild(spot);
				spot.unmount();
			});
			this._alignSpots.clear();
		}
		document.removeEventListener("keydown", this._keydownEvent);
		document.removeEventListener("keyup", this._keyupEvent);
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void {
		if (this._alignSpots) {
			this._alignSpots.forEach((spot: AlignSpot) => {
				spot.handle_mouseup(evt);
			});
		}
	}
}
