import { EventEmitter } from "eventemitter3";
import { EventsModel } from "./events";
import { IModel } from "./interfaces";
import { TType } from "./types";

export interface ModelProps { }

/**
 * 数据管理容器
 * Model
 * 	--Model
 */
export class Model<S extends TType> extends EventEmitter implements IModel<S> {
	_pros?: ModelProps;
	_datas: S[] = [];
	_datasMap: Map<string, S> = new Map();

	public constructor(pros?: ModelProps) {
		super();
		this._pros = pros;
	}

	/**
	 * add data to datas
	 * @param data
	 * @returns
	 */
	add(data: S) {
		const datas = this._datas,
			datasMap = this._datasMap;
		if (this.contains(data)) {
			return this;
		}
		const old: Array<S> = [];
		Object.assign(old, datas);
		datas.push(data);
		datasMap.set(data._id, data);
		this.emit(EventsModel.Add, {
			source: this,
			data,
		});
		return this;
	}

	/**
	 * get by index
	 * @param id
	 * @returns
	 */
	get(index: number): S | undefined {
		return this._datas[index];
	}

	/**
	 * get S By Id
	 * @param id
	 * @returns
	 */
	getById(id: string): S | undefined {
		return this._datasMap.get(id);
	}

	/**
	 * remove data from datas
	 * @param data
	 * @returns
	 */
	remove(data: S | S[]) {
		if (!data) return this;
		if (data instanceof Array) {
			data.forEach((d: S) => {
				this.remove(d);
			});
		} else {
			const datas = this._datas,
				index = datas.indexOf(data);
			if (index >= 0) {
				datas.splice(index, 1);
				this._datasMap.delete(data._id);
				this.emit(EventsModel.Remove, {
					source: this,
					data,
				});
			}
		}

		return this;
	}

	/**
	 *
	 * @param data
	 * @returns
	 */
	contains(data: S): boolean {
		return !!this._datasMap.get(data._id);
	}

	/**
	 *
	 * @param callback
	 * @param thisArg
	 * @returns
	 */
	forEach(callback: (data: S, index: number) => void, thisArg?: unknown) {
		this._datas.forEach(callback, thisArg);
		return this;
	}

	/**
	 * clear
	 * @returns
	 */
	clear() {
		const old = this._datas;
		this._datas = [];
		this._datasMap.clear();
		this.emit(EventsModel.Clear, {
			source: this,
			data: null,
		});
		return this;
	}

	/**
	 * get datas array
	 * @returns
	 */
	getDatas(filter?: (element: S) => boolean): S[] {
		if (filter) {
			let datas: S[] = [];
			this._datas.forEach((data: S) => {
				if (filter(data)) {
					datas.push(data);
				}
			});
			return datas;
		}
		return this._datas;
	}

	/**
	 * get datas map
	 * @returns
	 */
	getDatasMap(filter?: (element: S) => boolean): Map<string, S> {
		if (filter) {
			let datasMap: Map<string, S> = new Map();
			this._datas.forEach((data: S) => {
				if (filter(data)) {
					datasMap.set(data._id, data);
				}
			});
			return datasMap;
		}
		return this._datasMap;
	}

	/**
	 * 批量添加数据
	 * @param data {S[]} 添加的数据
	 * @returns
	 */
	batchAdd(data: S[]) {
		const datas = this._datas,
			datasMap = this._datasMap;
		const old: S[] = [];
		Object.assign(old, datas);
		data.forEach((d: S) => {
			if (this.contains(d)) {
				return this;
			}
			datas.push(d);
			datasMap.set(d._id, d);
		});
		this.emit(EventsModel.BatchAdd, {
			source: this,
			data,
		});
		return this;
	}
}
