import { mat2d, vec2 } from "gl-matrix";
import { geometries, primitives } from "@jscad/modeling";
import { Geom2 } from "@jscad/modeling/src/geometries/types";
import { Element, ElementProps } from "./Element";
import { BaseClientProps, BaseStyleProps, SHAPE, UI2D, UI3D } from "./types";
import { AABB, OBB } from "../shapes";

/**
 * Display object props
 */
export interface NodeProps<StyleProps = any, ClientProps = any>
	extends ElementProps { }

/**
 * Display object: Node
 */
export class Node<
	StyleProps extends BaseStyleProps = any,
	ClientProps extends BaseClientProps = any
	> extends Element<StyleProps, ClientProps> {
	constructor(props?: NodeProps<StyleProps, ClientProps>) {
		super({
			type: SHAPE.Node,
			...props,
		});
		this._updatePoints();
	}

	/**
	 *
	 * @param padding  {number | { top: number, right: number, bottom: number, left: number }} 间距
	 * @returns
	 */
	public getBoundingBox(
		padding?:
			| number
			| { top: number; right: number; bottom: number; left: number }
	): AABB {
		const aabb = this.getAABB().clone();
		let rect = new AABB(aabb.x, aabb.y, aabb.width, aabb.height);
		if (padding && typeof padding === "number") {
			rect.x -= padding;
			rect.y -= padding;
			rect.width += 2 * padding;
			rect.height += 2 * padding;
		} else if (padding && typeof padding === "object") {
			rect.x -= padding.left || 0;
			rect.y -= padding.top || 0;
			rect.width += (padding.left || 0) + (padding.right || 0);
			rect.height += (padding.top || 0) + (padding.bottom || 0);
		}
		return rect;
	}

	/**
	 * 计算内部顶点
	 */
	_updatePoints() {
		const rect: Geom2 = primitives.rectangle({
			center: [this.x, this.y],
			size: [this.width, this.height],
		});
		const points = geometries.geom2.toPoints(rect);
		if (this.angle !== 0) {
			const matrix = this._angleToMatrix(this.angle);
			this._matrix = matrix;
			const newPoints: number[][] = [];
			for (let i = 0, n = points.length; i < n; i++) {
				const out = vec2.create();
				const point = points[i];
				const pp = vec2.transformMat2d(
					out,
					vec2.fromValues(point[0], point[1]),
					mat2d.clone(matrix)
				);
				newPoints[i] = [pp[0], pp[1]];
			}
			this._points = newPoints;
		} else {
			this._points = points;
		}
	}

	/**
	 * AABB Bounds
	 */
	public getAABB(): AABB {
		this._updatePoints();
		const aabb = AABB.fromPoints(this._points);
		return aabb;
	}

	/**
	 * get OBB
	 * @returns
	 */
	public getOBB(
		padding?:
			| number
			| { top: number; right: number; bottom: number; left: number }
	): OBB {
		const points: number[][] = [];
		this._points.map((point: any) => {
			points.push([point[0], point[1]]);
		});
		const obb = new OBB(points);
		return obb;
	}
}
