import { InteractionEvent, Point } from "pixi.js";
import { InteractionEvents, PixiRenderer } from "..";
import { Element } from "@blood4/modeling";
import { Grid } from "../components";
import { UI } from "../ui";
import { DefaultInteraction } from "./DefaultInteraction";

export class EditInteraction extends DefaultInteraction {
	constructor(renderer: PixiRenderer<any>) {
		super(renderer);
	}

	/**
	 * handle zoomed
	 * @param e
	 */
	handle_zoomed(evt: InteractionEvent) {
		// if (this.renderer.grid && this.renderer.grid instanceof Grid) {
		// 	this.renderer.refline.clear();
		// }
	}

	/**
	 * handle panned
	 * @param e
	 */
	handle_panned(evt: InteractionEvent) {
		if (this.renderer.grid && this.renderer.grid instanceof Grid) {
			this.renderer.refline.clear();
		}
	}

	handle_mousedown(evt: InteractionEvent): void {
		super.handle_mousedown(evt);
		const element = this._hitTarget?._element;
		if (!element) return;

		const model = this.renderer.model;
		const datas = model.getDatas();
		element.gatherRects(datas);
		const refLine = element.refLine;
		element.updater = refLine.adsorbCreator({
			pageX: evt.data.global.x,
			pageY: evt.data.global.y,
			distance: 24,
		});
	}

	/**
	 * drag Move
	 * @param evt
	 */
	protected __dragMove(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		if (!hitTarget || !hitTarget.parent) return;
		const position = evt.data.getLocalPosition(hitTarget.parent);
		if (!this._isDragging && this._mousePoint) {
			if (
				Math.abs(position.x - this._mousePoint.x) >= 0.1 ||
				Math.abs(position.y - this._mousePoint.y) >= 0.1
			) {
				this._isDragging = true;
				this.emit(InteractionEvents.DragStart, {
					source: this,
					target: hitTarget._element,
				});
				// console.log('__DragStart');
			}
		}
		if (!this._isDragging) return;

		if (hitTarget instanceof UI && this._mousePoint) {
			// console.log('__dragMove');
			// this.renderer.moveSelectedElements(position.x - this._mousePoint.x, position.y - this._mousePoint.y);
			this._mousePoint = new Point(position.x, position.y);
			evt.stopPropagation();
			this.emit(InteractionEvents.Dragging, {
				source: this,
				target: hitTarget._element,
			});
			if (!hitTarget._element.dragging) {
				hitTarget._element.dragging = true;
			}
			const element = this._hitTarget?._element;
			if (!element) return;

			const refLine = element.refLine;
			const updater = element.updater;
			if (this.renderer.refline) {
				this.renderer.refline.datas = refLine.getAllRefLines() as any;
			}
			const scale = this.renderer?.getScaled() || 1;
			const { delta } = updater({
				pageX: evt.data.global.x,
				pageY: evt.data.global.y,
				distance: 24,
			});
			this.renderer.moveSelectedElements(
				delta.left / scale,
				delta.top / scale
			);
			evt.stopPropagation();
		}
	}

	/**
	 * drag End
	 * @param evt
	 */
	protected __dragEnd(evt: InteractionEvent) {
		if (this._isDragging) {
			this._isDragging = false;
			evt.stopPropagation();
			// console.log('__dragEnd', this._isDragging);
			const hitTarget = this._hitTarget;
			if (hitTarget && hitTarget._element) {
				hitTarget._element.dragging = false;
				this.emit(InteractionEvents.DragEnd, {
					source: this,
					target: hitTarget._element,
				});
				const element = hitTarget._element;
				const updater = element.updater;
				const { delta } = updater({
					pageX: evt.data.global.x,
					pageY: evt.data.global.y,
				});
				element.x += delta.left;
				element.y += delta.top;
			}
		}
		this._mousePoint = undefined;
	}
}
