import { Matrix, Text, Rectangle, Polygon } from "pixi.js";
import { Element, ShapeLine } from "@blood4/modeling";
import { UI } from ".";
import { DashLine } from "../components";
import { PixiRenderer } from "../PixiRenderer";

export class ShapeLineUI extends UI {
	text?: Text;
	constructor(element: Element, renderer: PixiRenderer<any>) {
		super(element, renderer);
	}

	/**
	 * update global styles from view
	 * @returns
	 */
	protected _updateStyles(): void {
		super._updateStyles();
		const element = this._element as Element;
		if (!element) return;
		const points4 = element.getHitArea(2);
		const path4: number[] = [];
		points4.forEach((point: number[]) => {
			path4.push(point[0], point[1]);
		});
		this.hitArea = new Polygon(path4);
	}

	/**
	 * 绘制 BoundingBox
	 */
	protected _drawBoundingBox() {
		const element = this._element as Element;
		if (!element) return;
		const rect: Rectangle = new Rectangle();
		if (this.text) {
			this.text.getLocalBounds(rect);
		}
		const boundingbox = element.getBoundingBox(10);
		this.lineStyle(1, 0xff6666, 1.0);
		this.beginFill(0x0000, 0.0);
		this.drawRect(
			boundingbox.x,
			boundingbox.y,
			boundingbox.width,
			boundingbox.height
		);
		this.endFill();

		const boundingbox2 = this._boundsRect;
		this.lineStyle(1, 0xff0033, 1.0);
		this.beginFill(0x0000, 0.0);
		this.drawRect(
			boundingbox2.x,
			boundingbox2.y,
			boundingbox2.width,
			boundingbox2.height
		);
		this.endFill();

		if (this.text) {
			const rect: Rectangle = new Rectangle();
			if (this.text) {
				this.text.getLocalBounds(rect);
			}
			const viewport: any = this.parent;
			const scale = viewport.scaled || 1.0;
			this.lineStyle(1, 0xffff66, 1.0);
			this.beginFill(0x0000, 0.0);
			this.drawRect(this.text.x, this.text.y, rect.width, rect.height);
			this.endFill();
		}
	}

	/**
	 * 绘制AABB Bounds
	 */
	protected _drawAABB() {
		const element = this._element as Element;
		if (!element) return;
		const aabb = element.getAABB();
		this.lineStyle(1, 0x00ff00, 1.0);
		this.beginFill(0xffff00, 0.0);
		this.drawRect(aabb.x, aabb.y, aabb.width, aabb.height);
		this.endFill();
		this.lineStyle(1, 0x000000, 1.0);
		this.drawEllipse(
			aabb.x + aabb.width / 2,
			aabb.y + aabb.height / 2,
			aabb.width / 2,
			aabb.height / 2
		);
	}

	/**
	 * 绘制OBB Bounds
	 */
	protected _drawOBB() {
		const element = this._element as Element;
		if (!element) return;
		const obb = element.getOBB();
		const points = obb.points;
		this.lineStyle(1, 0xffff00, 1.0);
		this.beginFill(0xffff00, 0.0);
		const path: number[] = [];
		points.forEach((point: number[]) => {
			path.push(point[0], point[1]);
		});
		this.drawPolygon(path);

		const points4 = element.getHitArea(2);
		this.lineStyle(10, 0x99cc33, 1.0);
		this.beginFill(0x99cc33, 0.5);
		const path4: number[] = [];
		points4.forEach((point: number[]) => {
			path4.push(point[0], point[1]);
		});
		this.drawPolygon(path4);
		this.closePath();
	}

	/**
	 * draw Body
	 */
	protected _drawBody(): void {
		const element = this._element as ShapeLine;

		if (!element) return;
		const points = element.points;
		if (!points || points.length === 0) return;

		const matrix = element._matrix,
			angle = element.angle;
		if (angle !== 0) {
			this.setMatrix(
				new Matrix(
					matrix[0],
					matrix[1],
					matrix[2],
					matrix[3],
					matrix[4],
					matrix[5]
				)
			);
		}
		const path: number[] = [];
		points.forEach((point: any) => {
			path.push(point[0], point[1]);
		});
		const lineDash = element.dash;
		if (lineDash) {
			const dash = new DashLine(this, {
				dash: lineDash,
				width: element.styles.strokeWidth || 1,
				color: element.styles.strokeColor,
				alpha: element.styles.strokeAlpha,
				useTexture: false,
				useDots: true,
			});
			dash.drawPolygon(path);
		} else {
			const isDrawing = element.clients.isDrawing;
			if (isDrawing) {
				points.forEach((point: number[], index: number) => {
					if (index === points.length - 2) {
						this.lineStyle(1.5, 0x737373, 1.0);
						this.beginFill(0xffffff, 1.0);
						this.drawCircle(point[0], point[1], 3);
						this.endFill();
					} else if (index === points.length - 1) {
						this.lineStyle(1.0, 0x000000, 0.35);
						this.beginFill(0xffffff, 1.0);
						this.drawCircle(point[0], point[1], 2.5);
						this.endFill();
					}
				});
			}
			this.lineStyle(
				element.styles.strokeWidth || 1,
				element.styles.strokeColor || 0xf78284,
				element.styles.strokeAlpha || 1.0
			);
			points.forEach((point: number[], index: number) => {
				if (index === 0) {
					this.moveTo(point[0], point[1]);
				} else {
					this.lineTo(point[0], point[1]);
				}
			});
		}
		if (angle !== 0) {
			this.setMatrix(new Matrix());
		}
	}

	protected _drawHover(): void {
		const element = this._element as Element;
		if (!element) return;
		const points = element.points;
		if (!points || points.length === 0) return;
		if (element.hovered) {
			const points = element.points;
			this.lineStyle(
				element.styles.hoverStrokeWidth || 1,
				element.styles.hoverStrokeColor || 0xf78284,
				element.styles.hoverStrokeAlpha || 1.0
			);
			points.forEach((point: number[], index: number) => {
				if (index === 0) {
					this.moveTo(point[0], point[1]);
				} else {
					this.lineTo(point[0], point[1]);
				}
			});
		}
	}

	protected _drawSelect(): void {
		const element = this._element as Element;
		if (!element) return;
		const points = element.points;
		if (!points || points.length === 0) return;
		if (element.selected) {
			const points = element.points;
			this.lineStyle(
				element.styles.selectStrokeWidth || 1,
				element.styles.selectStrokeColor || 0xf78284,
				element.styles.selectStrokeAlpha || 1.0
			);
			points.forEach((point: number[], index: number) => {
				if (index === 0) {
					this.moveTo(point[0], point[1]);
				} else {
					this.lineTo(point[0], point[1]);
				}
			});
		}
	}

	/**
	 * check all gizmos
	 */
	protected _checkGizmos(): void {
		const element = this._element;
		if (!element) return;
		this._checkLabelGizmo();
		this._checkLabel2Gizmo();
		this._checkEditorGizmo();
	}
}
