import { mat2d, vec2 } from "gl-matrix";
import {
	DEG_TO_RAD,
	Graphics,
	InteractionEvent,
	Point,
	RAD_TO_DEG,
	Rectangle,
	Text,
} from "pixi.js";
import { Element } from "@blood4/modeling";
import { PixiRenderer } from "../../PixiRenderer";
import { UI } from "../../ui";

// resize direction
export enum ResizeDirectionType {
	Center = "Center",
	Top = "Top",
	Bottom = "Bottom",
	Left = "Left",
	Right = "Right",
	TopLeft = "TopLeft",
	TopRight = "TopRight",
	BottomLeft = "BottomLeft",
	BottomRight = "BottomRight",
}

const _directionsMap = new Map();
_directionsMap.set("0.5", ResizeDirectionType.Top);
_directionsMap.set("1.5", ResizeDirectionType.Right);
_directionsMap.set("2.5", ResizeDirectionType.Bottom);
_directionsMap.set("3.5", ResizeDirectionType.Left);
_directionsMap.set("0", ResizeDirectionType.TopLeft);
_directionsMap.set("1", ResizeDirectionType.TopRight);
_directionsMap.set("2", ResizeDirectionType.BottomRight);
_directionsMap.set("3", ResizeDirectionType.BottomLeft);

export class DimensionSpot extends Graphics {
	protected _element: Element;
	protected _from: {
		p1: number[];
		p2: number[][];
	};
	protected _to: {
		p1: number[];
		p2: number[][];
	};
	protected _index: number;
	protected _angle: number = 0;
	protected _renderer: PixiRenderer<any>;
	protected _center: number[] = [0, 0];
	protected _fillColor = 0x7c7c7c;
	protected _fillAlpha = 0.1;
	protected _text?: Text;
	protected _size: number = 4;

	_mousedownEvent!: (evt: InteractionEvent) => void;
	_mouseupEvent!: (evt: InteractionEvent) => void;
	_mousemoveEvent!: (evt: InteractionEvent) => void;
	_mouseoverEvent!: (evt: InteractionEvent) => void;
	_mouseoutEvent!: (evt: InteractionEvent) => void;

	_hitTarget?: UI;
	_isDragging = false;
	_mousePoint: Point = new Point(0, 0);
	_doubleClickedTimeout: NodeJS.Timeout | null = null;

	constructor(
		element: Element,
		from: {
			p1: number[];
			p2: number[][];
		},
		to: {
			p1: number[];
			p2: number[][];
		},
		renderer: PixiRenderer<any>,
		index: number
	) {
		super();
		this._zIndex = 999;
		this._element = element;
		this._renderer = renderer;
		this._index = index;
		this._from = from;
		this._to = to;

		// this._size = Number(
		// 	vec2.distance(vec2.fromValues(from.p1[0], from.p1[1]), vec2.fromValues(to.p1[0], to.p1[1])).toFixed(0)
		// );

		// const v = vec2.fromValues(to.p1[0] - from.p1[0], to.p1[1] - from.p1[1]);

		// const angle = vec2.angle(v, vec2.fromValues(v[0], 0));
		// this._angle = v[0] * v[1] >= 0 ? angle : -angle;

		// this._center = [(from.p2[0][0] + from.p2[1][0]) / 2, (from.p2[0][1] + from.p2[1][1]) / 2];

		this._invalidate();
		this._validate();
		this.interactive = false;
		this.buttonMode = false;
		this.mount();
	}

	public mount(): void {
		this._text = new Text("Length: ", {
			fontFamily: "PingFangSC-Regular, PingFang SC",
			fontSize: 11,
			fill: 0x000000,
			align: "center",
			fontWeight: "400",
		});
		this._text.resolution = 2;
		this._text.anchor.set(this._element.x || 0, this._element.y || 0);
		// this._text.text = `${this._index} - ${this._size} -${(this._angle * RAD_TO_DEG).toFixed(0)}`;
		this._text.text = `${this._size.toFixed(0)}`;
		this._text.zIndex = 999;
		this._text.angle = this._angle * RAD_TO_DEG;
		this.addChild(this._text);
	}

	public unmount(): void {
		// console.log('dimension spot unmount');
		this.clear();
		if (this._text) {
			this.removeChild(this._text);
		}
	}

	/**
	 * _invalidate
	 */
	public _invalidate(): void {
		// console.log('_invalidate');
		// this.unmount();
		this.clear();
	}

	/**
	 * validate
	 */
	public _validate(): void {
		// console.log('_validate');
		const from = this._from,
			to = this._to;
		this._from = from;
		this._to = to;
		if (!from || !to || !from.p1 || !to.p1) {
			this.unmount();
			return;
		}
		this._size = Number(
			vec2.distance(
				vec2.fromValues(from.p1[0], from.p1[1]),
				vec2.fromValues(to.p1[0], to.p1[1])
			)
		);

		const v = vec2.fromValues(to.p1[0] - from.p1[0], to.p1[1] - from.p1[1]);
		const angle = vec2.angle(v, vec2.fromValues(v[0], 0));
		this._angle = v[0] * v[1] >= 0 ? angle : -angle;
		this._center = [
			(from.p1[0] + to.p1[0]) / 2,
			(from.p1[1] + to.p1[1]) / 2,
		];

		this._updateStyles();
		this._validateImpl();
	}

	protected _validateImpl(): void {
		this._draw();
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		const element = this._element;
		if (!element) return;
		this.interactive = element.styles.dimensionGizmoInteractive;
		this.buttonMode = element.styles.dimensionGizmoButtonMode;
		this.zIndex = element.styles.dimensionGizmoZIndex;
		if (this._text) {
			const rect: Rectangle = new Rectangle();
			this._text.getLocalBounds(rect);
			this._text.x = this._center[0];
			this._text.y = this._center[1];
			this._text.anchor.set(0.5, 0.5);
			// this._text.text = `${this._index} - ${this._size} -${(this._angle * RAD_TO_DEG).toFixed(0)}`;
			this._text.text = `${this._size.toFixed(0)}`;
			this._text.angle = this._angle * RAD_TO_DEG;
			this._text.zIndex = element.styles.dimensionGizmoZIndex + 1;
			// this.x = this._center[0];
			// this.y = this._center[1];
		}
		this.visible = this._size > 20; // 100 mm
	}

	protected _draw() {
		this.clear();
		this._drawBody();
	}

	/**
	 * update
	 */
	public _drawBody(): void {
		const element = this._element;
		if (!element) return;

		const from = this._from,
			to = this._to;

		const fromV = vec2.fromValues(from.p1[0], from.p1[1]);
		const toV = vec2.fromValues(to.p1[0], to.p1[1]);
		const fromToV = vec2.fromValues(
			to.p1[0] - from.p1[0],
			to.p1[1] - from.p1[1]
		);
		const fromToVN = vec2.create(); // normalize
		vec2.normalize(fromToVN, fromToV);
		const fromRotateV = vec2.create();
		vec2.rotate(
			fromRotateV,
			fromToV,
			vec2.fromValues(0, 0),
			-90 * DEG_TO_RAD
		);
		vec2.normalize(fromRotateV, fromRotateV);
		const fromRotateV_Center = vec2.create();
		vec2.scaleAndAdd(fromRotateV_Center, fromV, fromRotateV, 5);
		vec2.scaleAndAdd(fromRotateV, fromV, fromRotateV, 10);
		this.lineStyle(0.4, 0xcdcdcd, 0.55);
		this.moveTo(fromV[0], fromV[1]);
		this.lineTo(fromRotateV[0], fromRotateV[1]);
		this.closePath();

		const toRotateV = vec2.create();
		vec2.rotate(
			toRotateV,
			fromToV,
			vec2.fromValues(0, 0),
			-90 * DEG_TO_RAD
		);
		vec2.normalize(toRotateV, toRotateV);
		const toRotateV_Center = vec2.create();
		vec2.scaleAndAdd(toRotateV_Center, toV, toRotateV, 5);
		vec2.scaleAndAdd(toRotateV, toV, toRotateV, 10);
		this.lineStyle(0.4, 0xcdcdcd, 0.55);
		this.moveTo(toV[0], toV[1]);
		this.lineTo(toRotateV[0], toRotateV[1]);
		this.closePath();

		this.lineStyle(1, 0xcdcdcd, 0.55);
		this.moveTo(fromRotateV_Center[0], fromRotateV_Center[1]);
		this.lineTo(toRotateV_Center[0], toRotateV_Center[1]);
		this.closePath();

		this._center = [
			(fromRotateV_Center[0] + toRotateV_Center[0]) / 2,
			(fromRotateV_Center[1] + toRotateV_Center[1]) / 2,
		];
		if (this._text) {
			this._text.x = this._center[0];
			this._text.y = this._center[1];
		}
		// old
		return;
		if (element.debug) {
			/* this.lineStyle(1, 0x000000, 1);
			this.moveTo(from.p1[0], from.p1[1]);
			this.lineTo(from.p2[0][0], from.p2[0][1]);
			this.closePath();
			this.lineStyle(1, 0x000000, 1);
			this.moveTo(from.p1[0], from.p1[1]);
			this.lineTo(from.p2[1][0], from.p2[1][1]);
			this.closePath(); */
			// this.lineStyle(1, 0x0000ff, 1);
			// this.moveTo(to.p1[0], to.p1[1]);
			// this.lineTo(from.p2[2][0], from.p2[2][1]);
			// this.closePath();
		}

		// this.lineStyle(0.4, 0x000000, 0.55);
		// this.moveTo(from.p1[0], from.p1[1]);
		// this.lineTo(from.p2[0][0], from.p2[0][1]);
		// this.closePath();

		if (!from.p2 || !to.p2) return;

		// 辅助线 |--------|
		this.lineStyle(0.4, 0xcdcdcd, 0.55);
		this.moveTo(from.p1[0], from.p1[1]);
		this.lineTo(from.p2[0][0], from.p2[0][1]);
		this.closePath();

		this.lineStyle(0.4, 0xcdcdcd, 0.55);
		this.moveTo(to.p1[0], to.p1[1]);
		this.lineTo(from.p2[1][0], from.p2[1][1]);
		this.closePath();

		// 辅助线 ----------
		const out1 = vec2.create();
		vec2.scaleAndAdd(
			out1,
			vec2.fromValues(from.p1[0], from.p1[1]),
			vec2.fromValues(
				from.p2[0][0] - from.p1[0],
				from.p2[0][1] - from.p1[1]
			),
			0.5
		);
		// this.lineStyle(0.4, 0xcdcdcd, 0.55);
		// this.moveTo(to.p1[0], to.p1[1]);
		// this.lineTo(from.p2[2][0], from.p2[2][1]);
		// this.closePath();

		const out2 = vec2.create();
		vec2.scaleAndAdd(
			out2,
			vec2.fromValues(to.p1[0], to.p1[1]),
			vec2.fromValues(from.p2[1][0] - to.p1[0], from.p2[1][1] - to.p1[1]),
			0.5
		);

		this.lineStyle(1, 0xcdcdcd, 0.55);
		this.moveTo(out1[0], out1[1]);
		this.lineTo(out2[0], out2[1]);
		this.closePath();

		this._center = [(out1[0] + out2[0]) / 2, (out1[1] + out2[1]) / 2];

		// const angle = this._angle;
		// const center = this._center;
		// const a = Math.sin(angle);
		// const b = Math.cos(angle);
		// const tx = center[0] * (1 - b) + center[1] * a;
		// const ty = center[1] * (1 - b) - center[0] * a;
		// const matrix = mat2d.fromValues(b, a, -a, b, tx, ty);
		// this.setMatrix(new Matrix(matrix[0], matrix[1], matrix[2], matrix[3], matrix[4], matrix[5]));
		// const scaled = this._renderer.getScaled();
		// let height = scaled < 1 ? 2 / scaled : 2;
		// let width = this._size;
		// this.beginFill(this._fillColor, this._fillAlpha);
		// this.drawRect(this._center[0] - width / 2, this._center[1] - height / 2, width, height);
		// this.endFill();
		// this.setMatrix(new Matrix());
	}

	/**
	 * set fill color
	 */
	public set fillColor(color: number) {
		this._fillColor = color;
		this.update();
	}

	public set center(center: number[]) {
		this._center = center;
		this.update();
	}

	public setAngle(angle: number) {
		this._angle = angle;
		this.update();
	}

	/**
	 * update
	 */
	public update() {
		this._invalidate();
		this._validate();
	}

	/**
	 * handle zoomed
	 * @param e
	 */
	handle_zoomed(evt: InteractionEvent) {
		// console.log(evt)
	}

	/**
	 * handle panned
	 * @param e
	 */
	handle_panned(evt: InteractionEvent) {
		// console.log(evt)
	}

	isCtrlDown(evt: KeyboardEvent) {
		return evt.ctrlKey || evt.metaKey;
	}

	/**
	 * mouse down
	 * @param e
	 */
	handle_mousedown(evt: InteractionEvent): void {
		evt.stopPropagation();
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		const hitTarget = renderer.renderer.plugins.interaction.hitTest(
			evt.data.global
		);
		this._hitTarget = hitTarget;
		if (hitTarget instanceof DimensionSpot) {
		}
		if (this._doubleClickedTimeout) {
			clearTimeout(this._doubleClickedTimeout);
			this._doubleClickedTimeout = null;
		} else {
		}
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void {
		this._doubleClickedTimeout = setTimeout(() => {
			this._doubleClickedTimeout = null;
		}, 300);
		// this.__dragEnd(evt);
	}

	/**
	 * mouse move
	 * @param e
	 */
	handle_mousemove(evt: InteractionEvent): void {
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		// this.__dragMove(evt);
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseout(evt: InteractionEvent): void {
		this._fillColor = 0x7c7c7c;
		this._fillAlpha = 0.1;
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseover(evt: InteractionEvent): void {
		this._fillColor = 0x3477fc;
		this._fillAlpha = 1.0;
	}

	/**
	 * drag start
	 * @param evt
	 */
	protected __dragStart(evt: InteractionEvent) { }

	/**
	 * drag Move
	 * @param evt
	 */
	protected __dragMove(evt: InteractionEvent) { }

	/**
	 * drag End
	 * @param evt
	 */
	protected __dragEnd(evt: InteractionEvent) { }

	/**
	 * key down event
	 * @param scope
	 * @param evt
	 */
	protected handle_keydown(evt: KeyboardEvent): void { }

	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_keyup(evt: KeyboardEvent): void { }
	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_contextmenu(e: any): void {
		e.preventDefault();
		this.__dragEnd(e);
	}

	protected translate(dx: number, dy: number) {
		this._center[0] += dx;
		this._center[1] += dy;
		this.update();
	}

	public setSize(size: number) {
		this._size = size;
		this.update();
	}

	/**
	 *
	 * @param from
	 */
	public setFrom(from: { p1: number[]; p2: number[][] }) {
		this._from = from;
		this.update();
	}

	/**
	 *
	 * @param to
	 */
	public setTo(to: { p1: number[]; p2: number[][] }) {
		this._to = to;
		this.update();
	}

	/**
	 *
	 * @param from
	 * @param to
	 */
	public setFromTo(
		from: {
			p1: number[];
			p2: number[][];
		},
		to: {
			p1: number[];
			p2: number[][];
		}
	) {
		this._from = from;
		this._to = to;
		this.update();
	}
}
