import { InteractionEvent, Point } from "pixi.js";
import { PixiRenderer } from "../PixiRenderer";
import { CreateInteraction } from "./CreateInteraction";

export class AutoCreateInteraction extends CreateInteraction {
	constructor(
		renderer: PixiRenderer<any>,
		onMouseDown?: (point?: Point, scope?: CreateInteraction) => any,
		onMouseMove?: (point?: Point, scope?: CreateInteraction) => any,
		onMouseUp?: (point?: Point, scope?: CreateInteraction) => any,
		onFinish?: (scope?: CreateInteraction) => any
	) {
		super(renderer, onMouseDown, onMouseMove, onMouseUp, onFinish);
	}

	/**
	 * mouse down
	 * @param e
	 */
	handle_mousedown(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
	}

	/**
	 * mouse move
	 * @param e
	 */
	handle_mousemove(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		const model = renderer.model;
		if (!viewport || !model || !renderer) return;
		const point: Point = evt && evt.data.getLocalPosition(viewport);
		if (this._onMouseMove && !this.element) {
			this.element = this._onMouseMove(point, this);
			if (this.element) {
				model.add(this.element);
				this.element.isEditing = true;
				this._hitTarget = renderer.uis.get(this.element.id);
				this.__dragStart(evt);
				this._onDocumentMouseDown =
					this.handle_documentMouseDown.bind(this);
				document.addEventListener(
					"mousedown",
					this._onDocumentMouseDown
				);
			}
		} else {
			this.__dragMove(evt);
		}
		evt.stopPropagation();
	}

	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_keyup(evt: KeyboardEvent): void {
		super.handle_keyup(evt);
		if (evt.code === "Escape") {
			document.removeEventListener(
				"mousedown",
				this._onDocumentMouseDown
			);
		}
	}

	handle_documentMouseDown(e: MouseEvent) {
		if (this._oldOutTarget) {
			e.preventDefault();
			e.stopPropagation();
			this._onFinish && this._onFinish(this, e);
			this._oldOutTarget = undefined;
		}
		document.removeEventListener("mousedown", this._onDocumentMouseDown);
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseover(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._oldOutTarget && this._oldMousePoint) {
			const mousePoint = this._oldMousePoint;
			const position = evt.data.getLocalPosition(viewport);
			this._hitTarget = this._oldOutTarget;
			this.element = this._hitTarget._element;
			this.element.translation(
				position.x - mousePoint.x,
				position.y - mousePoint.y
			);
			this.__dragStart(evt);
			this._oldOutTarget = undefined;
		}
	}
}
