import geom2 from "@jscad/modeling/src/geometries/geom2";
import path2 from "@jscad/modeling/src/geometries/path2";
import { Vec2 } from "@jscad/modeling/src/maths/types";
import { expand } from "@jscad/modeling/src/operations/expansions";
import { hull } from "@jscad/modeling/src/operations/hulls";
import {
	BaseClientProps,
	BaseStyleProps,
	Shape,
	SHAPE,
	ShapeProps,
	UI2D,
	Element,
	LayerType,
} from "../elements";

/**
 * Shape object props
 */
export interface GroupProps<StyleProps = any, ClientProps = any>
	extends ShapeProps { }

/**
 * Shape object: Shape
 */
export class Group<
	StyleProps extends BaseStyleProps = any,
	ClientProps extends BaseClientProps = any
	> extends Shape<StyleProps, ClientProps> {
	constructor(props?: GroupProps<StyleProps, ClientProps>) {
		super({
			type: SHAPE.Group,
			...props,
		});
		this.ui2d = UI2D.GroupUI;
	}

	public get points(): number[][] {
		const children = this._children;
		let points: number[][] = [];
		children.forEach((child: Element) => {
			points.push(child.center);
		});
		if (points.length >= 3) {
			const geometry1 = geom2.fromPoints(points as Vec2[]);
			const geometry2 = hull(geometry1);
			const hullPoints = geom2.toPoints(geometry2);
			const linePath2 = path2.fromPoints(
				{ closed: false },
				points as Vec2[]
			);
			const expandedPathGeom2 = expand(
				{ delta: 20, corners: "round", segments: 20 },
				linePath2
			);
			const expandedPoints = geom2.toPoints(expandedPathGeom2);
			const result: number[][] = [];
			expandedPoints.forEach((point: number[]) => {
				result.push([point[0], point[1]]);
			});
			this._points = result;
		} else {
			this._points = points;
		}
		return this._points;
	}
}
