import { InteractionEvent, Point } from "pixi.js";
import { PixiRenderer } from "../PixiRenderer";
import { UI } from "../ui";
import { DefaultInteraction } from "./DefaultInteraction";
import { Element, LayerType } from "@blood4/modeling";
import { InteractionEvents } from "../events";

export class CreateInteraction extends DefaultInteraction {
	public element?: Element;
	protected _onMouseDown?: (point?: Point, scope?: CreateInteraction) => any;
	protected _onMouseMove?: (point?: Point, scope?: CreateInteraction) => any;
	protected _onMouseUp?: (point?: Point, scope?: CreateInteraction) => any;
	protected _onFinish?: (scope?: CreateInteraction, evt?: MouseEvent) => any;

	constructor(
		renderer: PixiRenderer<any>,
		onMouseDown?: (point?: Point, scope?: CreateInteraction) => any,
		onMouseMove?: (point?: Point, scope?: CreateInteraction) => any,
		onMouseUp?: (point?: Point, scope?: CreateInteraction) => any,
		onFinish?: (scope?: CreateInteraction) => any
	) {
		super(renderer);
		this._onMouseDown = onMouseDown?.bind(this);
		this._onMouseMove = onMouseMove?.bind(this);
		this._onMouseUp = onMouseUp?.bind(this);
		this._onFinish = onFinish?.bind(this);
	}

	/**
	 * mount
	 * @returns
	 */
	mount() {
		// console.log('create interaction mount');
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		viewport
			.drag({
				wheel: false,
				mouseButtons: "right",
				pressDrag: true,
				direction: "all", // 'x' | 'y' |'all' | null
			})
			.pinch()
			.wheel({
				wheelZoom: true,
			});
		this._zoomedEvent = this.handle_zoomed.bind(this);
		this._pannedEvent = this.handle_panned.bind(this);
		this._mousedownEvent = this.handle_mousedown.bind(this);
		this._mouseupEvent = this.handle_mouseup.bind(this);
		this._mousemoveEvent = this.handle_mousemove.bind(this);
		this._mouseoutEvent = this.handle_mouseout.bind(this);
		this._mouseoverEvent = this.handle_mouseover.bind(this);
		this._keydownEvent = this.handle_keydown.bind(this);
		this._keyupEvent = this.handle_keyup.bind(this);
		this._contextmenu = this.handle_contextmenu.bind(this);

		viewport.on("zoomed", this._zoomedEvent);
		viewport.on("moved", this._pannedEvent);
		viewport.on("mousedown", this._mousedownEvent);
		viewport.on("mouseup", this._mouseupEvent);
		viewport.on("mousemove", this._mousemoveEvent);
		viewport.on("mouseout", this._mouseoutEvent);
		viewport.on("mouseover", this._mouseoverEvent);

		document.addEventListener("keydown", this._keydownEvent);
		document.addEventListener("keyup", this._keyupEvent);
		document.addEventListener("contextmenu", this._contextmenu);
	}

	/**
	 * unmount
	 * @returns
	 */
	unmount() {
		// console.log('create interaction unmount');
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		viewport
			.drag({
				wheel: false,
				mouseButtons: "right",
				pressDrag: false,
			})
			.wheel({
				wheelZoom: false,
			});
		(viewport as any).off("zoomed", this._zoomedEvent);
		(viewport as any).off("moved", this._pannedEvent);
		(viewport as any).off("mousedown", this._mousedownEvent);
		(viewport as any).off("mouseup", this._mouseupEvent);
		(viewport as any).off("mousemove", this._mousemoveEvent);
		(viewport as any).off("mouseout", this._mouseoutEvent);
		document.removeEventListener("keydown", this._keydownEvent);
		document.removeEventListener("keyup", this._keyupEvent);
		document.removeEventListener("contextmenu", this._contextmenu);
		this.clear();
	}

	/**
	 * clear
	 */
	clear() {
		// console.log('create interaction clear');
		super.clear();
		this._hitTarget = undefined;
		this.element = undefined;
		this._onMouseDown = undefined;
		this._onMouseMove = undefined;
		this._onMouseUp = undefined;
	}

	/**
	 * handle zoomed
	 * @param e
	 */
	handle_zoomed(evt: InteractionEvent) { }

	/**
	 * handle panned
	 * @param e
	 */
	handle_panned(evt: InteractionEvent) { }

	isCtrlDown(evt: KeyboardEvent) {
		return evt.ctrlKey || evt.metaKey;
	}

	/**
	 * mouse down
	 * @param e
	 */
	handle_mousedown(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		const model = renderer.model;
		if (!viewport || !viewport || !model) return;
		const point: Point = evt && evt.data.getLocalPosition(viewport);
		if (this._onMouseDown) {
			const element = this._onMouseDown(point, this);
			if (element) {
				if (element.id === this.element?.id) {
					this._onMouseMove && this._onMouseMove(point, this);
					return;
				} else {
					this.element = element;
					model.add(element);
				}
			}
		}
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void {
		this._doubleClickedTimeout = setTimeout(() => {
			this._doubleClickedTimeout = null;
		}, 300);
		this.__dragEnd(evt);
		if (this._onMouseUp) {
			const renderer = this.renderer;
			const viewport = renderer.viewport;
			if (!viewport) return;
			const point: Point = evt && evt.data.getLocalPosition(viewport);
			if (this.element) {
				this.element.isEditing = false;
				const element = this.element;
				const sm = renderer.selectionModel;
				if (sm) {
					// ctrl down append Or remove
					if (this._ctrlDown) {
						if (sm.contains(element)) {
							sm.removeSelection(element);
						} else {
							sm.appendSelection(element);
						}
					} else {
						// normal
						if (!sm.contains(element)) {
							const selection = sm.getSelection();
							renderer.changeLayer(
								selection,
								LayerType.DefaultZIndex
							);
							sm.setSelection(element);
							renderer.changeLayer(
								element,
								LayerType.SelectZIndex
							);
						}
					}
				}
			}
			this._onMouseUp(point, this);
		}
	}

	/**
	 * mouse move
	 * @param e
	 */
	handle_mousemove(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._hitTarget && this._hitTarget instanceof UI) {
			const element = this._hitTarget._element as Element;
			if (element) {
				const center = element.center;
				const dx = evt.data.global.x - center[0],
					dy = evt.data.global.y - center[1];
				element.translation(dx, dy);
			}
		}
		if (this.element && this._onMouseMove) {
			const point: Point = evt && evt.data.getLocalPosition(viewport);
			this._onMouseMove(point, this);
		}
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseout(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		this.__dragEnd(evt);
		this._oldOutTarget = this._hitTarget;
		const position = evt.data.getLocalPosition(viewport);
		this._oldMousePoint = new Point(position.x, position.y);
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseover(evt: InteractionEvent): void {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._oldOutTarget && this._oldMousePoint) {
			const mousePoint = this._oldMousePoint;
			const position = evt.data.getLocalPosition(viewport);
			this._hitTarget = this._oldOutTarget;
			this._hitTarget._element.translation(
				position.x - mousePoint.x,
				position.y - mousePoint.y
			);
			this.__dragStart(evt);
			this._oldOutTarget = undefined;
			document.removeEventListener("mouseup", this._onDocumentMouseUp);
		}
	}

	/**
	 * drag start
	 * @param evt
	 */
	protected __dragStart(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		if (this._isDragging) return;
		if (hitTarget instanceof UI) {
			const position = evt.data.getLocalPosition(hitTarget.parent);
			this._mousePoint = new Point(position.x, position.y);
			this._isDragging = true;
		}
	}

	/**
	 * drag Move
	 * @param evt
	 */
	protected __dragMove(evt: InteractionEvent) {
		if (!this._isDragging) return;
		const hitTarget = this._hitTarget;
		if (hitTarget instanceof UI && this._mousePoint) {
			const position = evt.data.getLocalPosition(hitTarget.parent);
			this.renderer.moveUI(
				hitTarget,
				position.x - this._mousePoint.x,
				position.y - this._mousePoint.y
			);
			this._mousePoint = new Point(position.x, position.y);
			evt.stopPropagation();
			this.emit(InteractionEvents.Dragging, {
				source: this,
				target: hitTarget._element,
			});
		}
	}

	/**
	 * drag End
	 * @param evt
	 */
	protected __dragEnd(evt?: InteractionEvent) {
		const renderer = this.renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._isDragging) {
			this._isDragging = false;
			evt && evt.stopPropagation();
		}
	}

	/**
	 * key down event
	 * @param scope
	 * @param evt
	 */
	protected handle_keydown(evt: KeyboardEvent): void {
		const key = evt.code;
		this._ctrlDown = this.isCtrlDown(evt);
		this.__dragEnd();
		this._onFinish && this._onFinish(this);
	}

	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_keyup(evt: KeyboardEvent): void {
		const key = evt.code;
		this._ctrlDown = this.isCtrlDown(evt);
		if (evt.code === "Escape") {
			const renderer = this.renderer;
			const viewport = renderer.viewport;
			if (!viewport) return;
			if (this._isDragging) {
				this._isDragging = false;
			}
			this._onFinish && this._onFinish(this);
		}
	}
	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_contextmenu(e: any): void {
		e.preventDefault();
		this.__dragEnd(e);
		this._onFinish && this._onFinish(this);
	}

	/**
	 * set onMouseDown function
	 */
	public set onMouseDown(
		cb: (point?: Point, scope?: CreateInteraction) => any
	) {
		this._onMouseDown = cb.bind(this);
	}

	/**
	 * set onMouseMove function
	 */
	public set onMouseMove(
		cb: (point?: Point, scope?: CreateInteraction) => any
	) {
		this._onMouseMove = cb.bind(this);
	}

	/**
	 * set onMouseUp function
	 */
	public set onMouseUp(
		cb: (point?: Point, scope?: CreateInteraction) => any
	) {
		this._onMouseUp = cb.bind(this);
	}

	/**
	 * set onFinish function
	 */
	public set onFinish(cb: (scope?: CreateInteraction) => any) {
		this._onFinish = cb.bind(this);
	}
}
