import { mat2d, vec2 } from "gl-matrix";
import {
	Graphics,
	InteractionEvent,
	Matrix,
	Point,
	Rectangle,
	Text,
} from "pixi.js";
import { Element, Node, SHAPE, Shape, ShapeLine } from "@blood4/modeling";
import { PixiRenderer } from "../../PixiRenderer";
import { UI } from "../../ui";

// resize direction
export enum ResizeDirectionType {
	Center = "Center",
	Top = "Top",
	Bottom = "Bottom",
	Left = "Left",
	Right = "Right",
	TopLeft = "TopLeft",
	TopRight = "TopRight",
	BottomLeft = "BottomLeft",
	BottomRight = "BottomRight",
}

const _directionsMap = new Map();
_directionsMap.set("0.5", ResizeDirectionType.Top);
_directionsMap.set("1.5", ResizeDirectionType.Right);
_directionsMap.set("2.5", ResizeDirectionType.Bottom);
_directionsMap.set("3.5", ResizeDirectionType.Left);
_directionsMap.set("0", ResizeDirectionType.TopLeft);
_directionsMap.set("1", ResizeDirectionType.TopRight);
_directionsMap.set("2", ResizeDirectionType.BottomRight);
_directionsMap.set("3", ResizeDirectionType.BottomLeft);

export class AlignSpot extends Graphics {
	protected _element: Element;
	protected _from: number[];
	protected _to: number[];
	protected _index: number;
	protected _angle: number;
	protected _shape: string = "roundedRect";
	protected _renderer: PixiRenderer<any>;
	protected _center: number[] = [0, 0];
	protected _points?: number[][];
	protected _fillColor = 0x7c7c7c;
	protected _fillAlpha = 0.1;
	protected _text?: Text;
	protected _size: number = 4;
	mode: string = "align"; // align, resize , scale, point
	_mousedownEvent!: (evt: InteractionEvent) => void;
	_mouseupEvent!: (evt: InteractionEvent) => void;
	_mousemoveEvent!: (evt: InteractionEvent) => void;
	_mouseoverEvent!: (evt: InteractionEvent) => void;
	_mouseoutEvent!: (evt: InteractionEvent) => void;

	_hitTarget?: UI;
	_isDragging = false;
	_mousePoint: Point = new Point(0, 0);
	_ctrlDown: boolean = false;
	_doubleClickedTimeout: NodeJS.Timeout | null = null;
	_originSize?: number[];
	_originCenter?: number[];

	constructor(
		element: Element,
		from: number[],
		to: number[],
		renderer: PixiRenderer<any>,
		index: number,
		shape: string = "roundedRect" // circle
	) {
		super();
		this._zIndex = 999;
		this._element = element;
		this._renderer = renderer;
		this._index = index;
		this._shape = shape;
		this._from = from || [0, 0];
		this._to = to || [0, 0];

		this._size = vec2.distance(
			vec2.fromValues(from[0], from[1]),
			vec2.fromValues(to[0], to[1])
		);

		const v = vec2.fromValues(to[0] - from[0], to[1] - from[1]);
		const angle = vec2.angle(v, vec2.fromValues(v[0], 0));
		this._angle = v[0] * v[1] >= 0 ? angle : -angle;

		const center = [(from[0] + to[0]) / 2, (from[1] + to[1]) / 2];
		this._center = center;

		this._invalidate();
		this._validate();
		this.interactive = true;
		this.buttonMode = false;
		this.mount();
	}

	public mount(): void {
		// console.log('drag spot mount');
		this._mousedownEvent = this.handle_mousedown.bind(this);
		this._mouseupEvent = this.handle_mouseup.bind(this);
		this._mousemoveEvent = this.handle_mousemove.bind(this);
		this._mouseoverEvent = this.handle_mouseover.bind(this);
		this._mouseoutEvent = this.handle_mouseout.bind(this);

		this.on("mousedown", this._mousedownEvent);
		this.on("mouseup", this._mouseupEvent);
		// this.on('mousemove', this._mousemoveEvent);
		this.on("mouseover", this._mouseoverEvent);
		this.on("mouseout", this._mouseoutEvent);
		this._text = new Text("Length: ", {
			fontFamily: "PingFangSC-Regular, PingFang SC",
			fontSize: 6,
			fill: 0x000000,
			align: "center",
			fontWeight: "100",
		});
		this._text.resolution = 2;
		this._text.anchor.set(this._element.x || 0, this._element.y || 0);
		this._text.text = `${this._index}`;
		this._text.zIndex = 999;
		// this._text.angle = this._angle * RAD_TO_DEG;
		// this.addChild(this._text);
	}

	public unmount(): void {
		// console.log('drag spot unmount');
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		this.off("mousedown", this._mousedownEvent);
		this.off("mouseup", this._mouseupEvent);
		this.off("mousemove", this._mousemoveEvent);
		this.off("mouseover", this._mouseoverEvent);
		this.off("mouseout", this._mouseoutEvent);
		if (viewport) {
			viewport.off("mousemove", this._mousemoveEvent);
		}
		this.clear();
		if (this._text) {
			this.removeChild(this._text);
		}
	}

	/**
	 * _invalidate
	 */
	public _invalidate(): void {
		// console.log('_invalidate');
		// this.unmount();
		this.clear();
	}

	/**
	 * validate
	 */
	public _validate(): void {
		// console.log('_validate');
		const from = this._from,
			to = this._to;
		this._from = from || [0, 0];
		this._to = to || [0, 0];

		this._size = vec2.distance(
			vec2.fromValues(from[0], from[1]),
			vec2.fromValues(to[0], to[1])
		);

		const v = vec2.fromValues(to[0] - from[0], to[1] - from[1]);
		const angle = vec2.angle(v, vec2.fromValues(v[0], 0));
		this._angle = v[0] * v[1] >= 0 ? angle : -angle;

		const center = [(from[0] + to[0]) / 2, (from[1] + to[1]) / 2];
		this._center = center;

		this._updateStyles();
		this._validateImpl();
	}

	protected _validateImpl(): void {
		this._draw();
	}

	/**
	 * update Styles
	 */
	protected _updateStyles(): void {
		const element = this._element;
		if (!element) return;
		if (this._text) {
			const rect: Rectangle = new Rectangle();
			this._text.getLocalBounds(rect);
			this._text.x = this._center[0];
			this._text.y = this._center[1];
			this._text.anchor.set(0.5, 0.5);
			this._text.text = `${this._index}`;
			// this._text.angle = this._angle * RAD_TO_DEG;
			// this.x = this._center[0];
			// this.y = this._center[1];
		}
	}

	protected _draw() {
		this.clear();
		this._drawBody();
	}

	/**
	 * update
	 */
	public _drawBody(): void {
		const element = this._element;
		if (!element) return;
		const angle = this._angle;
		const center = this._center;
		const a = Math.sin(angle);
		const b = Math.cos(angle);
		const tx = center[0] * (1 - b) + center[1] * a;
		const ty = center[1] * (1 - b) - center[0] * a;
		const matrix = mat2d.fromValues(b, a, -a, b, tx, ty);
		this.setMatrix(
			new Matrix(
				matrix[0],
				matrix[1],
				matrix[2],
				matrix[3],
				matrix[4],
				matrix[5]
			)
		);
		const scaled = this._renderer.getScaled();

		let height = scaled < 1 ? 2 / scaled : 2;
		let width = this._size;
		switch (this._shape) {
			case "rect":
				this.beginFill(this._fillColor, this._fillAlpha);
				this.drawRect(
					this._center[0] - width / 2,
					this._center[1] - height / 2,
					width,
					height
				);
				break;
			case "circle":
				let size = scaled < 1 ? 3 / scaled : 3;
				if (this._hitTarget) {
					size = scaled < 1 ? 3 / scaled : 3;
					this.lineStyle(1.5, 0x737373, 1.0);
					this.beginFill(0xffffff, 1.0);
					this.drawCircle(this._center[0], this._center[1], size);
				} else {
					size = scaled < 1 ? 2.5 / scaled : 2.5;
					this.lineStyle(1, 0xa6a6a6, 1.0);
					this.beginFill(0xffffff, 1.0);
					this.drawCircle(this._center[0], this._center[1], size);
				}
				break;
			case "roundedRect":
				this.lineStyle(0.6, 0x000000, 0.25);
				this.beginFill(0xffffff, 1.0);
				this.drawRoundedRect(
					this._center[0] - width / 2,
					this._center[1] - height / 2,
					width,
					height,
					1
				);
				break;
		}

		this.endFill();
		this.setMatrix(new Matrix());
		/* const bounds = this.getLocalBounds();
		this.lineStyle(1.0, 0xff0000, 1.0);
		this.beginFill(0xff0000, 1.0);
		this.drawRect(bounds.x, bounds.y, bounds.width, bounds.height);
		this.endFill(); */
	}

	/**
	 * set fill color
	 */
	public set fillColor(color: number) {
		this._fillColor = color;
		this.update();
	}

	public set center(center: number[]) {
		this._center = center;
		this.update();
	}

	public setAngle(angle: number) {
		this._angle = angle;
		this.update();
	}

	public set points(points: number[][]) {
		this._points = points;
		this.update();
	}

	/**
	 * update
	 */
	public update() {
		this._invalidate();
		this._validate();
	}

	/**
	 * handle zoomed
	 * @param e
	 */
	handle_zoomed(evt: InteractionEvent) {
		// console.log(evt)
	}

	/**
	 * handle panned
	 * @param e
	 */
	handle_panned(evt: InteractionEvent) {
		// console.log(evt)
	}

	isCtrlDown(evt: KeyboardEvent) {
		return evt.ctrlKey || evt.metaKey;
	}

	/**
	 * mouse down
	 * @param e
	 */
	handle_mousedown(evt: InteractionEvent): void {
		// console.log('handle_mousedown');
		evt.stopPropagation();
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		const hitTarget = renderer.renderer.plugins.interaction.hitTest(
			evt.data.global
		);
		this._hitTarget = hitTarget;
		if (hitTarget instanceof AlignSpot) {
			// this.__dragStart(evt);
			// viewport.on('mousemove', this._mousemoveEvent);
		}
		if (this._doubleClickedTimeout) {
			clearTimeout(this._doubleClickedTimeout);
			this._doubleClickedTimeout = null;
		} else {
			this._handleAlignOption();
		}
	}

	/**
	 * mouse up
	 * @param e
	 */
	handle_mouseup(evt: InteractionEvent): void {
		this._doubleClickedTimeout = setTimeout(() => {
			this._doubleClickedTimeout = null;
		}, 300);
		// this.__dragEnd(evt);
	}

	/**
	 * mouse move
	 * @param e
	 */
	handle_mousemove(evt: InteractionEvent): void {
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		// this.__dragMove(evt);
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseout(evt: InteractionEvent): void {
		this._fillColor = 0x7c7c7c;
		this._fillAlpha = 0.1;
	}

	/**
	 *
	 * @param evt
	 */
	handle_mouseover(evt: InteractionEvent): void {
		this._fillColor = 0x3477fc;
		this._fillAlpha = 1.0;
	}

	/**
	 * drag start
	 * @param evt
	 */
	protected __dragStart(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (this._isDragging) return;
		if (hitTarget instanceof AlignSpot) {
			const position = evt.data.getLocalPosition(viewport);
			this._mousePoint = new Point(position.x, position.y);
			this._isDragging = true;
			const element = hitTarget._element;
			this._originSize = [element.width, element.height];
			this._originCenter = [element.x, element.y];
		}
	}

	/**
	 *
	 * @param p
	 * @param center
	 * @param angle
	 * @returns
	 */
	private _reversPoint(
		p: number[],
		center: number[],
		angle: number
	): number[] {
		angle *= -1;
		let cos = Math.cos((angle * Math.PI) / 180);
		let sin = Math.sin((angle * Math.PI) / 180);
		let x = p[0] - center[0];
		let y = p[1] - center[1];

		let newX = x * cos - y * sin;
		let newY = x * sin + y * cos;

		p[0] = newX + center[0];
		p[1] = newY + center[1];
		return p;
	}

	/**
	 * drag Move
	 * @param evt
	 */
	protected __dragMove(evt: InteractionEvent) {
		if (this.mode === "align") {
			this._handleAlign(evt);
		} else if (this.mode === "resize") {
			this._handleResize(evt);
		} else if (this.mode === "scale") {
			this._handleScale(evt);
		}
	}

	_handleAlign(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (!this._isDragging) return;
		if (!hitTarget || !this._originSize || !this._originCenter) return;
		const element = hitTarget._element;
		if (element.type === SHAPE.Node) {
			const position = evt.data.getLocalPosition(viewport);
			const dx = position.x - this._mousePoint.x,
				dy = position.y - this._mousePoint.y;
			this._mousePoint = new Point(position.x, position.y);

			const element = hitTarget._element;
			const direction = _directionsMap.get(`${this._index}`);
			let from =
				this._index % 1 === 0
					? (element.points.length + this._index - 1) %
					element.points.length
					: (element.points.length + this._index - 0.5) %
					element.points.length,
				to =
					this._index % 1 === 0
						? (element.points.length + this._index + 1) %
						element.points.length
						: (element.points.length + this._index + 0.5) %
						element.points.length,
				current =
					this._index % 1 === 0
						? this._index % element.points.length
						: (this._index + 0.5) % element.points.length;

			const angle = element.angle;
			const point = this._reversPoint(
				[position.x, position.y],
				[this._originCenter[0], this._originCenter[1]],
				angle
			);
			const scaleX =
				Math.abs((point[0] - this._originCenter[0]) * 2) /
				element.width;
			const scaleY =
				Math.abs((point[1] - this._originCenter[1]) * 2) /
				element.height;

			switch (direction) {
				case ResizeDirectionType.Right:
					element.translatePoint(
						[from, to],
						[
							[dx, 0],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.Bottom:
					element.translatePoint(
						[from, to],
						[
							[0, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.Left:
					element.translatePoint(
						[from, to],
						[
							[dx, 0],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.Top:
					element.translatePoint(
						[from, to],
						[
							[0, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.TopRight:
					element.translatePoint(
						[from, current, to],
						[
							[0, dy],
							[dx, dy],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.BottomRight:
					element.translatePoint(
						[from, current, to],
						[
							[dx, 0],
							[dx, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.BottomLeft:
					element.translatePoint(
						[from, current, to],
						[
							[0, dy],
							[dx, dy],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.TopLeft:
					element.translatePoint(
						[from, current, to],
						[
							[dx, 0],
							[dx, dy],
							[0, dy],
						]
					);
					break;
			}
			// evt.stopPropagation();
		} else if (
			element.type === SHAPE.Shape ||
			element.type === SHAPE.Domain
		) {
			const position = evt.data.getLocalPosition(viewport);
			const dx = position.x - this._mousePoint.x,
				dy = position.y - this._mousePoint.y;
			this._mousePoint = new Point(position.x, position.y);

			const element = hitTarget._element;
			let from =
				this._index % 1 === 0
					? (element.points.length + this._index - 1) %
					element.points.length
					: (element.points.length + this._index - 0.5) %
					element.points.length,
				to =
					this._index % 1 === 0
						? (element.points.length + this._index + 1) %
						element.points.length
						: (element.points.length + this._index + 0.5) %
						element.points.length;
			// const angle = element.angle;
			// const point = this._reversPoint(
			// 	[position.x, position.y],
			// 	[this._originCenter[0], this._originCenter[1]],
			// 	angle
			// );
			const fromPoint = element.points[from],
				toPoint = element.points[to];
			const a = vec2.fromValues(
				fromPoint[0] - toPoint[0],
				fromPoint[1] - toPoint[1]
			);
			let b = vec2.fromValues(-a[1], a[0]);
			vec2.normalize(b, b);
			const c = vec2.fromValues(dx, dy);
			const d = vec2.dot(c, b);
			const r = vec2.create();
			vec2.scale(r, b, d);
			element.translatePoint(
				[from, to],
				[
					[r[0], r[1]],
					[r[0], r[1]],
				]
			);
		} else if (
			element.type === SHAPE.ShapeLine ||
			element.type === SHAPE.Line ||
			element.type === SHAPE.AlignLine
		) {
			const position = evt.data.getLocalPosition(viewport);
			const dx = position.x - this._mousePoint.x,
				dy = position.y - this._mousePoint.y;
			this._mousePoint = new Point(position.x, position.y);
			const element = hitTarget._element;
			element.translatePoint([this._index], [[dx, dy]]);
		}
	}

	_handleResize(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (!this._isDragging) return;
		if (!hitTarget || !this._originSize || !this._originCenter) return;
		const element = hitTarget._element;
		if (element.type === SHAPE.Node) {
			const position = evt.data.getLocalPosition(viewport);
			const dx = position.x - this._mousePoint.x,
				dy = position.y - this._mousePoint.y;
			this._mousePoint = new Point(position.x, position.y);

			const element = hitTarget._element;
			const direction = _directionsMap.get(`${this._index}`);
			let from =
				this._index % 1 === 0
					? (element.points.length + this._index - 1) %
					element.points.length
					: (element.points.length + this._index - 0.5) %
					element.points.length,
				to =
					this._index % 1 === 0
						? (element.points.length + this._index + 1) %
						element.points.length
						: (element.points.length + this._index + 0.5) %
						element.points.length,
				current =
					this._index % 1 === 0
						? this._index % element.points.length
						: (this._index + 0.5) % element.points.length;

			const angle = element.angle;
			const point = this._reversPoint(
				[position.x, position.y],
				[this._originCenter[0], this._originCenter[1]],
				angle
			);
			const scaleX =
				Math.abs((point[0] - this._originCenter[0]) * 2) /
				element.width;
			const scaleY =
				Math.abs((point[1] - this._originCenter[1]) * 2) /
				element.height;

			switch (direction) {
				case ResizeDirectionType.Right:
					element.translatePoint(
						[from, to],
						[
							[dx, 0],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.Bottom:
					element.translatePoint(
						[from, to],
						[
							[0, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.Left:
					element.translatePoint(
						[from, to],
						[
							[dx, 0],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.Top:
					element.translatePoint(
						[from, to],
						[
							[0, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.TopRight:
					element.translatePoint(
						[from, current, to],
						[
							[0, dy],
							[dx, dy],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.BottomRight:
					element.translatePoint(
						[from, current, to],
						[
							[dx, 0],
							[dx, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.BottomLeft:
					element.translatePoint(
						[from, current, to],
						[
							[0, dy],
							[dx, dy],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.TopLeft:
					element.translatePoint(
						[from, current, to],
						[
							[dx, 0],
							[dx, dy],
							[0, dy],
						]
					);
					break;
			}
			// evt.stopPropagation();
		} else if (
			element.type === SHAPE.Shape ||
			element.type === SHAPE.Domain
		) {
			const position = evt.data.getLocalPosition(viewport);
			const dx = position.x - this._mousePoint.x,
				dy = position.y - this._mousePoint.y;
			this._mousePoint = new Point(position.x, position.y);

			const element = hitTarget._element;
			let from =
				this._index % 1 === 0
					? (element.points.length + this._index - 1) %
					element.points.length
					: (element.points.length + this._index - 0.5) %
					element.points.length,
				to =
					this._index % 1 === 0
						? (element.points.length + this._index + 1) %
						element.points.length
						: (element.points.length + this._index + 0.5) %
						element.points.length;
			// const angle = element.angle;
			// const point = this._reversPoint(
			// 	[position.x, position.y],
			// 	[this._originCenter[0], this._originCenter[1]],
			// 	angle
			// );
			const fromPoint = element.points[from],
				toPoint = element.points[to];
			const a = vec2.fromValues(
				fromPoint[0] - toPoint[0],
				fromPoint[1] - toPoint[1]
			);
			let b = vec2.fromValues(-a[1], a[0]);
			vec2.normalize(b, b);
			const c = vec2.fromValues(dx, dy);
			const d = vec2.dot(c, b);
			const r = vec2.create();
			vec2.scale(r, b, d);
			element.translatePoint(
				[from, to],
				[
					[r[0], r[1]],
					[r[0], r[1]],
				]
			);
		} else if (
			element.type === SHAPE.ShapeLine ||
			element.type === SHAPE.Line ||
			element.type === SHAPE.AlignLine
		) {
			const position = evt.data.getLocalPosition(viewport);
			const dx = position.x - this._mousePoint.x,
				dy = position.y - this._mousePoint.y;
			this._mousePoint = new Point(position.x, position.y);
			const element = hitTarget._element;
			element.translatePoint([this._index], [[dx, dy]]);
		}
	}
	_handleScale(evt: InteractionEvent) {
		const hitTarget = this._hitTarget;
		const renderer = this._renderer;
		const viewport = renderer.viewport;
		if (!viewport) return;
		if (!this._isDragging) return;
		if (!hitTarget || !this._originSize || !this._originCenter) return;
		const element = hitTarget._element;
		const position = evt.data.getLocalPosition(viewport);
		let dx = position.x - this._mousePoint.x,
			dy = position.y - this._mousePoint.y;
		this._mousePoint = new Point(position.x, position.y);
		const direction = _directionsMap.get(`${this._index}`);
		let from =
			this._index % 1 === 0
				? (element.points.length + this._index - 1) %
				element.points.length
				: (element.points.length + this._index - 0.5) %
				element.points.length,
			to =
				this._index % 1 === 0
					? (element.points.length + this._index + 1) %
					element.points.length
					: (element.points.length + this._index + 0.5) %
					element.points.length,
			current =
				this._index % 1 === 0
					? this._index % element.points.length
					: (this._index + 0.5) % element.points.length;
		// const angle = element.angle;
		// const point = this._reversPoint(
		// 	[position.x, position.y],
		// 	[this._originCenter[0], this._originCenter[1]],
		// 	angle
		// );

		if (element instanceof Node) {
			switch (direction) {
				case ResizeDirectionType.Right:
					element.translatePoint(
						[from, to],
						[
							[dx, 0],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.Bottom:
					element.translatePoint(
						[from, to],
						[
							[0, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.Left:
					element.translatePoint(
						[from, to],
						[
							[dx, 0],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.Top:
					element.translatePoint(
						[from, to],
						[
							[0, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.TopRight:
					element.translatePoint(
						[from, current, to],
						[
							[0, dy],
							[dx, dy],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.BottomRight:
					element.translatePoint(
						[from, current, to],
						[
							[dx, 0],
							[dx, dy],
							[0, dy],
						]
					);
					break;
				case ResizeDirectionType.BottomLeft:
					element.translatePoint(
						[from, current, to],
						[
							[0, dy],
							[dx, dy],
							[dx, 0],
						]
					);
					break;
				case ResizeDirectionType.TopLeft:
					element.translatePoint(
						[from, current, to],
						[
							[dx, 0],
							[dx, dy],
							[0, dy],
						]
					);
					break;
			}
			// evt.stopPropagation();
		} else if (element instanceof Shape) {
			// const origin = [
			// 	this._originCenter[0] + this._originSize[0] / 2,
			// 	this._originCenter[1] + this._originSize[1] / 2
			// ];
			// const distance = vec2.distance(
			// 	vec2.fromValues(position.x, position.y),
			// 	vec2.fromValues(origin[0], origin[1])
			// );
			// console.log(origin, position, distance);
			let size = element.size;
			let diagonal = Math.sqrt(size[0] * size[0] + size[1] * size[1]),
				diagonal3 = 1,
				scale = 1;
			switch (direction) {
				case ResizeDirectionType.Right:
					scale = (dx + size[0]) / size[0];
					element.scaled(scale, ResizeDirectionType.Left);
					break;
				case ResizeDirectionType.Bottom:
					scale = (dy + size[1]) / size[1];
					element.scaled(scale, ResizeDirectionType.Top);
					break;
				case ResizeDirectionType.Left:
					scale = (-dx + size[0]) / size[0];
					element.scaled(scale, ResizeDirectionType.Right);
					break;
				case ResizeDirectionType.Top:
					scale = (-dy + size[1]) / size[1];
					element.scaled(scale, ResizeDirectionType.Bottom);
					break;
				case ResizeDirectionType.TopRight:
					diagonal3 = Math.sqrt(
						(dx + size[0]) * (dx + size[0]) +
						(-dy + size[1]) * (-dy + size[1])
					);
					scale = diagonal3 / diagonal;
					element.scaled(scale, ResizeDirectionType.BottomLeft);
					break;
				case ResizeDirectionType.BottomRight:
					diagonal3 = Math.sqrt(
						(dx + size[0]) * (dx + size[0]) +
						(dy + size[1]) * (dy + size[1])
					);
					scale = diagonal3 / diagonal;
					element.scaled(scale, ResizeDirectionType.TopLeft);
					break;
				case ResizeDirectionType.BottomLeft:
					diagonal3 = Math.sqrt(
						(-dx + size[0]) * (-dx + size[0]) +
						(dy + size[1]) * (dy + size[1])
					);
					scale = diagonal3 / diagonal;
					element.scaled(scale, ResizeDirectionType.TopRight);
					break;
				case ResizeDirectionType.TopLeft:
					diagonal3 = Math.sqrt(
						(-dx + size[0]) * (-dx + size[0]) +
						(-dy + size[1]) * (-dy + size[1])
					);
					scale = diagonal3 / diagonal;
					element.scaled(scale, ResizeDirectionType.BottomRight);
					break;
			}
		} else if (element instanceof ShapeLine) {
		}
	}

	/**
	 * drag End
	 * @param evt
	 */
	protected __dragEnd(evt: InteractionEvent) {
		if (this._isDragging) {
			this._isDragging = false;
			this._originSize = undefined;
			this._originCenter = undefined;
			this.off("mousemove", this._mousemoveEvent);
			// evt.stopPropagation();
		}
	}

	/**
	 * key down event
	 * @param scope
	 * @param evt
	 */
	protected handle_keydown(evt: KeyboardEvent): void {
		const key = evt.code;
		this._ctrlDown = this.isCtrlDown(evt);
	}

	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_keyup(evt: KeyboardEvent): void {
		const key = evt.code;
		this._ctrlDown = this.isCtrlDown(evt);
	}
	/**
	 * key up event
	 * @param scope
	 * @param evt
	 */
	protected handle_contextmenu(e: any): void {
		e.preventDefault();
		this.__dragEnd(e);
	}

	protected translate(dx: number, dy: number) {
		this._center[0] += dx;
		this._center[1] += dy;
		this.update();
	}

	public setSize(size: number) {
		this._size = size;
		this.update();
	}

	/**
	 * 处理拉齐业务逻辑
	 */
	private _handleAlignOption() { }

	/**
	 *
	 * @param from
	 */
	public setFrom(from: number[]) {
		this._from = from;
		this.update();
	}

	/**
	 *
	 * @param to
	 */
	public setTo(to: number[]) {
		this._to = to;
		this.update();
	}

	/**
	 *
	 * @param from
	 * @param to
	 */
	public setFromTo(from: number[], to: number[]) {
		this._from = from;
		this._to = to;
		this.update();
	}
}
