import { BaseClientProps, BaseStyleProps, SHAPE } from "../elements/types";
import { Node, NodeProps } from "../elements";
import { Edge } from "./Edge";
import { Events } from "../events";

/**
 * Display object props
 */
export interface VertexProps<StyleProps = any, ClientProps = any>
	extends NodeProps { }

/**
 * Display object: Node
 */
export class Vertex<
	StyleProps extends BaseStyleProps = any,
	ClientProps extends BaseClientProps = any
	> extends Node<StyleProps, ClientProps> {
	_edges: Set<Edge> = new Set();
	_fromEdges: Set<Edge> = new Set();
	_toEdges: Set<Edge> = new Set();

	constructor(props?: VertexProps<StyleProps, ClientProps>) {
		super({
			type: SHAPE.Node,
			...props,
		});
		this.width = 16;
		this.height = 16;
		this.image =
			"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAGBJREFUeNpiZGBg+M9AJvj//z8jC5RBsmZGRkYwzQITCGlaS7TmNXXBcDYTA4Vg1IDBYAAjJB2Rl5DgKRGWqshyATm2o3uBgcwMBbacBZdmZJfh8CJIASMLodxGCAAEGACTAB0gDBYjygAAAABJRU5ErkJggg==";
		this._updatePoints();
	}

	/**
	 *
	 * @param dx
	 * @param dy
	 */
	translation(dx: number, dy: number) {
		super.translation(dx, dy);
		const edges = this.edges;
		edges.forEach((edge: Edge) => {
			const from = edge.from;
			if (from?.id === this.id) {
				edge.emit(Events.AttributeChange, {
					source: edge,
					prop: "_from",
				});
			}

			const to = edge.to;
			if (to?.id === this.id) {
				edge.emit(Events.AttributeChange, {
					source: edge,
					prop: "_to",
				});
			}
		});
		const parent = this.parent;
		if (parent) {
			parent.emit(Events.AttributeChange, {
				source: parent,
				prop: "child",
			});
		}
	}

	addToEdge(edge: Edge) {
		this._toEdges.add(edge);
	}

	addFromEdge(edge: Edge) {
		this._fromEdges.add(edge);
	}

	deleteEdge(edge: Edge) {
		this._fromEdges.delete(edge);
		this._toEdges.delete(edge);
	}

	get edges() {
		this._edges = new Set([...this._fromEdges, ...this._toEdges]);
		return this._edges;
	}

	getFromEdges() {
		return this._fromEdges;
	}

	getToEdges() {
		return this._toEdges;
	}

	getEdgeById(id: string) {
		const edges = this.edges;
		console.log(edges);
	}

	/**
	 * clear edge
	 */
	clearEdge() {
		this._fromEdges.clear();
		this._toEdges.clear();
		this._edges.clear();
	}
}
